import { Project } from './project';
import { Invoice } from './invoice';
import { Client } from './data.service';
import { Utils } from '../utils/utils';

export class ProjectViewModel {
  project: Project = new Project();
  client: Client = new Client();
  invoices: Invoice[] = [];

  accountManager = '';
  projectManager = '';

  static lastInvoicedDateStr(pv: ProjectViewModel): string {
    const inv = ProjectViewModel.lastInvoice(pv);
    return inv ? Utils.getDateStr(inv.invoiceDate) : '';
  }
  static lastInvoice(pv: ProjectViewModel ): Invoice | null {
    if (pv.invoices.length <= 0) { return null; }
    pv.invoices = pv.invoices.sort((a, b) => a.invoiceDate > b.invoiceDate ? -1 : 1);
    return pv.invoices[0];
  }
  static hasNoInvoice(pv: ProjectViewModel): boolean {
    return (pv.invoices.length <= 0);
  }

  static firstInvoiceIsOverdue(pv: ProjectViewModel, months: number): boolean {
    if (pv.project.completed) { return false; } // for completed projects nothing is due
    if (this.hasNoInvoice(pv)) {
      return Utils.isOlderThanNMonths(pv.project.startDate, months);
    }
    return false;
  }

  static nextInvoiceOverdue(pv: ProjectViewModel, months: number): boolean {
    if (pv.project.completed) { return false; } // for completed projects nothing is due
    const lastInvoice = ProjectViewModel.lastInvoice(pv);
    if (lastInvoice != null) {
      return Utils.isOlderThanNMonths(lastInvoice.invoiceDate, months);
    }
    return false;
  }
}
