import { Component, OnInit } from "@angular/core";
import { DataService, Supplier } from "../data/data.service";

@Component({
  selector: "app-supplier-new",
  templateUrl: "./supplierNew.component.html",
  styleUrls: ["./supplierNew.component.scss"],
})
export class SupplierNewComponent {
  supplier: Supplier = new Supplier();
  supplierList: Supplier[] = [];

  formTitle = "New Supplier";
  btnTitle = "Add this Supplier";
  successMessage = "";
  errorMessage = "";

  constructor(private data: DataService) {
    data.getListOfSuppliers().then(
      (res) => (this.supplierList = res),
      (err) => {
        console.log("Error " + err);
        this.setError(err);
      }
    );
    this.init();
  }

  init() {
    //this.data.getNextSupplierCode().subscribe(sc => this.supplier.supplierCode = sc);
    this.supplier = new Supplier();
    this.successMessage = "";
    this.errorMessage = "";
  }

  setError(err: any) {
    this.errorMessage = "Error: Could not update: " + err;
  }

  onSubmit() {
    this.successMessage = "";
    this.errorMessage = "";
    console.log("Creating new Supplier" + this.supplier.name);
    const nameDuplicate = this.supplierList.findIndex(
      (s, i) => s.name === this.supplier.name
    );
    const codeDuplicate = this.supplierList.findIndex(
      (s, i) => s.supplierCode === this.supplier.supplierCode
    );
    if (nameDuplicate !== -1) {
      this.errorMessage = "Supplier with same name already exists!";
    } else if (codeDuplicate !== -1) {
      this.errorMessage = "Duplicate Supplier code.";
    } else {
      console.log(this.supplier)
      this.data.addSupplier(this.supplier).subscribe(
        (s) => {
          console.log(s);
          this.supplierList.concat(this.supplier);
          this.successMessage = "Added new supplier " + this.supplier.name;
          this.resetForm();
        },
        (err) => {
          console.log(err);
          this.setError(err);
        }
      );
    }
  }

  resetForm() {
    setTimeout(() => {
      this.init();
    }, 1000);
  }
}
