import { DataService, PurchaseOrder, Vendor, Fileblob, Client } from '../data/data.service';
import { Router } from '@angular/router';
import { Project } from '../data/project';
import { Invoice } from '../data/invoice';
import * as FileSaver from 'file-saver';
import { OnInit, Directive } from '@angular/core';
import { AttachmentInterface } from '../attachments/attachments.component';

@Directive()
export class InvoiceBase implements OnInit {
  buttonTitle = 'Create Invoice';
  formTitle = 'New Invoice';
  clientId = 0;
  clientName = '';
  selectedPo: PurchaseOrder | undefined; // selected PO
  selectedProject: Project | undefined;
  vendor: Vendor = new Vendor();
  showForm = true;

  get vendorName() {
    return this.vendor.name;
  } // selected vendor
  curSymbol: string | undefined; // selected currency symbol

  //  purchaseOrders: PurchaseOrder[] | undefined;
  projects: Project[] | undefined;

  client: Client | undefined;
  isInternalCustomer: boolean = false;
  attachments: Fileblob[] | undefined;
  attachmentmsg: string | undefined;
  showAttachmentButton = false;
  doFadeIn = false;
  doFadeOut = false;


  // following for invoice internal edit new
  externalInvoice = new Invoice();
  externalPO = new PurchaseOrder();
  externalVendor = new Vendor();
  extCurSymbol = '';
  updating = false;
  endClient = new Client();

  // -----

  showPeriodOfServiceTip = false;

  get showTDS() {
    return this.client ? this.client.country == 'India' : false;
  }

  get balanceAmount() {
    var bal = this.invoice.amount - this.invoice.receivedAmount - this.invoice.bankCharges - this.invoice.tds;
    return bal > 0 ? bal : 0;
  }
  get invoicestatus() {
    return this.invoice.status.toString();
  }
  set invoicestatus(val: string) {
    this.invoice.status = parseInt(val, 10);
  }
  invoice: Invoice = new Invoice();
  successMessage = '';
  errorMessage = '';
  viewMode: boolean | undefined = undefined;
  showReceived: boolean | undefined = undefined;
  showInternalInvoiceEdit = false;
  InternalInvoiceEditTitle = '';
  InternalInvoiceId = 0;
  InternalInvoiceEdit = false;
  showClientSelection = true;
  singlePO = false;
  fileblob = new Fileblob();
  StatusOptions: any[] = [];
  attachmentIntf: AttachmentInterface = new AttachmentInterface();

  get PDFlink() {
    return this.fileblob.id > 0 ? this.data.getInvoicePDFlink(this.invoice.id) : '';
  }
  get PDFFileName() {
    return this.fileblob.name;
  }

  enableRefreshInvoiceNumber = false;

  // enum StatusEnum { ReviewAwaited = 0, Received = 1, Requested = 2, Sent = 3, Overdue = 4, 
  //PartlyReceived = 5, WrittenOff = 6, Canceled = 7 }

  get received() {
    return this.invoice.status === 1 || this.invoice.status === 5 || this.invoice.status == 6 || this.invoice.status == 7;
  }
  get selectedPoNumber() {
    return this.selectedPo ? this.selectedPo.poNumber : '';
  }

  //  get singlePO() { return this.purchaseOrders && this.purchaseOrders.length === 1; }
  get singleProject() {
    return this.projects && this.projects.length === 1;
  }

  getAttachmentLink(attachmentId: number): string {
    return this.data.getBaseInvoiceAttachmentLink(attachmentId);
  }
  ngOnInit() {}
  updateTitles() {}

  constructor(protected data: DataService, protected router: Router) {
    this.StatusOptions = Invoice.getStatusOptions();
  }

  async attachmentLinkClicked(attachmentId: number, filename: string) {
    if (attachmentId <= 0 || !filename) {
      console.log('attachment id is zero');
      return;
    }
    console.log(`attachment id is ${attachmentId} and filename is ${filename}`);
    const urlstr = this.data.getBaseInvoiceAttachmentLink(attachmentId);
    const res = await this.data.downloadResource(urlstr);
    const fileblob = new Blob([res], { type: 'application/octet-stream' });
    FileSaver.saveAs(fileblob, filename);
  }

  async pdflinkClicked() {
    if (this.fileblob.id <= 0) {
      console.log('fileblob id is zero');
      return;
    }
    const urlstr = this.data.getInvoicePDFlink(this.invoice.id);
    const res = await this.data.downloadResource(urlstr);
    const fileblob = new Blob([res], { type: 'application/octet-stream' });
    FileSaver.saveAs(fileblob, this.PDFFileName);
  }

  

  async onClientSelected() {
    if (this.clientId) {
      this.resetUserInputs();
      this.client = await this.data.getClient(this.clientId);
      this.isInternalCustomer = this.data.isInternalCustomer(this.client);
      console.log('InternalCustomer', this.isInternalCustomer)
      this.clientName = this.client.name;
      await this.data
        .getListOfProjectsForClient(this.clientId)
        .toPromise()
        .then(
          res => {
            this.projects = res.filter(proj => proj.completed <= 0); // show only not completed projects
            if (this.projects.length === 1) {
              const project = this.projects[0];
              this.invoice.projectId = project.id;
              this.onProjectSelected();
            }
          },
          err => this.setError('Error : unable to fetch project info ', err)
        );
    }
  }

  async onPOSelected() {
    if (this.invoice.purchaseOrderId) {
      // && this.purchaseOrders) {
      // Note : this should be == the browser makes this.selectedPoId as string so === fails
      // tslint:disable-next-line:triple-equals
      //  this.purchaseOrders.find(order => order.id == this.invoice.purchaseOrderId);
      this.selectedPo = await this.data.getPurchaseOrder(this.invoice.purchaseOrderId).toPromise();
      if (this.selectedPo) {
        this.invoice.paymentTerms = this.selectedPo.paymentTerms;
        await this.data.getcurSymbol(this.selectedPo.currencyId).then(sym => (this.curSymbol = sym));
        await this.data.getVendor(this.selectedPo.vendorId).then(v => (this.vendor = v));
      }
    }
  }

  onProjectSelected() {
    if (this.invoice.projectId && this.projects) {
      // tslint:disable-next-line:triple-equals
      this.selectedProject = this.projects.find(p => p.id == this.invoice.projectId);
    }
  }

  async fileUploadChanged(files: any) {}

  async refreshInvoiceNumberClicked() {}

  async generateInvoiceNumber() {
    if (this.selectedPo) {
      this.invoice.invoiceNumber = 'Generating...';
      this.data
        .getNextInvoiceNumber(this.selectedPo.vendorId, new Date((this.invoice.invoiceDate as Date)), this.invoice.isExpenseInvoice)
        .subscribe(invNumber => this.invoice.invoiceNumber = invNumber)
        ;
    }
  }

  setError(msg: string, err: any) {
    this.errorMessage = msg + err
  }

  OnBankChargesFocus() {
    if (this.invoice.bankCharges === 0) {
      if (this.invoice.receivedAmount > 0) {
        const delta = this.invoice.amount - this.invoice.receivedAmount;
        if (delta > 0 && delta < 100) {
          this.invoice.bankCharges = delta;
        }
      }
    }
  }

  async updateFileblob() {
    if (this.invoice.id > 0) {
      this.data
        .getFileblobForInvoice(this.invoice.id)
        .subscribe(fb => this.fileblob = fb);
    } else {
      this.fileblob.name = '';
      this.fileblob.id = 0;
      this.fileblob.size = 0;
    }
  }

  resetUserInputs() {
    // this.purchaseOrders = undefined;
    this.projects = undefined;
    // this.clientId = undefined;
    this.selectedProject = undefined;
    this.selectedPo = undefined;
    this.successMessage = '';
    this.errorMessage = '';
    this.vendor = new Vendor(); //  = '';
    this.curSymbol = '';
    this.fileblob = new Fileblob();
  }

  // resetForm(reInit: boolean) {
  //   setTimeout(async () => {
  //     console.log('resetForm');
  //     const invNumber = this.invoice.invoiceNumber;
  //     this.invoice = new Invoice();
  //     this.attachmentmsg = '';
  //     this.resetUserInputs();
  //     const invoice = await this.data.getInvoiceForInvoiceNumber(invNumber).toPromise();
  //     this.viewMode = true;
  //     this.updateTitles();
  //     this.router.navigateByUrl('/invoice/' + invoice.id);
  //     if (reInit) {
  //       console.log('with reInit');
  //       this.ngOnInit();
  //     }
  //     // setTimeout(() => this.router.navigateByUrl('/invoice/' + invoice.id), 10);
  //     // If the router is given the same URL the page does not refresh
  //     // this is hack to first go to some other page and then come back
  //     // the  onSameUrlNavigation?: 'reload' | 'ignore' on router does not seem
  //     // to work on this version of angular
  //   }, 500);
  // }
  editInternalInvoice() {}
  OnFocusInvoiceNumber() {}
}
