import { Injectable } from '@angular/core';
import { DataService, UserData } from './data.service';
import { map, catchError, switchMap } from 'rxjs/operators';
import { Observable, of, BehaviorSubject } from 'rxjs';

@Injectable()
export class UserService {
  private currentUserSubject: BehaviorSubject<UserData>;
  currentUser: Observable<UserData>;

  private localStorageKey = 'currentuserdata';

  constructor(protected data: DataService) {
    // first check if the local storage has previous login information
    const json = localStorage.getItem(this.localStorageKey);
    const olduserdata = json ? (JSON.parse(json) as UserData) : new UserData();

    this.currentUserSubject = new BehaviorSubject<UserData>(olduserdata);
    this.currentUser = this.currentUserSubject.asObservable();

    this.currentUser.subscribe(userdata => {
      this.data.setHttpAuthorizationHeader(userdata.token);
      localStorage.setItem(this.localStorageKey, JSON.stringify(userdata));
      if (userdata.user) {
        console.log(`${userdata.user} logged in.`);
      } else {
        console.log('Awaiting login');
      }
    });
  }

  createUser(username: string, fullname:string, password: string, email: string, role: string) {
    return this.data.createUser(username, fullname, password, email, role);
  }

  updateUser(username: string, fullname:string,  password: string, email: string, role: string) {
    return this.data.updateUser(username, fullname, password, email, role);
  }

  login(username: string, password: string): Observable<UserData> {
    return this.data.login(username, password).pipe(
      map(userdata => {
        this.currentUserSubject.next(userdata);
        return userdata;
      }),
      catchError(err => {
        console.log(err);
        this.logout();
        throw err;
      })
    );
  }

  logout() {
    this.currentUserSubject.next(new UserData());
  }
}
