import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'sortBy' })
export class SortPipe implements PipeTransform {
  transform(items: Array<any>, args: string, ascending: boolean): Array<any> {
    return items.sort((a: any, b: any) => {
      const rt = (a[args] > b[args]) ? +1 : (a[args] < b[args]) ? -1 : 0;
      return ascending ? rt : -rt;
    });
  }
}

