import { Component } from '@angular/core';
import { DataService } from '../data/data.service';

import { ActivatedRoute, Router } from '@angular/router';
import { ContactBase } from './contactBase';
import { Location } from '@angular/common';

@Component({
 //   selector: 'app-invoicenew',
    templateUrl: './contactForm.component.html',
    styleUrls: ['./contactForm.component.scss']
})
export class ContactEditComponent extends ContactBase  {

    constructor(protected data: DataService, protected router: Router,
        protected route: ActivatedRoute, protected location: Location) {
            super(data, router, route, location);
    }
    get buttonTitle() { return 'Save Contact'; }
    get formTitle() { return 'Modify Contact'; }

    // for new contact the id passed is the clientId
    // for edit contact the id passed is the contactId
    async initialize(contactId: number) {
            this.data.getContact(contactId).subscribe( con => {
                this.contact = con;
                this.data.getClient(this.contact.clientId).then( cl => this.client = cl);
            },
            err => {
                this.errorMessage = 'Error: Unable to get contact info' + err;
                 this.resetForm();
             }
            );
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Updating Contact' + this.contact.name);
        this.data.updateContact(this.contact)
            .subscribe(c => { console.log(c); this.successMessage = 'saved Contact ' + this.contact.name; this.resetForm(); },
                err => { console.log(err); this.setError('Could not save Contact: Error: ', err); });
    }
}

