import { Component, OnInit } from '@angular/core';
import { DataService, PurchaseOrder, Vendor, Client } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { OrderBase } from './orderBase';
import { UserService } from '../data/user.service';

@Component({
    selector: 'app-orderinternalnew',
    templateUrl: './orderForm.component.html',
    styleUrls: ['./orderForm.component.scss']
})
export class OrderInternalNewComponent extends OrderBase implements OnInit {
  

    isCreating = false;
    buttonTitle = '';
    get formTitle() { return 'Internal Purchase Order'; }


    updateButtonTitle()
    {
        this.buttonTitle = this.isCreating ? 'Creating PO...' : 'Create this Purchase Order';
    }
    constructor(data: DataService, protected router: Router, protected route: ActivatedRoute,
        protected user: UserService,
        protected location: Location) {
        super(data, router, user, location);
        this.updateButtonTitle();
    }


    // the id is the ID of the external PO on which the internal PO is based on
    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        const id = idstr ? +idstr : 0;
        this.showUploadPOFile = false; // for internal PO form do not show upload PO prompt
        this.data.getPurchaseOrder(id)
            .subscribe(
                p => { this.externalPO = p; this.initialize(); },
                err => { console.log(err); this.setError('Error : Could not locate external PO :', err); }
            );
    }

    transferRate = 0.90; // = 0.75;  The % transferred to Tismo India
    initialize() {
        this.getExternalPODetails().then(() => {
            this.data.getInternalPOforEndPO(this.externalPO.id).subscribe(poid => {
                this.InternalPOId = poid;
                if (this.InternalPOId > 0) { // existing internal PO -- we are updating it.
                    this.router.navigateByUrl(`/editinternalpo/${this.externalPO.id}`); // Go to edit internal po component
                } else { // new internal PO fill internal po with external po details
                    this.po.clientId = this.extVendor.clientId; // external PO vendor becomes our client
                    this.clientName = this.extVendor.name;
                    this.po.poDate = this.externalPO.poDate;
                    this.data.getNextInternalPONumber(this.extVendor.id, this.data.getTismoIndiaVendorId(), this.po.poDate)
                             .subscribe( ponumber => this.po.poNumber = ponumber );
                    this.po.description = this.externalPO.description;
                    this.po.currencyId = this.externalPO.currencyId;
                    this.po.amount = this.transferRate * this.externalPO.amount;
                    this.po.expenseAmount = this.externalPO.expenseAmount;
                    this.po.isFixedPrice = this.externalPO.isFixedPrice;
                    this.po.completed = this.externalPO.completed;
                    this.po.vendorId = this.data.getTismoIndiaVendorId();
                    this.po.tandMBillRate = this.transferRate * this.externalPO.tandMBillRate;
                    this.po.remarks = this.externalPO.remarks;
                    this.showIsCompleted = false;
                    this.viewMode = undefined;
                    this.showClientSelection = false;
               }
            });
        });
    }


    async getExternalPODetails() {
        const ev = await this.data.getVendor(this.externalPO.vendorId);
        this.extVendor = ev;
        const ec = await this.data.getClient(this.externalPO.clientId);
        this.endClient = ec;
        const ci = await this.data.getcurSymbol(this.externalPO.currencyId);
        return this.extCurSymbol = ci;
    }


    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        this.po.isFixedPrice = this.po.isFixedPrice ? 1 : 0;
        this.po.completed = this.po.completed ? 1 : 0;
        console.log('Creating internal PO' + this.po.clientId, this.po.amount, this.po.poDate, this.po.currencyId, this.po.description,
            this.po.isFixedPrice, this.po.tandMBillRate, this.po.completed, '***');
        try {
            this.isCreating = true;
            this.updateButtonTitle();
            this.data.addInternalPOforEndPO(this.po, this.externalPO.id)
                .subscribe(poid => {
                    this.po.id = poid;
                    this.successMessage = 'Internal PO created';
                    // this.isCreating = false;
                    // this.updateButtonTitle();
                    this.goToCurrentPO(this.po.id, 5000);
                },
                err => {
                    console.log(err); this.setError('Could not create PO: Error: ', err);
                    this.setError('Could not create PO: Error: ', err);
                    this.resetForm();
                })
          
        } catch (e) {
            console.log(e); this.setError('Could not create PO: Error: ', e);
            this.resetForm();
        }
       
    }


}

