import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { SupplierInvoiceBase } from './supplierInvoiceBase';

@Component({
  selector: 'app-supplier-invoice-form',
  templateUrl: './supplierInvoiceForm.component.html',
  styleUrls: ['./supplierInvoiceForm.component.scss']
})
export class SupplierInvoiceFormComponent extends SupplierInvoiceBase implements OnInit {

  constructor(
    data: DataService,
    private route: ActivatedRoute,
    protected router: Router
  ) {
    super(data, router);
    this.showReceived = false;
  }

  async ngOnInit(){
    const spoidstr = this.route.snapshot.paramMap.get('spoid');
    const spoid = spoidstr ? +spoidstr : 0;
    const supplierIdstr = this.route.snapshot.paramMap.get('supplierId');
    const supplierId = supplierIdstr ? + supplierIdstr : 0;
    if (spoid > 0 && supplierId > 0) {
      this.supplierId = supplierId;
      await super.onSupplierSelected();
      this.showSupplierSelection = false;
      this.invoice.supplierPOId = spoid;
      this.invoice.invoiceDate = await this.computeDefaultInvoiceDate();
      await super.onSPOSelected();
    }
  }

  async onSubmit() {
    this.successMessage = '';
    this.errorMessage = '';
    console.log('Submitting new Invoice');
    if (this.invoice.projectId === 0) {
      this.setError(' Please select a project for this invoice. ', '');
      return;
    }
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = 'Creating Invoice...';
    await this.data
      .addSupplierInvoice(this.invoice)
      .toPromise()
      .then(
        c => {
          console.log(c);
          this.successMessage = 'added Invoice ' + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(false);
        },
        err => {
          this.buttonTitle = oldButtonTitle;
          console.log(err);
          this.setError('Could not add Invoice: Error: ', err);
        }
      );
  }

  resetForm(reInit: boolean) {
    const tout = reInit ? 500 : 2000;
    setTimeout(async () => {
      //  console.log('resetForm-invoiceNew');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data.getSupplierInvoiceForInvoiceNumber(this.invoice.invoiceNumber).toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl('/supplierinvoice/' + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  async computeDefaultInvoiceDate(): Promise<Date> {
    let dt = new Date();
    if (dt.getDate() <= 6) {
      const anyInvoicesThisMonth = await this.data.anySupplierInvoice(dt.getMonth() + 1, dt.getFullYear()).toPromise();
      if (anyInvoicesThisMonth <= 0) {
        // if no invoices this month, return last month's last working day
        console.log('No invoices this month');
        dt = this.getPreviousMonthsLastWorkingDay(dt);
      }
    }
    return dt;
  }
  getPreviousMonthsLastWorkingDay(dt: Date): Date {
    dt.setDate(dt.getDate() - dt.getDate()); // go to previous month
    if (dt.getDay() === 0) {
      // sunday ?
      dt.setDate(dt.getDate() - 2); // go to the previous friday
    }
    if (dt.getDay() === 6) {
      // saturday ?
      dt.setDate(dt.getDate() - 1); // go to the previous friday
    }
    return dt;
  }

}
