import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { Utils } from '../utils/utils';
import { Invoice } from '../data/invoice';


@Component({
  selector: 'app-receivables',
  templateUrl: './receivables.component.html',
  styleUrls: ['./receivables.component.scss']
})
export class ReceivablesComponent implements OnInit {

  fullList: InvoiceViewObj[] = [];
  endClientGroups: InvoiceViewObj[][] = []; // grouped client list
  displayClientGroups: InvoiceViewObj[][] = []; // grouped client list
  internalClientGroups: InvoiceViewObj[][] = [];

  progressMessage = '';
  errorMessage = '';
  clientType = 'endClients';
  total = '';

  loaded = false;

  isInternalView = false; // showing receivables from internal group companies

  constructor(private data: DataService) { }

  ngOnInit() {
    this.fetchReceivablesList();
  }

  fetchReceivablesList() {
    this.fullList = [];
    this.endClientGroups = [];

    this.progressMessage = 'Fetching Invoices ...';
    this.errorMessage = '';
    this.data.getReceivables().
      subscribe(
      {
          next: fl => { this.fullList.push(...fl); },
          error: err => {
          this.errorMessage = 'Connection Errors';
          this.progressMessage = '';
        },
          complete: () => {
            this.progressMessage = '';
            this.endClientGroups = this.getEndClientInvoiceList(this.fullList);
            const internalClientInvoices = this.fullList
              .filter(inv => this.data.isInternalCustomer(inv.client))
              .sort(InvoiceViewObj.invoiceDateSortCompare());
            this.internalClientGroups.push(internalClientInvoices);
            this.OnClientTypeChanged();
            this.loaded = true;
           
        }
      }
    );
  }
  OnHeaderClicked(headerName: string) {
  }

  getEndClientInvoiceList(list: InvoiceViewObj[]): InvoiceViewObj[][] {
    return this.groupBy(list.filter(inv => !this.data.isInternalCustomer(inv.client)));
  }

  groupBy(list: InvoiceViewObj[]): InvoiceViewObj[][] {
    // reduce : the accumulated value is a dictionary. key is the client id and the value is array of invoice view objs
    const clientgroups: {
      [id: number]: InvoiceViewObj[];
    } = list.reduce((groups: { [id: number]: InvoiceViewObj[]; }, inv) => {
      const val = inv.client.id;
      groups[val] = groups[val] || [];
      groups[val].push(inv);
      return groups;
    }, {});

    const clientarray: InvoiceViewObj[][] = [];
    // tslint:disable-next-line:forin
    for (const key in clientgroups) {
      clientarray.push(clientgroups[key]);
    }
    const datefilter = InvoiceViewObj.invoiceDateSortCompare();
    return clientarray.sort((a, b) => datefilter(a[0], b[0]));

  }


  OnClientTypeChanged() {
    switch (this.clientType) {
      case 'internalClients':
        this.displayClientGroups = this.internalClientGroups;
        this.isInternalView = true;
        this.updateTotal();
        break;
      case 'endClients':
      default:
        this.displayClientGroups = this.endClientGroups;
        this.isInternalView = false;
        this.updateTotal();
        break;
    }
  }

  

 

  updateTotal() {
    let total = 0;
    this.displayClientGroups.forEach(lst => {
      total += lst.reduce((acc, inv) => acc + InvoiceViewObj.getDueAmountInUSD(inv), 0);
    });
    this.total = Utils.toStr(total);
  }

  exportLink() {
    let filename = this.isInternalView ? "Receivables_fromGroup_" : "Receivables_fromClients_";

    Utils.exportCSVFile(this.getCSV(this.displayClientGroups), filename);
  }


  getExternalInvoiceStatus(inv: InvoiceViewObj)
  {
    if (inv.hasExternalInvoice) {
      return Invoice.getStatusStr(inv.externalInvoice);  
    }
    return "";
  }

  getCSV(groupedInvoiceList: InvoiceViewObj[][]): string {

    let expList: any[] = [];
    
    groupedInvoiceList.forEach(invlist => {
      let tmpList = invlist.map(inv => {
        let invReceivable = {
          Client: inv.client.name,
         
          ProjectCode: inv.projectCode,
          InvoiceDate: Utils.getDateStr(inv.invoice.invoiceDate),
          InvoiceNumber: inv.invoice.invoiceNumber,
         
          Description: inv.invoice.description,
          Currency: inv.curName,
          Amount: Utils.toStr(inv.invoice.amount),
          Received: Utils.toStr(inv.invoice.receivedAmount),
          Due: Utils.toStr(InvoiceViewObj.getDueAmount(inv)),
          Status: inv.status,
       
        };
        if (this.isInternalView) {
            (<any>invReceivable).EndClientStatus = this.getExternalInvoiceStatus(inv)
        }
        return invReceivable;
      });
     
      expList.push(...tmpList);
    });
    return Utils.tocsv(expList);
    
  }

}
