import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { SortPipe } from './utils/sort.pipe';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AppComponent } from './app.component';

import { OrderBookComponent } from './orderbook/orderbook.component';
import { OrderComponent } from './orderbook/order.component';
import { OrderNewComponent } from './orderbook/orderNew.component';
import { NavMenuComponent } from './navmenu/navmenu.component';
import { DataService } from './data/data.service';
import { InvoiceService } from './invoices/invoice.service';
import { ProjectNewComponent } from './projects/projectNew.component';
import { ProjectListComponent } from './projects/projectList.component';
import { ProjectComponent } from './projects/project.component';
import { ClientListComponent } from './clients/clientList.component';
import { ClientComponent } from './clients/client.component';
import { ClientNewComponent } from './clients/clientNew.component';
import { ClientEditComponent } from './clients/clientEdit.component';
import { InvoiceComponent } from './invoices/invoice.component';
import { InvoiceListComponent } from './invoices/invoiceList.component';
import { InvoiceNewComponent } from './invoices/invoiceNew.component';
import { ProjectEditComponent } from './projects/projectEdit.component';
import { OrderEditComponent } from './orderbook/orderEdit.component';
import { InvoiceEditComponent } from './invoices/invoiceEdit.component';
import { ClientDetailComponent } from './clients/clientDetail.component';
// import { PODetailComponent } from './orderbook/poDetail.component';
import { OrderInternalNewComponent } from './orderbook/orderInternalNew.component';
import { OrderInternalEditComponent } from './orderbook/orderInternalEdit.component';
import { InvoiceInternalNewComponent } from './invoices/invoiceInternalNew.component';
import { InvoiceInternalEditComponent } from './invoices/invoiceInternalEdit.component';
import { ContactNewComponent } from './contacts/contactNew.component';
import { ContactEditComponent } from './contacts/contactEdit.component';
import { DateselectComponent } from './utils/dateselect.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MonthchartComponent } from './dashboard/monthchart.component';
import { TwoDigitDateStrPipe } from './utils/TwoDigitDateStrPipe';
import { DateStrPipe } from './utils/DateStrPipe';
import { StatusMsgComponent } from './utils/statusmg.component';
import { ProjectPinComponent } from './projects/projectpin.component';

import { LoginComponent } from './login/login.component';
import { UserService } from './data/user.service';
import { Authguard } from './data/authguard.service';
import { ErrorInterceptor } from './data/errorInterceptor.service';
import { UserComponent } from './user/user.component';
import { ReceivablesComponent } from './receivables/receivables.component';
import { ReceivableItemComponent } from './receivables/receivableItem.component';
import { ClientInvoiceComponent } from './clients/clientinvoice.component';
import { ClientProjectComponent } from './clients/clientproject.component';
import { AngularDateHttpInterceptor } from './utils/angulardateinterceptor';
import { ClientpoComponent } from './clients/clientpo.component';
import { AttachmentsComponent } from './attachments/attachments.component';
import { ClientpobothComponent } from './clients/clientpoboth.component';
import { ProjectEmailComponent } from './projects/projectEmail.component';
import { SoftexImportComponent } from './invoices/softexImport.component';
import { SEBRCImportComponent } from './invoices/sebrcImport.component';
import { SuppliersComponent } from './suppliers/suppliers.component';
import { SupplierListComponent } from './suppliers/supplierList.component';
import { SupplierNewComponent } from './suppliers/supplierNew.component';
import { SupplierContactNewComponent } from './supplierContacts/supplierContactsForm.component';
import { SupplierContactEditComponent } from './supplierContacts/supplierContactEdit.component';
import { SupplierDetailsComponent } from './suppliers/supplierDetails.component';
import { SupplierEditComponent } from './suppliers/supplierEdit.component';
import { SupplierpoComponent } from './suppliers/supplierpo.component';
import { SupplierOrderFormComponent } from './supplierOrders/supplierOrderForm.component';
import { SupplierEditOrderComponent } from './supplierOrders/supplierEditOrder.component';
import { SupplierInvoiceFormComponent } from './supplierInvoices/supplierInvoiceForm.component';
import { SupplierInvoiceEditComponent } from './supplierInvoices/supplierInvoiceEdit.component';


const appRoutes: Routes = [
  { path: 'orderbook', component: OrderBookComponent, canActivate: [Authguard] },
  { path: 'neworder', component: OrderNewComponent, canActivate: [Authguard] },
  { path: 'neworder/:clientId', component: OrderNewComponent, canActivate: [Authguard] },

  { path: 'order/:id', component: OrderEditComponent, canActivate: [Authguard] },
  { path: 'newinternalpo/:id', component: OrderInternalNewComponent, canActivate: [Authguard] },
  { path: 'editinternalpo/:id', component: OrderInternalEditComponent, canActivate: [Authguard] },

  { path: 'projectlist', component: ProjectListComponent, canActivate: [Authguard] },
  { path: 'newproject', component: ProjectNewComponent, canActivate: [Authguard] },
  { path: 'newproject/:clientId', component: ProjectNewComponent, canActivate: [Authguard] }, // new project on an existing client
  { path: 'project/:id', component: ProjectEditComponent, canActivate: [Authguard] },
  { path: 'projectpin/:id', component: ProjectPinComponent, canActivate: [Authguard] }, // view project pin

  { path: 'clientlist', component: ClientListComponent, canActivate: [Authguard] },
  { path: 'newclient', component: ClientNewComponent, canActivate: [Authguard] },
  { path: 'client/:id', component: ClientEditComponent, canActivate: [Authguard] },
  { path: 'clienttrack/:id', component: ClientDetailComponent, canActivate: [Authguard] },

  { path: 'invoicelist', component: InvoiceListComponent, canActivate: [Authguard] },
  { path: 'newinvoice', component: InvoiceNewComponent, canActivate: [Authguard] },
  { path: 'invoice/:id', component: InvoiceEditComponent, canActivate: [Authguard] },
  { path: 'newinternalinvoice/:id', component: InvoiceInternalNewComponent, canActivate: [Authguard] },
  { path: 'editinternalinvoice/:id', component: InvoiceInternalEditComponent, canActivate: [Authguard] },

  { path: 'receivables', component: ReceivablesComponent, canActivate: [Authguard] },

  { path: 'softeximport', component: SoftexImportComponent, canActivate: [Authguard] },
  { path: 'ebrcimport', component: SEBRCImportComponent, canActivate: [Authguard] },

  { path: 'newcontact/:id', component: ContactNewComponent, canActivate: [Authguard] }, // id of the client
  { path: 'editcontact/:id', component: ContactEditComponent, canActivate: [Authguard] }, // id of the contact to edie

  { path: 'dashboard', component: DashboardComponent, canActivate: [Authguard] },

  { path: 'login', component: LoginComponent },
  { path: 'createuser', component: UserComponent, canActivate: [Authguard] },
  { path: 'suppliers', component: SuppliersComponent, canActivate: [Authguard] },
  { path: 'supplierlist', component: SupplierListComponent, canActivate: [Authguard] },
  { path: 'newsupplier', component: SupplierNewComponent, canActivate: [Authguard] },
  { path: 'supplier/:id', component: SupplierEditComponent , canActivate: [Authguard] },
  { path: 'suppliernewcontact/:id', component: SupplierContactNewComponent, canActivate: [Authguard] }, // id of the supplier
  { path: 'suppliereditcontact/:id', component: SupplierContactEditComponent, canActivate: [Authguard] }, // id of the supplierContact
  { path: 'suppliertrack/:id', component: SupplierDetailsComponent, canActivate: [Authguard] },
  { path: 'supplierneworder/:supplierId', component: SupplierOrderFormComponent, canActivate: [Authguard] },
  { path: 'supplierorder/:id', component: SupplierEditOrderComponent, canActivate: [Authguard] },   
  { path: 'newsupplierinvoice', component: SupplierInvoiceFormComponent, canActivate: [Authguard] },
  { path: 'supplierinvoice/:id', component: SupplierInvoiceEditComponent, canActivate: [Authguard] },


  { path: '**', component: ClientListComponent, canActivate: [Authguard] }
];

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    OrderBookComponent,
    OrderComponent,
    OrderNewComponent,
    OrderEditComponent,
    OrderInternalNewComponent,
    OrderInternalEditComponent,

    ProjectNewComponent,
    ProjectListComponent,
    ProjectEditComponent,
    ProjectComponent,
    ProjectPinComponent,
    ProjectEmailComponent,

    ClientListComponent,
    ClientComponent,
    ClientInvoiceComponent,
    ClientNewComponent,
    ClientEditComponent,
    ClientDetailComponent,
    ClientProjectComponent,
    ClientpoComponent,
    ClientpobothComponent,

    InvoiceComponent,
    InvoiceListComponent,
    InvoiceNewComponent,
    InvoiceEditComponent,
    InvoiceInternalNewComponent,
    InvoiceInternalEditComponent,

    ContactNewComponent,
    ContactEditComponent,
    SortPipe,
    DateStrPipe,
    TwoDigitDateStrPipe,

    DateselectComponent,
    DashboardComponent,
    MonthchartComponent,

    StatusMsgComponent,
    LoginComponent,
    UserComponent,
    ReceivablesComponent,
    ReceivableItemComponent,
    SoftexImportComponent,
    SEBRCImportComponent,
    AttachmentsComponent,
    SuppliersComponent,
    SupplierListComponent,
    SupplierNewComponent,
    SupplierEditComponent,
    SupplierContactNewComponent,
    SupplierContactEditComponent,
    SupplierDetailsComponent,
    SupplierpoComponent,
    SupplierOrderFormComponent,
    SupplierEditOrderComponent,
    SupplierInvoiceFormComponent,
    SupplierInvoiceEditComponent,

  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    RouterModule.forRoot(appRoutes, { enableTracing: false, onSameUrlNavigation: 'reload' })
  ],

  providers: [
    HttpClientModule,
    DataService,
    UserService,
    InvoiceService,
    SortPipe,
    Authguard,
    { provide: 'BASE_URL', useFactory: getBaseUrl, deps: [] },

    { provide: HTTP_INTERCEPTORS, useClass: AngularDateHttpInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },

  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function getBaseUrl() {
  return document.getElementsByTagName('base')[0].href;
}
