import { Component, OnInit } from '@angular/core';

import { DataService } from '../data/data.service';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { Utils } from '../utils/utils';
import { InvoiceService } from './invoice.service';
import { MonthYear } from '../utils/financialYear';
import { UserService } from '../data/user.service';
import { getCSV, getSoftexFormatCSV } from './invoiceListExport';


interface IDictionary {
  [index: string]: boolean;
}

@Component({
  selector: 'app-invoicelist',
  templateUrl: './invoiceList.component.html',
  styleUrls: ['./invoiceList.component.scss']
})
export class InvoiceListComponent implements OnInit {
  //  invoiceMonth = new Date();
  successMessage = '';
  errorMessage = '';
  company = 'Group';
  progressMessage = 'Fetching Invoices ...';
  sortState = {} as IDictionary;
  selectedMonth = 1;
  selectedYear = 2018;
  invoiceList: InvoiceViewObj[] = [];
  fullList: InvoiceViewObj[] = [];
  total = '0';
  filterTerm = '';

  showSoftex = false;

  constructor(
    private data: DataService,
    private invoiceService: InvoiceService,
    private user: UserService
  ) { }
  ngOnInit() {
    this.user.currentUser.subscribe(userdata => {
      this.showSoftex = userdata.isaccounting > 0 || userdata.ismgmt > 0 || userdata.isadmin > 0;
    });
  }

  async OnCompanyChange() {
    this.filterTerm = '';
    await this.updateInvoiceList();
  }

  OnDurationChanged(monthRange: MonthYear[]) {
    monthRange.forEach(m => console.log(' m: ' + m.month + ' y:' + m.year));
    this.fetchFullInvoiceList(monthRange);
  }

  fetchFullInvoiceList(monthRange: MonthYear[]) {
    this.fullList = [];
    this.invoiceList = [];
    this.progressMessage = 'Fetching Invoices ...';
    this.errorMessage = '';
    this.invoiceService.fetchAllInvoices(monthRange).subscribe(
      fl => {
        this.fullList.push(...fl);
      },
      err => {
        this.errorMessage = 'Connection Errors';
        this.progressMessage = '';
      },
      () => {
        this.progressMessage = '';
        this.updateInvoiceList();
      }
    );
  }

  updateInvoiceList() {
    this.invoiceList = this.fullList
      .filter(this.filterForVendor())
      .filter(this.filterForSearchTerm())
      .sort(InvoiceViewObj.invoiceDateSortCompare());
    this.updateTotal();
    this.sortState['invoiceDate'] = true;
    if (this.invoiceList.length === 0) {
      this.progressMessage = '-- No invoices --';
    }
  }


  OnHeaderClicked(headerName: string) {
    this.sortState[headerName] = !this.sortState[headerName];
    let fil = (a: InvoiceViewObj, b: InvoiceViewObj) => 1;
    switch (headerName) {
      case 'clientName':
        fil = (a, b) => (a.client.name > b.client.name ? +1 : -1);
        break;
      case 'invoiceNumber':
        fil = (a, b) => (a.invoice.invoiceNumber > b.invoice.invoiceNumber ? +1 : -1);
        break;
      case 'invoiceDate':
        fil = InvoiceViewObj.invoiceDateSortCompare();
        break;
      case 'projectCode':
        fil = (a, b) => (a.projectCode > b.projectCode ? +1 : -1);
        break;
      case 'description':
        fil = (a, b) => (a.invoice.description > b.invoice.description ? +1 : -1);
        break;
      case 'invoiceAmount':
        fil = (a, b) => (a.invoice.amount > b.invoice.amount ? +1 : -1);
        break;
      case 'amountInUSD':
        fil = (a, b) => (a.amountInUSD > b.amountInUSD ? +1 : -1);
        break;
      case 'status':
        fil = (a, b) => (a.status > b.status ? +1 : -1);
        break;
    }
    const gil = this.sortState[headerName] ? fil : (a: InvoiceViewObj, b: InvoiceViewObj) => -1 * fil(a, b);
    this.invoiceList = this.invoiceList.sort(gil);
  }

  exportLink() {
    const filename = 'Invoices_' + '_Tismo_' + this.company + '_' + this.filterTerm;
    Utils.exportCSVFile(getCSV(this.invoiceList), filename);
  }

  exportSoftexLink() {
    const filename = 'Softex_' + '_Tismo_' + this.company + '_' + this.filterTerm;
    Utils.exportCSVFile(getSoftexFormatCSV(this.invoiceList), filename);
  }


  filterForVendor(): (a: InvoiceViewObj) => boolean {
    let filter = (a: InvoiceViewObj) => true;
    switch (this.company) {
      case 'USA':
        filter = (a: InvoiceViewObj) => this.data.isTismoUS(a.vendorId); //   a.vendorId === usaId;
        break;
      case 'Germany':
        filter = (a: InvoiceViewObj) => this.data.isTismoGermany(a.vendorId); //   a.vendorId === germany id;
        break;
      case 'Group':
        filter = (a: InvoiceViewObj) => !this.data.isInternalCustomer(a.client);
        break;
      // !a.client.customerCode.includes('TCI'); break; // TCI indicates internal customer
      default:
      case 'India':
        filter = (a: InvoiceViewObj) => this.data.isTismoIndia(a.vendorId); // a.vendorId === indiaId;
        break;
    }
    return filter;
    // this.invoiceList = this.fullList.filter(filter);
  }

  filterForSearchTerm(): (inv: InvoiceViewObj) => boolean {
    if (this.filterTerm === '') {
      return (inv: InvoiceViewObj) => true;
    }
    const srchTarget = (inv: InvoiceViewObj) =>
      [
        inv.client.name,
        inv.invoice.invoiceNumber,
        Utils.getDateStr(inv.invoice.invoiceDate),
        inv.projectCode,
        inv.invoice.description,
        inv.invoice.amount.toString(),
        inv.amountInUSD.toString(),
        inv.status
      ].join(' ');

    const regEx = new RegExp(this.filterTerm, 'i');
    return (inv: InvoiceViewObj) => regEx.test(srchTarget(inv));
  }

  updateTotal() {
    this.total = Utils.toStr(this.invoiceList.reduce((acc, inv) => acc + inv.amountInUSD, 0));
    this.progressMessage = `${this.invoiceList.length} invoices`;
  }

  filterTermChanged(newSrchTerm: string) {
    this.filterTerm = newSrchTerm.replace(',', '');
    if (!this.filterTerm || this.filterTerm.length >= 2) {
      this.updateInvoiceList();
    }
  }
}
