import { Component } from '@angular/core';
import { DataService } from '../data/data.service';
import { trigger, style, animate, transition } from '@angular/animations';
import { SBAssignmentMgr } from './SBAssignmentMgr';



@Component({
    selector: 'app-softeximport',
    templateUrl: './softexImport.component.html',
    styleUrls: ['./softexImport.component.scss'],
    animations: [
        trigger(
            'inOutAnimation',
            [
                transition(
                    ':enter',
                    [
                        style({ opacity: 0 }),
                        animate('0.25s ease-out',
                            style({ opacity: 1 }))
                    ]
                ),
                transition(
                    ':leave',
                    [
                        style({ opacity: 1 }),
                        animate('0.25s ease-in',
                            style({ opacity: 0 }))
                    ]
                )
            ]
        )
    ]
})

export class SEBRCImportComponent {

    sbm: SBAssignmentMgr


    pageTitle = 'eBRC Import'

    instruction = 'Copy entire eBRC No - Softex No columns from Excel and paste it here in the box.'


    constructor(protected data: DataService) {

        this.sbm = new SBAssignmentMgr()
        this.sbm.placeholder =
`009BC73210970037	S19000240117
009BC73210970049	S19000240111
009BC73210970025	S20001528300
009BC73210970042	S19000240097`

        this.sbm.isThisTheCorrectInvoice = (p, inv) => p.invReference == inv.softexNumber // in this case invoice is referred by softex
        this.sbm.alreadyAssigned = (p, inv) => p.numToAssign == inv.ebrcNumber
        this.sbm.fetchInvoice = p => this.data.getInvoiceForSoftexNumber(p.invReference)
        this.sbm.assignAndUpdate = (p, inv) => {
            inv.ebrcNumber = p.numToAssign
            return this.data.updateInvoice(inv)
        }


    }
}



