import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FinancialYear, MonthYear } from '../utils/financialYear';
import { DataService, UserData } from '../data/data.service';
import { Month, ChartInput } from './monthchart.component';
import { Observable, timer } from 'rxjs';
import { timeInterval } from 'rxjs/operators';
import { UserService } from '../data/user.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  prevSalesChartInput = new ChartInput();
  saleschartinput = new ChartInput();

  prevPoChartInput = new ChartInput();
  pochartinput = new ChartInput();

  prevInrChartInput = new ChartInput();
  inrchartinput = new ChartInput();
  company = 'Group';

  get potitle() {
    return this.prevPoChartInput.chartTitle;
  }
  set potitle(v: string) {
    this.prevPoChartInput.chartTitle = v;
  }
  get inrtitle() {
    return this.inrchartinput.chartTitle;
  }
  set inrtitle(v: string) {
    this.inrchartinput.chartTitle = v;
  }

  finYear = FinancialYear.GetCurrentFinancialYear();
  prevFinYear = this.finYear.PrevYear();

  get progress() {
    const st = this.salesstatus + this.postatus + this.currencystatus;
    return st ? 'Fetching ' + st : '';
  }
  errorMessage = '';

  salesstatus = '';
  postatus = '';
  currencystatus = '';

  constructor(private data: DataService, private user: UserService) {

  }

  getSalesChartInput(title: string, year: FinancialYear): ChartInput {
    const ch = new ChartInput();
    ch.chartTitle = title;
    ch.financialYear = () => year.toString();
    return ch;
  }

  initChartInputs() {
    this.prevPoChartInput = new ChartInput();
    this.pochartinput = new ChartInput();
    this.prevSalesChartInput = new ChartInput();
    this.saleschartinput = new ChartInput();
    this.inrchartinput = new ChartInput();
    this.prevInrChartInput = new ChartInput();

    this.prevSalesChartInput.chartTitle = 'Tismo Group Revenue';
    this.prevSalesChartInput.financialYear = () => this.prevFinYear.toString();
    this.saleschartinput.financialYear = () => this.finYear.toString();

    this.prevPoChartInput.chartTitle = 'Tismo Group New Purchase Orders';
    this.prevPoChartInput.financialYear = () => this.prevFinYear.toString();
    this.pochartinput.financialYear = () => this.finYear.toString();

    this.prevInrChartInput.chartTitle = 'USD to INR';
    this.prevInrChartInput.isRate = true;
    this.prevInrChartInput.financialYear = () => this.prevFinYear.toString();

    this.inrchartinput.financialYear = () => this.finYear.toString();
  }

  ngOnInit() {
    try {
      console.log('dashboard ngOnInit ');
      this.init();
    } catch (e) {
      this.errorMessage = 'Error while fetching information.';
    }
  }

  // refresh() {
  //   this.ref.detectChanges();
  // }

  clearStatus() {
    this.salesstatus = '';
    this.postatus = '';
    this.currencystatus = '';
  }

  init() {
    try {
      this.errorMessage = '';
      this.clearStatus();

      this.initChartInputs();

      const sc = this.salesCard();
      const poc = this.poCard();

      sc.then(() => poc.then(() => { this.INRCurrencyCard(); }));


    } catch (err) {
      this.errorMessage = 'Connection Errors';
      this.clearStatus();
    }
  }

  OnCompanyChange() {
    this.init();
  }

  getTismoSales(month: number, year: number, company: string): Promise<number> {
    switch (company) {
      case 'Germany':
        return this.data.getTismoGermanySales(month, year).toPromise();
      case 'USA':
        return this.data.getTismoUSSales(month, year).toPromise();
      case 'Group':
        return this.data.getTismoGroupSales(month, year).toPromise();
      default:
      case 'India':
        return this.data.getTismoIndiaSales(month, year).toPromise();
    }
  }

  getSalesTitle(): string {
    switch (this.company) {
      case 'Germany':
        return 'Tismo Germany Revenues';
      case 'USA':
        return 'Tismo US Revenues';
      case 'Group':
        return 'Tismo Group Revenues';
      default:
      case 'India':
        return 'Tismo India Revenues';
    }
  }

  async salesCard() {
    this.salesstatus = ' Sales ';

    const prevchartinput = this.getSalesChartInput(this.getSalesTitle(), this.prevFinYear);
    const chartinput = this.getSalesChartInput(this.getSalesTitle(), this.finYear);

    const prevMonthYear = this.prevFinYear.getFyMonthAndQuarterRangeForFullYear();
    prevchartinput.months = await this.salesMonthValueList(this.company, prevMonthYear);

    const monthyear = this.finYear.getFyMonthAndQuarterRangeForFullYear();
    chartinput.months = await this.salesMonthValueList(this.company, monthyear);

    this.saleschartinput = chartinput;
    this.prevSalesChartInput = prevchartinput;

    this.salesstatus = '';

  }

  async poCard() {
    this.postatus = ' POs ';
    this.potitle = this.getPOTitle();

    const prevMonthYear = this.prevFinYear.getFyMonthAndQuarterRangeForFullYear();
    this.prevPoChartInput.months = await this.poMonthValueList(this.company, prevMonthYear);

    const monthyear = this.finYear.getFyMonthAndQuarterRangeForFullYear();
    this.pochartinput.months = await this.poMonthValueList(this.company, monthyear);

    this.postatus = '';

  }

  salesMonthValueList(company: string, monthRange: MonthYear[]): Promise<Month[]> {
    return this.getMonthValuesForFullYear(company, monthRange, (m, y, s) => this.getTismoSales(m, y, s));
  }

  poMonthValueList(company: string, monthRange: MonthYear[]): Promise<Month[]> {
    return this.getMonthValuesForFullYear(company, monthRange, (m, y, c) => this.getTismoPO(m, y, c));
  }

  getMonthValuesForFullYear(
    company: string,
    monthRange: MonthYear[],
    fn: (m: number, y: number, c: string) => Promise<number>
  ): Promise<Month[]> {
    // const monthRange = this.finYear.getFyMonthRangeForFullYear();
    return this.getMonthValues(monthRange, company, fn);
  }

  getMonthValues(
    monthRange: MonthYear[],
    company: string,
    fn: (m: number, y: number, c: string) => Promise<number>
  ): Promise<Month[]> {
    const lst = monthRange.map(async mth => {
      const m: Month = {
        monthNumber: mth.month,
        monthValue: await fn(mth.month, mth.year, company)
      };
      return m;
    });
    return Promise.all(lst);
  }

  getPOTitle(): string {
    switch (this.company) {
      case 'Germany':
        return 'Tismo Germany New Purchase Orders';
      case 'USA':
        return 'Tismo US New Purchase Orders';
      case 'Group':
        return 'Tismo Group New Purchase Orders';
      default:
      case 'India':
        return 'Tismo India New Purchase Orders';
    }
  }

  getTismoPO(month: number, year: number, company: string): Promise<number> {
    switch (company) {
      case 'Germany':
        return this.data.getTismoGermanyPOamount(month, year).toPromise();
      case 'USA':
        return this.data.getTismoUSPOamount(month, year).toPromise();
      case 'Group':
        return this.data.getTismoGroupPOamount(month, year).toPromise();
      default:
      case 'India':
        return this.data.getTismoIndiaPOamount(month, year).toPromise();
    }
  }

  INRCurrencyCard() {
    this.user.currentUser.subscribe(
      ud => {
        //if (ud.ismgmt) {
          // show INR chart only for mgmt users
          this.currencystatus = ' USD-INR ';

          this.data.getINRCurrency().then((inr) => {
            this.GetCurrencyCard(inr.id, this.prevFinYear, true).then(mthdata => {
              this.prevInrChartInput.months = mthdata;
              this.GetCurrencyCard(inr.id, this.finYear, true).then(mdata => {
                this.inrchartinput.months = mdata;
                this.currencystatus = '';
              });
            });
          });

        },
    //  },
      err => {
        console.log(err);

      }
    );
  }
  // async EurCurrencyCard() {
  //     this.progress = 'Fetching EUR currency rate information ...';
  //     const eur = await this.data.getEURCurrency();
  //     this.eurMonths = await this.GetCurrencyCard(eur.id, false);
  //     this.progress = '';
  // }
  // async GBPCurrencyCard() {
  //     this.progress = 'Fetching GBP currency rate information ...';
  //     const gbp = await this.data.getGBPCurrency();
  //     this.gbpMonths = await this.GetCurrencyCard(gbp.id, false);
  //     this.progress = '';
  // }
  async GetCurrencyCard(currencyId: number, yr: FinancialYear, flip: boolean) {
    const monthRange = yr.getFyMonthAndQuarterRangeForFullYear();
    const now = new Date();
    const lst = monthRange.map(async mth => {
      let rate = 0;
      if (new Date(mth.year, mth.month - 1, 1) <= now) {
        // our months are 1 based, Date constructor takes 0 based
        rate = await this.data.getCurrencyRate(currencyId, mth.month, mth.year);
        rate = flip ? (rate > 0 ? 1.0 / rate : 0) : rate;
      }
      const m: Month = {
        monthNumber: mth.month,
        monthValue: rate
      };
      return m;
    });
    return Promise.all(lst);
  }
}
