import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { InvoiceInternalNewComponent } from './invoiceInternalNew.component';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-invoiceinternaledit',
  templateUrl: './invoiceForm.component.html',
  styleUrls: ['./invoiceForm.component.scss']
})
export class InvoiceInternalEditComponent extends InvoiceInternalNewComponent {
  constructor(
    data: DataService,
    protected router: Router,
    protected route: ActivatedRoute,
    protected location: Location
  ) {
    super(data, router, route, location);
  }

  updateTitles() {
    this.buttonTitle = this.viewMode ? 'Modify this invoice' : 'Save changes';
    this.formTitle = 'Internal Invoice';
  }

  async initialize(externalInvoiceId: number) {
    try {
      this.viewMode = true;
      this.updateTitles();
      this.showReceived = true;

      // first check if the id passed to us is for the external or internal
      let external = externalInvoiceId;
      let internal = 0;
      internal = await this.data.getInternalInvoiceForEndInvoice(external).toPromise();
      if (internal <= 0) {
        // id given is for internal invoice
        internal = externalInvoiceId;
        external = await this.data.getEndInvoiceforInternalInvoice(internal).toPromise();
        if (external <= 0) {
          console.log('Unable to find internal / external po ' + externalInvoiceId);
          this.resetForm(false);
          return;
        }
      }
      this.InternalInvoiceId = internal;
      await this.prefillData(external);
    } catch (e) {
      this.errorMessage = 'Error: Unable to get invoice details ' + e;
      this.resetForm(false);
    }
  }

  async prefillData(externalInvoiceId: number) {
    // get everything else from invoice Id
    this.externalInvoice = await this.data.getInvoice(externalInvoiceId).toPromise();
    this.externalPO = await this.data.getPurchaseOrder(this.externalInvoice.purchaseOrderId).toPromise();
    this.externalVendor = await this.data.getVendor(this.externalPO.vendorId);
    this.invoice = await this.data.getInvoice(this.InternalInvoiceId).toPromise();
    this.selectedPo = await this.data.getPurchaseOrder(this.invoice.purchaseOrderId).toPromise();
    this.clientId = this.selectedPo.clientId;
    this.client = await this.data.getClient(this.clientId);
    this.clientName = this.client.name;
    // this.purchaseOrders = [this.selectedPo];
    this.curSymbol = await this.data.getcurSymbol(this.selectedPo.currencyId);
    this.vendor = await this.data.getVendor(this.selectedPo.vendorId);
    this.projects = await this.data.getListOfProjectsForClient(this.externalPO.clientId).toPromise();
    this.selectedProject = this.projects.find(p => p.id === this.invoice.projectId);
    this.invoice.receivedDate = new Date(this.invoice.receivedDate);
    if (this.invoice.receivedDate.getFullYear() > 2100) {
      this.invoice.receivedDate = new Date();
    }
    await this.getExternalInvoiceDetails();
    this.showClientSelection = false;

    // Only for Tismo India to Tismo GmbH invoices show the tip 
    // Period of service should be included in the description
    this.showPeriodOfServiceTip = this.data.isClientTismoGermany(this.client) && this.data.isTismoIndia(this.vendor.id); 

    await this.updateFileblob();
  }

  async onSubmit() {
    if (this.viewMode) {
      this.viewMode = undefined;
      this.updateTitles();
      return;
    }
    this.successMessage = '';
    this.errorMessage = '';
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = 'Saving ...';
    await this.data
      .updateInvoice(this.invoice)
      .toPromise()
      .then(
        () => {
          this.successMessage = 'Updated internal Invoice ' + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(true);
        },
        err => {
          this.buttonTitle = oldButtonTitle;
          this.setError('Could not update internal Invoice : ', err);
        }
      );

    // await this.updateFileblob();
  }
  resetForm(reInit: boolean) {
    const tout = reInit ? 500 : 2000;
    setTimeout(async () => {
      //  console.log('resetForm-internalEdit');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data.getInvoiceForInvoiceNumber(this.invoice.invoiceNumber).toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl('/editinternalinvoice/' + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  editInternalInvoice() {}
}
