import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { UserService } from './user.service';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';


@Injectable()
export class Authguard implements CanActivate {
    constructor(private router: Router, private user: UserService) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
      if (route.url[0] && route.url[0].path &&  route.url[0].path === 'createuser') {
          return this.user.currentUser.pipe(map(ud => ud.isadmin > 0 ? true : this.router.parseUrl('/')));
      }
      return this.user.currentUser.pipe(map(ud => ud.user ? true : this.router.parseUrl('/login')));
      // if a user is logged in return true, otherwise re-direct to login screen
  }
}
