import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { Project } from '../data/project';
import { Utils } from '../utils/utils';
import { Invoice } from '../data/invoice';

// for csv export - provide category and country wise sum
function summarizeCategory(invoiceList: InvoiceViewObj[]) {
    const rowNumber = invoiceList.length + 1;
    const start = '\r\n\r\n\r\n';
    const tot = 'Total (INR),' + `"=SUM(M2:M${rowNumber})"`;
    const header = '\r\n\r\nSTP Category, Sum (INR)\r\n';
    let blanks = 6;
    const compute = Project.STPServiceTypeOptions().map(
        (st, i) => Project.getSTPServiceStr(st.val) + ',' + `"=SUMIF(E2:E${rowNumber},A${rowNumber + i + blanks},M2:M${rowNumber})"`
    );
    const categorysum = start + tot + header + compute.join('\r\n') + '\r\n\r\n';

    const countries = invoiceList.map(st => st.clientCountryCode);
    const countryCodes = [...new Set(countries)]; // unique -- removed duplicates
    blanks += Project.STPServiceTypeOptions().length + 3;
    const countrySummary = countryCodes.map(
        (c, i) => c + ',' + `"=SUMIF(B2:B${rowNumber},A${rowNumber + i + blanks},M2:M${rowNumber})"`
    );

    return categorysum + '\r\nCountry, Sum (INR)\r\n' + countrySummary.join('\r\n');

}

export function
    getCSV(invoiceList: InvoiceViewObj[]): string {
    const tmpList = invoiceList.map(inv => {
        return {
            Client: inv.client.name,
            Country: inv.clientCountryCode,
            ProjectCode: inv.projectCode,
            PONumber: inv.poNumber,
            PODate: Utils.getDateStr(inv.poDate),
            STPServiceType: Project.getSTPServiceStr(inv.stpService),
            InvoiceNumber: inv.invoice.invoiceNumber,
            SoftexNumber: inv.invoice.softexNumber,
            EBRCNumber: inv.invoice.ebrcNumber,
            InvoiceType: inv.invoice.isExpenseInvoice ? 'Expense' : 'Revenue',
            InvoiceDate: Utils.getDateStr(inv.invoice.invoiceDate),
            Description: inv.invoice.description,
            Currency: inv.curName,
            Amount: Utils.toStr(inv.invoice.amount),
            AmountInUSD: Utils.toStr(inv.amountInUSD),
            AmountInINR: Utils.toStr(inv.amountInINR),
            Status: inv.status,
            Received: Invoice.isReceived(inv.invoice) ? Utils.toStr(inv.invoice.receivedAmount) : ' - ',
            ReceivedDate: Invoice.isReceived(inv.invoice) ? Utils.getDateStr(inv.invoice.receivedDate as Date) : ' - ',
            BankCharges: Invoice.isReceived(inv.invoice) ? inv.invoice.bankCharges : ' - ',
            BankTransaction: Invoice.isReceived(inv.invoice) ? inv.invoice.bankTransaction : ' - ',
            Mgmt: inv.manager1,
            Manager: inv.manager2,
            Remarks: inv.invoice.remarks
        };
    });
    const invcsv = Utils.tocsv(tmpList);
    const summary = summarizeCategory(invoiceList);
    return invcsv + summary;
}

export function getSoftexFormatCSV(invoiceList: InvoiceViewObj[]): string {
    const tmpList = invoiceList
        .filter(inv => !inv.invoice.isExpenseInvoice) // softex - exclude expense invoices
        .filter(inv => inv.clientCountryCode !== 'IN') // softex - exclude invoices to Indian clients
        .map((inv, i) => {
            return {
                ContractNo : inv.poNumber,
                ContractDate: Utils.getTwoDigitDateStr(inv.poDate),
                ClientName: inv.client.name ,
                ClientAddress: inv.clientAddress ,
                CountryCode: inv.clientCountryCode ,
                ModOfRealization: 3 ,
                SoftwareType : 907,
                SoftwareCategory: ' ' ,
                SoftexNo: inv.invoice.softexNumber,
                InvoiceNo: inv.invoice.invoiceNumber,
                InvoiceDate: Utils.getTwoDigitDateStr(inv.invoice.invoiceDate),
                HSN_SAC_Code: 998314,
                CurrencyCode: inv.curName,
                ExportValue: Utils.toStrWithNoDecimals(inv.invoice.amount),
                CommissionAmount: 0,
                DeductionAmount:0 ,
                TransmissionAmount: 0,
                ExportValue_INR: Utils.toStrWithNoDecimals(inv.amountInINR)

            };
        });
    return Utils.tocsv(tmpList);
}

