import { Component, OnInit } from '@angular/core';
import { DataService, PurchaseOrder } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';

import { OrderInternalNewComponent } from './orderInternalNew.component';
import { UserService } from '../data/user.service';

@Component({
    selector: 'app-orderinternaledit',
    templateUrl: './orderForm.component.html',
    styleUrls: ['./orderForm.component.scss']
})
export class OrderInternalEditComponent extends OrderInternalNewComponent implements OnInit {

    externalPOId = 0;

    isUpdating = false;
    
    get formTitle() { return 'Internal Purchase Order'; }


    updateButtonTitle()
    {
        this.buttonTitle =
            this.viewMode ? 'Edit this Purchase Order'
            : this.isUpdating ? 'Updating ...'
            : 'Update this Purchase Order';
    }

    constructor(data: DataService, protected router: Router, protected route: ActivatedRoute,
        protected user: UserService,
        protected location: Location) {
        super(data, router, route, user, location);
        this.updateButtonTitle();
    }

    // the id is the ID of the external PO OR the internal PO
    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        this.externalPOId = idstr ? +idstr : 0; // assume it is external PO for now
      
        this.initialize();
    }

    getThePOs() {
        this.setupAttachments(this.InternalPOId);
        this.data.getPurchaseOrder(this.externalPOId).subscribe(
            extpo => {
                this.externalPO = extpo;
                this.getExternalPODetails().then(
                    () => {
                        this.data.getPurchaseOrder(this.InternalPOId)
                            .subscribe(
                                cl => {
                                    this.po = cl;
                                    this.updating = true;
                                    this.showIsCompleted = true; // internal PO can be closed separately from the end client PO
                                    this.viewMode = true;
                                    this.showUploadPOFile = false; // for internal PO form do not show upload PO prompt
                                    this.updateButtonTitle();
                                    this.showClientSelection = false;
                                    this.updateFileblob();
                                    this.data.getClient(this.po.clientId).then(cl => this.clientName = cl.name);
                                    this.viewMode = true;
                                    this.updateButtonTitle();
                                }
                                ,
                                err => { console.log(err); this.setError('Error : Could not locate internal PO :', err); });
                    }
                );
            }
        );
    }


    // the id could be of the external PO or the internal PO
    initialize() {
        this.data.getInternalPOforEndPO(this.externalPOId).subscribe(
            internal => {
                if (internal <= 0) {  // id given to us is probably for the internal PO.
                    internal = this.externalPOId;
                    this.data.getEndPOforInternalPO(internal).subscribe(
                        external => {
                            if (external <= 0) {
                                console.log('Error: Could not locate internal of external po for ' + this.externalPOId);
                                this.errorMessage = ' Could not locate internal PO';
                                this.resetForm();
                                return;
                            }
                            this.InternalPOId = internal;
                            this.externalPOId = external;
                            this.getThePOs();
                        }
                    );
                } else {
                    this.InternalPOId = internal;
                    this.getThePOs();
                }
            });
    }



    onSubmit() {
        if (this.viewMode) {
            this.viewMode = undefined;
            this.updateButtonTitle();
            return;
        }
        this.successMessage = '';
        this.errorMessage = '';
        this.po.isFixedPrice = this.po.isFixedPrice ? 1 : 0;
        this.po.completed = this.po.completed ? 1 : 0;
        console.log('Update internal PO' + this.po.clientId, this.po.amount, this.po.poDate, this.po.currencyId, this.po.description,
            this.po.isFixedPrice, this.po.tandMBillRate, this.po.completed, '***');
        try {
            this.isUpdating = true;
            this.updateButtonTitle();
            this.data.updatePurchaseOrder(this.po)
                .subscribe(p => {
                    this.upLoadPOFile();
                    this.successMessage = 'Internal PO updated';
                    this.goToCurrentPO(this.po.id);
                    this.isUpdating = false;
                    this.updateButtonTitle();
                });
        } catch (e) {
            console.log(e);
            this.setError('Could not update PO: Error: ', e);
            this.resetForm();
        }
    
    }

}

