import { Component } from '@angular/core';
import { DataService } from '../data/data.service';
import { trigger, style, animate, transition } from '@angular/animations';
import { SBAssignmentMgr } from './SBAssignmentMgr';



export interface SBAssignment {
    numToAssign: string;
    invReference: string; // invoice reference can be invoice number or softex number
    invoiceId: number;
    status: string;
}

@Component({
    selector: 'app-softeximport',
    templateUrl: './softexImport.component.html',
    styleUrls: ['./softexImport.component.scss'],
    animations: [
        trigger(
            'inOutAnimation',
            [
                transition(
                    ':enter',
                    [
                        style({ opacity: 0 }),
                        animate('0.25s ease-out',
                            style({ opacity: 1 }))
                    ]
                ),
                transition(
                    ':leave',
                    [
                        style({ opacity: 1 }),
                        animate('0.25s ease-in',
                            style({  opacity: 0 }))
                    ]
                )
            ]
        )
    ]
})
  
export class SoftexImportComponent  {
   
    sbm: SBAssignmentMgr

    pageTitle = 'Softex Import'

    instruction = 'Copy entire Softex No - Invoice No columns from Excel and paste it here in the box.'

    
    constructor(protected data: DataService) {

        this.sbm = new SBAssignmentMgr()
        this.sbm.placeholder =
`S21002110983	TD21AA001
S21002110984	TD21AA002
S21002110985	TD21AA003
S21002110992	TD21AA010`
        
        this.sbm.isThisTheCorrectInvoice = (p, inv) => p.invReference == inv.invoiceNumber
        this.sbm.alreadyAssigned = (p, inv) => p.numToAssign == inv.softexNumber
        this.sbm.assignAndUpdate = (p, inv) => {
            inv.softexNumber = p.numToAssign
            return this.data.updateInvoice(inv)
        }
        this.sbm.fetchInvoice = p => this.data.getInvoiceForInvoiceNumber(p.invReference)


    }
}



