import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { Utils } from '../utils/utils';
import { ProjectViewModel } from '../data/ProjectViewModel';


@Component({
    selector: 'app-project',
    templateUrl: './project.component.html',
    styleUrls: ['./project.component.scss']
})
export class ProjectComponent implements OnInit {


    @Input()
    pvmodel: ProjectViewModel | null = null;

    @Output()
    headerClicked: EventEmitter<string> = new EventEmitter();

    noInvoiceInLongTime = false;
    status = '';

    OnHeaderClicked(headerName: string) {
        this.headerClicked.emit(headerName);
    }
    ngOnInit(): void {
        if (this.pvmodel) {
            if (ProjectViewModel.firstInvoiceIsOverdue(this.pvmodel, 0)) {
                this.noInvoiceInLongTime = true;
                this.status = 'First invoice pending';
            } else if (ProjectViewModel.nextInvoiceOverdue(this.pvmodel, 3)) {
                this.noInvoiceInLongTime = true;
                this.status = 'Last invoiced ' + ProjectViewModel.lastInvoicedDateStr(this.pvmodel);
            }
        }
    }
}
