import { Component, Inject } from '@angular/core';
import { DataService,  Client, UserData } from '../data/data.service';
import { UserService } from '../data/user.service';


@Component({
    selector: 'app-clientlist',
    templateUrl: './clientList.component.html',
    styleUrls: ['./clientList.component.scss'],
})
export class ClientListComponent {
    clientList: Client[] = [];
    fullClientList: Client[] = [];
    serverError = false;
    filterTerm = '';
    userData = new UserData();
    get canAddNewClients() { return this.userData.clientPerm.add ? true : false; }
    get canUpdateClients() { return this.userData.clientPerm.update ? true : false; }

    constructor(data: DataService, private user: UserService) {
        user.currentUser.subscribe(
            ud => this.userData = ud,
            err => {
                console.log(err);
                this.serverError = true;
            }
        );

        data.getListOfClientsObs().subscribe(
            res => {
                this.fullClientList = res.sort( (a, b) =>  a.name.toUpperCase() >= b.name.toUpperCase() ? 1 : -1 );
                this.updateClientList();
            },
            err => {
            console.log('Error ' + err);
            this.serverError = true;
        });
    }

    updateClientList() {
        this.clientList = this.fullClientList.filter(this.filterForSearchTerm());
    }

    filterTermChanged(newSrchTerm: string) {
        this.filterTerm = newSrchTerm;
        this.updateClientList();
    }

    filterForSearchTerm(): (cl: Client) => boolean {
        if (this.filterTerm === '' || this.filterTerm.length < 2 ) { return (cl: Client) => true; }
        const srchTarget = (cl: Client) => [cl.name, cl.customerCode, cl.country].join(' ');
        const regEx = new RegExp(this.filterTerm, 'i');
        return (cl: Client) => regEx.test(srchTarget(cl));
    }

}


