import { Component, OnInit, Input } from '@angular/core';
import { Project } from '../data/project';
import { DataService, Manager, PurchaseOrder, Contact } from '../data/data.service';

@Component({
    selector: 'app-projectemail',
    templateUrl: './projectemail.component.html',
    styleUrls: ['./projectemail.component.scss']
})
export class ProjectEmailComponent implements OnInit {
    @Input()
    project!: Project 

    get clientEmailStr() {
        return this.strs.join(', ')
    }
    get ccEmailStr() {
        let set = new Set( [this.managerEmail, this.mgmtEmail, ...this.ccEmails ].map(str => str.trim()) )
        let lst = Array.from(set)
        lst = lst.filter(s => s)
        return lst.join(', ')
    }

    mgmtEmail = '';
    managerEmail = '';
    ccEmails:string[] = [];

    strs:string[] = []
    ngOnInit(): void {
        let ids = new Set( [this.project.clientSponsorId, this.project.clientManagerId, this.project.clientCommercialId] )
        ids.forEach(id => this.getContactEmailStr(id, str => this.strs.push(str)) )

        this.data.getListOfManagers()
            .subscribe(mgrs => this.processMgrs(mgrs))
    
        this.ccEmails = this.project.invoiceCopyTo.trim().split(',')
        console.log(this.ccEmails)
            
    }

    getContactEmailStr(contactId: number,  action: (str:string) => void ): void {
        if (contactId > 0) {
            this.data.getContact(contactId)
                .subscribe(c => action(this.contactEmailStr(c)))
        }
    }

    processMgrs(mgrs: Manager[]): void {
        if (this.project) {
            let pmid = this.project.projectManagerId
            let mgmtid = this.project.mgmtId
            let mgr = mgrs.find(m => m.id == pmid )
            if (mgr) {
                this.managerEmail = mgr.email; // this.emailStr(mgr.name, mgr.email);
            }
            let mgmt = mgrs.find(m => m.id ==  mgmtid)
            if (mgmt) {
                this.mgmtEmail = mgmt.email; // this.emailStr(mgmt.name, mgmt.email);
            }
        }
    }

    contactEmailStr(contact: Contact): string {
        return this.emailStr(contact.name, contact.email);
    }

    emailStr(name: string, email: string): string {
        return `${name}<${email}>`;
    }

    constructor(protected data: DataService) {
       
    }
    
}

