import { Component, OnInit } from '@angular/core';
import { DataService, Client } from '../data/data.service';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';


@Component({
    selector: 'app-clientedit',
    templateUrl: './clientNew.component.html',
    styleUrls: ['./clientNew.component.scss']
})
export class ClientEditComponent implements OnInit {
    client: Client = new  Client();

    formTitle = 'Modify Client';
    btnTitle = 'Update this Client';
    successMessage = '';
    errorMessage = '';

    constructor(private data: DataService, private route: ActivatedRoute,  private location: Location) {

    }
    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        const id = idstr ? +idstr : 0;
        this.data.getClient(id)
          .then(cl => this.client = cl, err => { console.log(err); this.setError(err); });
    }

    setError(err: any) {
        this.errorMessage = 'Error: Could not update: ' + err.Error;
    }


    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Updating Client' + this.client.name);
        this.data.updateClient(this.client)
            .subscribe(c => { console.log(c); this.successMessage = 'Updated ' + this.client.name; this.resetForm(); },
                err => { console.log(err); this.setError(err); });
    }

    resetForm() {
        setTimeout(() => {
            this.location.back();
        }, 1000);
    }
}

