import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService, UserData } from '../data/data.service';
import { UserService } from '../data/user.service';
// import { GoogleSignInSuccess } from 'angular-google-signin';
import '../../buildString';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent {
  userName = '';
  password = '';
  btnTitle = 'Login';
  successMessage = '';
  errorMessage = '';
  formTitle = 'Login';

  build = '';

  constructor(protected router: Router, protected user: UserService, protected data: DataService) {
    this.build = buildString;
  }

  onClick() {
    console.log('logging in');
    this.errorMessage = '';
    this.successMessage = '';
    this.user.login( this.userName, this.password).subscribe(
      user => {
        if (user) {
          this.successMessage = 'Logged In';
          this.router.navigateByUrl('/clientlist');
        } else {
          this.errorMessage = 'Login failure';
        }
      },
      err => {
        this.errorMessage = 'Login failure';
        console.log(err);
      }
    );
  }
}
