import { Component, Input } from '@angular/core';
import { Utils } from './utils';
import { merge, interval, Observable, Observer } from 'rxjs';
import { map, debounceTime, distinctUntilChanged } from 'rxjs/operators';


@Component({
    selector: 'app-statusmsg',
    template: `
       <div class='statusmsg text-muted' [ngClass]="{'fadeIn': doFadeIn, 'fadeOut': doFadeOut }"  >
                    {{message}}
       </div>
    `,
    styleUrls: ['../app.styles.scss'],
    styles: [`
        .statusmsg {
            height: 20px;
        }
    `]

})
export class StatusMsgComponent {
    public doFadeIn = false;
    public doFadeOut = false;

    // fade in and out duration - value defined
    // in the transitions in style.css file
    fadeDuration = 500; // milliseconds


    // how long is the message displayed after which
    // it would automtically be erased
    private _duration = 5000; // milliseconds
    @Input('duration')
    public set duration(value: number) {
        this._duration = value;
    }
    public get duration() {
        return this._duration;
    }

    private _message = '';

    @Input()
    public set message(value: string) {
        // if (!value) { return; }
        this.setMsg(value);
    }
    private setMsg = (str: string) => { };
    public get message() { return this._message; }

    constructor() {
        const subject: Observable<string> = Observable.create(
            (obs: Observer<string>) => {
                this.setMsg = str => {
                    obs.next(str);
                };
            }
        );

        const source = merge(
            subject,
            interval(this._duration).pipe( map(_ => '')),
        );
        source.pipe(
            debounceTime(this.fadeDuration),
            distinctUntilChanged()
        ).subscribe(
            async str => {
                if (this._message) {
                    this.doFadeIn = false;
                    this.doFadeOut = true;
                    await Utils.delay(this.fadeDuration); // wait for previous msg to fade out
                }

                this._message = str; // put in the new msg
                this.doFadeOut = false;
                this.doFadeIn = true;
            }
        );
    }

}
