import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../data/user.service';
import { DataService } from '../data/data.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  userName = '';

  fullName = '';
  password = '';
  email = '';
  role = '';

  btnTitle = 'Create User';
  successMessage = '';
  errorMessage = '';
  formTitle = 'Create User';

  constructor(protected router: Router, protected user: UserService, protected data: DataService) {}

  ngOnInit() { }
  
  goHome( delayTime: number = 2000) {
    setTimeout(() => {
      this.router.navigateByUrl('/')
    }, delayTime);
  }
  onClick() {
    console.log('Creating user');
    this.errorMessage = '';
    this.successMessage = 'Creating user ...';
    this.user.createUser(this.userName,this.fullName, this.password, this.email, this.role).subscribe(
      res => {
        if (res > 0) {
          this.successMessage = 'User created';
        } else {
          this.errorMessage = 'Unable to create user';
        }
        this.userName = '';
        this.fullName = '';
        this.password = '';
        this.email = '';
        this.role = '';

      },
      err => {
        this.errorMessage = 'Unable to create user';
        console.log(err);
      }
    );
  }
}
