import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OrderComponent, POStatus } from '../orderbook/order.component';
import { POViewInterface, POViewWithBalance } from '../data/POViewInterface';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { Router } from '@angular/router';
import { DataService } from '../data/data.service';
import { from } from 'rxjs';

@Component({
    selector: 'app-clientpo',
    templateUrl: './clientpo.component.html',
    styleUrls: ['./clientpo.component.scss']
})
export class ClientpoComponent extends OrderComponent implements OnInit {
    // @Input()
    // orderProormise!: Promise<POViewWithBalance>;

    @Input()
    invoiceList: InvoiceViewObj[] = [];

    @Input()
    canCreateInvoice = false;

    @Output()
    statusValueEvent: EventEmitter<POStatus> = new EventEmitter();


    showNewInvoiceButton = false;
    showthispo = true;
    negativebalance = false;
    clientabactive = true;
    internaltablactive = false;


    constructor(private router: Router, protected data: DataService) {
        super(data);
    }
    ngOnInit() {
        this.initialize();
        if (!this.order) {
            console.log ("**  order is null")
        }
      
        if (this.order) {
            this.showthispo = !this.order.completed;
            this.showNewInvoiceButton = !this.order.isInternalPO && !this.order.completed;
            this.negativebalance = this.order.balance < 0;
            this.statusValueEvent.emit(this.statusValue);
        }
    
        
        
    }
    toggleShowThisPO() {
        this.showthispo = !this.showthispo;

    }
    onNewInvoice() {
        this.router.navigate(['/newinvoice', { poid: this.order!.id, clientId: this.clientId }]);
    }
}
