import { Component, OnInit } from '@angular/core';
import { DataService, Client } from '../data/data.service';

import { Project } from '../data/project';
import { map, mergeAll, mergeMap } from 'rxjs/operators';
import { Utils } from '../utils/utils';
import { Invoice } from '../data/invoice';
import { ProjectViewModel } from '../data/ProjectViewModel';


interface IDictionary {
    [index: string]: boolean;
}
@Component({
    selector: 'app-projectlist',
    templateUrl: './projectList.component.html',
    styleUrls: ['./projectList.component.scss'],
})
export class ProjectListComponent implements OnInit {
    projectList: ProjectViewModel[] = [];
    fullProjectList: ProjectViewModel[] = [];
    serverError = false;
    message = '';
    filterTerm = '';
    sortState = {} as IDictionary;
    constructor(private data: DataService) {
    }

    ngOnInit() {
        this.getProjectList();
    }

    getProjectList() {
        this.message = 'Fetching project information...';

        this.data.getAllActiveProjectListView()
            .subscribe(pv => this.fullProjectList.push(...pv),
                err => { this.message = ''; this.serverError = true; console.log(err); }, // on eror
                () => this.updateProjectList()) // on completion )
            ;
        // this.data.getListOfClientsObs()
        //     .pipe(
        //         mergeAll(),
        //         mergeMap(client => this.data.getProjectViewListForClient(client.id)),
        //     )
        //     .subscribe(
        //         pv => this.fullProjectList = this.fullProjectList.concat(pv),
        //         err => { this.message = ''; this.serverError = true; console.log(err); }, // on eror
        //         () => this.updateProjectList() // on completion
        //     );
    }

    updateProjectList() {
        this.message = '';
        this.projectList = this.fullProjectList
            .filter(this.getFilterForActiveProjects())
            .filter(this.getFilterForSearchTerm())
            .sort((a, b) => a.project.startDate > b.project.startDate ? -1 : 1)
            ;
    }

    filterTermChanged(newSrchTerm: string) {
        this.filterTerm = newSrchTerm;
        this.updateProjectList();
    }

    getFilterForActiveProjects(): (cl: ProjectViewModel) => boolean {
        return pr => pr.project.completed > 0 ? false : true;
    }

    getFilterForSearchTerm(): (cl: ProjectViewModel) => boolean {
        if (this.filterTerm === '' || this.filterTerm.length < 2) { return (cl: ProjectViewModel) => true; }
        const srchTarget = (proj: ProjectViewModel) =>
            [
                proj.project.name,
                proj.project.projectCode,
                proj.client.name,
                proj.client.country,
                proj.client.customerCode,
                Utils.getDateStr(proj.project.startDate),
                proj.accountManager,
                proj.projectManager,
            ].join(' ');

        const regEx = new RegExp(this.filterTerm, 'i');
        return (pr: ProjectViewModel) => regEx.test(srchTarget(pr));
    }


    OnHeaderClicked(headerName: string) {
        this.sortState[headerName] = !this.sortState[headerName];
        let fil = (a: ProjectViewModel, b: ProjectViewModel) => 1;
        switch (headerName) {
            case 'client':
                fil = (a, b) => (a.client.name > b.client.name ? +1 : -1);
                break;
            case 'projectCode':
                fil = (a, b) => (a.project.projectCode > b.project.projectCode ? +1 : -1);
                break;
            case 'projectname':
                fil = (a, b) => (a.project.name > b.project.name ? +1 : -1);
                break;
            case 'startdate':
                fil = (a, b) => (a.project.startDate.getTime() > b.project.startDate.getTime() ? 1 : -1);
                break;
            case 'accountManager':
                fil = (a, b) => (a.accountManager) > b.accountManager ? 1 : -1;
                break;
            case 'projectManager':
                fil = (a, b) => (a.projectManager) > b.projectManager ? 1 : -1;
                break;
        }

        // check if we have to toggle ascending / descending
        const gil = this.sortState[headerName] ? fil : (a: ProjectViewModel, b: ProjectViewModel) => -1 * fil(a, b);
        this.projectList = this.projectList.sort(gil);
    }
}


