import { Component, Input, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { DataService, Client } from '../data/data.service';

@Component({
    selector: 'app-clientnew',
    templateUrl: './clientNew.component.html',
    styleUrls: ['./clientNew.component.scss']
})
export class ClientNewComponent {
    client: Client = new Client();
    clientList: Client[] = [];

    formTitle = 'New Client';
    btnTitle = 'Add this Client';
    successMessage = '';
    errorMessage = '';

    constructor(private data: DataService) {
        data.getListOfClients()
            .then(res => this.clientList = res,
                err => {
                    console.log('Error ' + err);
                    this.setError(err);
                });
        this.init();
    }

    init() {
        this.data.getNextCustomerCode().subscribe(cc => this.client.customerCode = cc);
        this.client = new Client();
        this.successMessage = '';
        this.errorMessage = '';
    }

    setError(err: any) {
        this.errorMessage = 'Error: Could not update: ' + err;
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Submitting new Client' + this.client.name);
        const nameDuplicate = this.clientList.findIndex((c, i) => (c.name === this.client.name));
        const codeDuplice = this.clientList.findIndex((c, i) => (c.customerCode === this.client.customerCode));
        if (nameDuplicate !== -1) {
            this.errorMessage = 'Client with same name already present';
        } else if (codeDuplice !== -1) {
            this.errorMessage = 'Duplicate customer code.';
        } else {
            this.data.addClient(this.client)
                .subscribe(c => {
                    console.log(c); this.clientList.concat(this.client);
                    this.successMessage = 'Added new client ' + this.client.name;
                    this.resetForm();
                },
                    err => { console.log(err); this.setError(err); });
        }
    }

    resetForm() {
        setTimeout(() => {
           this.init();
        }, 1000);
    }
}

