import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'app';

  collapsedState = false;

  onCollapseChanged(collapsedVal: boolean) {
    if (collapsedVal) { // while collapsing move the main content after 250ms
      setTimeout(() => {
        this.collapsedState = collapsedVal;
      }, 250);
    } else {
      this.collapsedState = collapsedVal; // while expanding move the main content immediately
    }
  }
}
