import { Component, OnInit } from '@angular/core';
import { DataService, Client } from '../data/data.service';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Project } from '../data/project';
import { ProjectBase } from './projectBase';


@Component({
    selector: 'app-projectedit',
    templateUrl: './projectForm.component.html',
    styleUrls: ['./projectForm.component.scss']
})
export class ProjectEditComponent extends ProjectBase implements OnInit {
    get buttonTitle() { return this.viewMode ? 'Edit this Project' : 'Update this Project'; }
    get formTitle() { return this.viewMode ? 'Project' : 'Modify Project'; }

    constructor(protected data: DataService, protected route: ActivatedRoute, protected location: Location) {
        super(data, route, location);
        this.viewMode = true;
        this.showIsCompleted = true;
    }
    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        if (idstr) {
            const id = +idstr;
            this.data.getProject(id)
                .subscribe(proj => this.setupProject(proj),
                    err => { console.log(err); this.setError(err); });
        }
    }

    setupProject(proj: Project) {
        Object.assign(this.project, proj);
        this.effort = this.project.getEffortInPM();
        this.duration = this.project.getdurationInMonths();
        this.electronics = this.project.isElectronics();
        this.firmware = this.project.isFirmware();
        this.software = this.project.isSoftware();
        this.mechanical = this.project.isMechanical();
        this.onClientSelected();
        this.showForm = true;
    }


    onSubmit() {
        if (this.viewMode) {
            this.viewMode = undefined;
            return;
        }
        this.successMessage = '';
        this.errorMessage = '';
        this.prepareProject();
        
        
        this.data.updateProject(this.project)
            .subscribe(c =>
            {
                
                this.successMessage = 'Updated ' + this.project.projectCode;
                this.viewMode = true;
                setTimeout(() => {
                   // this.location.back();
                   this.successMessage = '';
                }, 2000);
            },
                err => { console.log(err); this.setError(err); });
    }

}

