import { Component, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { DataService, UserData } from '../data/data.service';

import { UserService } from '../data/user.service';



@Component({
  selector: 'app-navmenu',
  templateUrl: './navmenu.component.html',
  styleUrls: ['./navmenu.component.scss']
})
export class NavMenuComponent {
  @Output() collapseChanged: EventEmitter<boolean> = new EventEmitter();

  isCollapsed = false;
  isAdmin = false;
  isAccounts = false;
  isMgmt = false;
  
  errorMessage = '';
  userName = '';

  currentSelection = '';

  showAccountingMenus = false;

  constructor(protected router: Router, protected data: DataService, protected user: UserService) {

    this.init();
    this.user.currentUser.subscribe(currentUser => {
      this.userName = currentUser.user;
      this.isAdmin = currentUser.isadmin > 0;
      this.isAccounts = currentUser.isaccounting > 0;
      this.isMgmt = currentUser.ismgmt > 0;
      this.showAccountingMenus = this.isAccounts || this.isAdmin || this.isMgmt;
    });

   
  }

  init() {
  }

  onClientsClicked() {
    this.currentSelection = 'Clients';
  }
  onOrderBookClicked() {
    this.currentSelection = 'OrderBook';
  }
  onProjectsClicked() {
    this.currentSelection = 'Projects';
  }
  onInvoicesClicked() {
    this.currentSelection = 'Invoices';
  }
  onCreateUserClicked() {
    this.currentSelection = 'CreateUser';
  }
  onSuppliersClicked() {
    this.currentSelection = 'Suppliers';
  }

  onDashboardClick() {
    this.currentSelection = 'Dashboard';
  }

  onLogout() {
    this.user.logout();
  }
  onHomeClick() {
    this.isCollapsed = this.isCollapsed ? false : true;
    this.collapseChanged.emit(this.isCollapsed);
    this.init();
  }
  onMainNavClick() {
    // it should work only when it is in collapsed state
    this.isCollapsed = false;
    this.collapseChanged.emit(this.isCollapsed);
    this.init();
  }
}
