import { Component, OnInit } from '@angular/core';
import { DataService, PurchaseOrder } from '../data/data.service';

import { SortPipe } from '../utils/sort.pipe';
import { POViewInterface, POViewWithBalance } from '../data/POViewInterface';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Utils } from '../utils/utils';


interface IDictionary {
    [index: string]: boolean;
}


@Component({
    selector: 'app-orderbook',
    templateUrl: './orderbook.component.html',
    styleUrls: ['./orderbook.component.scss'],
})
export class OrderBookComponent implements OnInit {
    orders: POViewWithBalance[] = []; // filtered list
    ordersMainList: POViewWithBalance[] = [];
    filterTerm = '';

    balanceInUSD = '';
    potype = 'EndClientPOs';
    errorMessage = '';
    progressMessage = '';
    sortState = {} as IDictionary;
    constructor(private data: DataService, private sortpipe: SortPipe) {
    }

    ngOnInit() {
        this.progressMessage = 'Fetching purchase orders';
        this.data
          .getListOfActiveEndClientPurchaseOrders()
          .subscribe(
            pv => pv.forEach(p => this.addToMainList(p)),
            err => {
              this.errorMessage = 'Connection Errors';
              this.progressMessage = '';
              console.log(err);
            },
            () => (this.progressMessage = '') // on completion
          );
    }

  

    filterTermChanged(newSrchTerm: string) {
        this.filterTerm = newSrchTerm;
        this.updateFilteredList();

    }

    getSearchFilter() {
        if (!this.filterTerm || this.filterTerm.length < 2) {
            return () => true;
        }
        const srchTarget = (po: POViewInterface) =>
            [po.poNumber,
            po.description,
            po.clientName,
            // po.clientCountry,
            po.poDate,
            po.amount,
            po.vendorName,
            // po.extPO.extPOClientName,
            // po.extPO.extPONumber,
            ].join(' ');

        const regEx = new RegExp(this.filterTerm, 'i');

        return (po: any) => regEx.test(srchTarget(po));
    }

    // private isInternalPO(po: POViewInterface) {
    //     return po.extPO.extPOId > 0;
    // }


    
    private addToMainList(pv: POViewInterface) {
        this.data.getPOViewWithBalance(pv)
            .then(pvb => {
                this.ordersMainList.push(pvb);
                this.updateFilteredList();
            });
    }

    private updateFilteredList() {
        this.orders = this.ordersMainList
                          .filter(this.getFilter())
                          .sort((p1, p2) => p1.poDate > p2.poDate ? -1 : 1); // sort filtered list by podate
        const TotalBalanceInUSD = this.getTotalBalanceInUSD(this.orders);
        const curSymbol = '$';
        this.balanceInUSD = Utils.getAmountStr(curSymbol, TotalBalanceInUSD); // update total balance as well
    }

    private  getTotalBalanceInUSD( lst: POViewWithBalance[]) {
        return lst.filter(order => order.balanceInUSD > 0)
                  .reduce((acc, cur) => acc + cur.balanceInUSD, 0);
    }

    getFilter(): (po: POViewInterface) => boolean {
        // const typeFilter = (po: POViewInterface) =>
        //     this.potype === 'EndClientPOs' ? !this.isInternalPO(po) : this.isInternalPO(po);
        const srchFilter = this.getSearchFilter();
        const isActive = (po: POViewInterface) => !po.completed;
        return (po: POViewInterface) => srchFilter(po) && isActive(po);
        // typeFilter(po) && srchFilter(po) && isActive(po);
    }

    // addToFilteredList(povObj: POViewInterface) {
    //     if (this.getFilter()(povObj)) {
    //         this.orders.push(povObj);
    //     }
    // }

    // header clicked event
    OnSort(field: string) {
        this.sortState[field] = this.sortState[field] ? false : true;
        this.orders = this.sortpipe.transform(this.orders, field, this.sortState[field]);
    }
    OnPOTypeChange() {
        this.updateFilteredList();
    }

    getOrderLink(orderId: number, poNumber:string ) {
        return `=hyperlink( "${this.data.baseRawUrl}order/${orderId}" , "${poNumber}" )  `
    }
    async exportLink() {
        const filename = 'PurchaseOrders_' + '_Tismo_' + '_' + this.filterTerm;
        const exportedOrders = Promise.all( this.orders.map(async (order) => {
            return {
                ClientName: order.clientName,
                PODate: Utils.getTwoDigitDateStr(order.poDate),
                PONumber: this.getOrderLink(order.id, order.poNumber),
                PODescription: order.description,
                Currency: (await (this.data.getCurrency(order.currencyId))).name,
                Amount: order.amount,
                ExpenseBudget: order.expenseBudget,
                Vendor: order.vendorName,
                InternalPO: order.isInternalPO,
                FixedPrice: order.isFixedPrice,
                Completed: order.completed,
                Billed: order.billed,
                Expensed: order.expense,
                Received: order.received,
                BankCharges: order.bankCharges,
                DueAmount: order.dueAmount,
                Balance: order.balance,
                BalanceInUSD: order.balanceInUSD,
                LastInvoicedOn: Utils.getTwoDigitDateStr(order.lastInvoicedOn),
            };
        }));
        exportedOrders.then(
            expOrders => Utils.exportCSVFile( Utils.tocsv(expOrders), filename)
        );
    }

}


