import { Component, Output, EventEmitter, OnInit } from '@angular/core';
import { MonthYear, FinancialYear } from './financialYear';


@Component({
    selector: 'app-dateselect',
    templateUrl: './dateselect.component.html',
    styleUrls: ['./dateselect.component.scss']
})
export class DateselectComponent implements OnInit {

    @Output() DurationChanged: EventEmitter<MonthYear[]> = new EventEmitter();

    finy = FinancialYear.GetCurrentFinancialYear();

    get finYear() { return this.finy.toString(); } // FY 2018-19
    get startYear() { return this.finy.startYear; }
    get endYear() { return this.finy.endYear; }



    // currentYearIndex = (new Date()).getFullYear() - 2018;

    monthIndex = 0; // Note: The user interface assigns a string value to this !!
    months!: string[];

    constructor() {
                    this.updateMonths();
                    const dt = new Date();
                    const yr = dt.getFullYear();
                    const mt = dt.getMonth(); // 0 for Jan
                    const currentMonth = mt;
                    this.monthIndex = this.finy.getFyMonthNumber(currentMonth + 1); // calendar month number to financial month index
                  }

    ngOnInit(): void {
        this.onMonthChanged();
    }


    updateMonths() {
        this.months = this.finy.rawmonths.map(
            (mth, index) => {
                if (index < 9) {
                    return mth + ' ' + this.finy.financialYear;   //  Apr 2018, May 2018 ...
                } else if (index < 12) {
                    return mth + ' ' + (this.finy.financialYear + 1); // Jan 2019, Feb 2019, Mar 2019, ...
                } else {
                    return mth;                 //  Q1, Q2, Q3, Q4, FY
                }
            });
    }

    onMonthChanged() {
        this.DurationChanged.emit(this.finy.getFyMonthRange(Number(this.monthIndex) ));
    }

    decrementFY() {
        this.finy = this.finy.PrevYear();
        this.monthIndex = 11; // decrement FY, show March
        this.updateMonths();
        this.onMonthChanged();
    }

    incrementFY() {
        this.finy = this.finy.NextYear();
        this.monthIndex = 0; // increment FY, show April
        this.updateMonths();
        this.onMonthChanged();
    }
}



