import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { InvoiceBase } from './invoiceBase';
import { AttachmentInterface } from '../attachments/attachments.component';

class fmonad<T>  {
  fobs: Promise<T>;
  constructor(obs: Promise<T>) {
    this.fobs = obs;
  }

  bind<U>(fn: (arg0: T) => Promise<U>): fmonad<U> {
    const k = this.fobs.then(t => fn(t));
    return new fmonad(k);
  }

  then(fn: (arg0: T) => any): any {
    return this.fobs.then(t => fn(t));
  }

  unwrap() {
    return this.fobs;
  }

};
@Component({
  selector: 'app-invoiceedit',
  templateUrl: './invoiceForm.component.html',
  styleUrls: ['./invoiceForm.component.scss']
})
export class InvoiceEditComponent extends InvoiceBase implements OnInit {
  // } implements OnInit {

  constructor(
    data: DataService,
    protected router: Router,
    private route: ActivatedRoute,
     
    protected location: Location
  ) {
    super(data, router);
    this.viewMode = true;
    this.updateTitles();
    this.showReceived = true;
  }

  updateTitles() {
    this.buttonTitle = this.viewMode ? 'Modify this invoice' : 'Save changes';
    this.formTitle = this.viewMode ? 'Invoice' : 'Modify Invoice';
  }

  ngOnInit() {
    const idstr = this.route.snapshot.paramMap.get('id');
    const id = idstr ? +idstr : 0;
    this.initialize(id);
  }

  initialize(id: number) {
    try {
      this.showForm = false;
      this.viewMode = true;
      this.successMessage = 'Fetching invoice details...';
      this.updateTitles();

      this.prefillData(id).then(() => {
        this.checkInternalInvoiceEdit().then(() => {
          this.successMessage = '';
          this.showForm = true;
        });
      });

    } catch (e) {
      this.errorMessage = 'Error: Unable to get invoice details ' + e;
      this.resetForm(false);
    }
  }

  prefillData(invoiceId: number): Promise<void> {
    // get everything else from invoice Id
    // check if this is an internal invoice first
    return this.data.getEndInvoiceforInternalInvoice(invoiceId)
      .toPromise()
      .then(externalInvoiceId => {
          if (externalInvoiceId > 0) {
            // this is an internal invoice
            this.router.navigateByUrl('/editinternalinvoice/' + externalInvoiceId);
            return Promise.resolve();
          }
          else {
            const af = new AttachmentInterface();
            af.id = invoiceId;
            af.addAttachment = (id, file) => this.data.addInvoiceAttachment(id, file);
            af.getAttachmentDetails = id => this.data.getListOfInvoiceAttachments(id);
            af.getAttachmentLink = id => this.data.getBaseInvoiceAttachmentLink(id);
            af.downloadResource = str => this.data.downloadResource(str);
            this.attachmentIntf = af;
            return this.getRestOfPrefillData(invoiceId);
          }
        });
  }


  getRestOfPrefillData(invoiceId: number): Promise<void> {
    
    return this.data.getInvoice(invoiceId).toPromise()
      .then(invoice => {
        this.invoice = invoice;
        return this.data.getPurchaseOrder(this.invoice.purchaseOrderId).toPromise();
      })
      .then(po => {
        this.selectedPo = po;
        this.clientId = po.clientId;
        return this.data.getClient(this.clientId);
      })
      .then(client => {
        this.clientName = client.name;
        this.client = client;
        this.showClientSelection = false;
        return this.data.getcurSymbol(this.selectedPo!.currencyId);
      })
      .then(curSymbol => {
        this.curSymbol = curSymbol;
        return this.data.getVendor(this.selectedPo!.vendorId);
      })
      .then(vendor => {
        this.vendor = vendor;
        return this.data.getListOfProjectsForClient(this.clientId).toPromise();
      })
      .then(projects => {
        this.projects = projects;
        this.selectedProject = this.projects.find(p => p.id === this.invoice.projectId);
        this.invoice.receivedDate = new Date(this.invoice.receivedDate);
        if (this.invoice.receivedDate.getFullYear() > 2100) {
          this.invoice.receivedDate = new Date();
        }
        if (this.invoice.paymentTerms < 0) {
          this.invoice.paymentTerms = this.selectedPo!.paymentTerms;
        }
        this.checkInternalInvoiceEdit()
          .then(() => this.updateFileblob()
            .then(() => this.getAttachmentList())
          );
        return Promise.resolve();
      });
    
  }

  getAttachmentList() {
   this.data.getListOfInvoiceAttachments(this.invoice.id)
      .subscribe(ls => {
        this.attachments = ls;
        this.showAttachmentButton = true;
      });
  }

  async fileUploadChanged(files: any) {
    if (files.length !== 1) {
      return;
    }
    const attachment = files[0] as File;
    this.attachmentmsg = 'Attaching ' + attachment.name;
    this.data.addInvoiceAttachment(this.invoice.id, attachment).subscribe(
       e => {
        this.attachmentmsg = attachment.name + ' attached to this invoice';
        this.getAttachmentList();
      },
      err => {
        this.attachmentmsg = 'Error: Attachment could not be uploaded ' + err;
        this.resetForm(false);
      }
    );
  }

  onSubmit() {
    if (this.viewMode) {
      this.viewMode = undefined;
      this.updateTitles();
      return;
    }
    this.successMessage = '';
    this.errorMessage = '';
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = 'Saving ...';
    this.data
      .updateInvoice(this.invoice)
      .toPromise()
      .then(
        () => {
          this.successMessage = 'Updated Invoice ' + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(true);
          // console.log('Update invoice successful');
        },
        err => {
          this.buttonTitle = oldButtonTitle;
          this.setError('Could not update Invoice : ', err);
        }
      );
  }

  resetForm(reInit: boolean) {
    const tout = reInit ? 2000 : 2000;
    setTimeout(async () => {
      // console.log('resetForm-invoiceEdit');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data.getInvoiceForInvoiceNumber(this.invoice.invoiceNumber).toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl('/invoice/' + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  editInternalInvoice() {
    if (this.InternalInvoiceId > 0) {
      this.router.navigateByUrl('/editinternalinvoice/' + this.invoice.id);
    } else {
      this.router.navigateByUrl('/newinternalinvoice/' + this.invoice.id);
    }
  }

  // show internal invoice edit button only if this invoice
  // is not from Tismo India
  async checkInternalInvoiceEdit() {
    this.showInternalInvoiceEdit = false;
    if (this.vendor.id !== this.data.getTismoIndiaVendorId()) {
      // this PO is for tismo usa or tismo germany ?
      // is there an internal PO ? for internal invoice, internal PO must be there
      const internalPoid = await this.data.getInternalPOforEndPO(this.invoice.purchaseOrderId).toPromise();
      if (internalPoid > 0) {
        // internal PO is present
        this.showInternalInvoiceEdit = true;
        await this.getButtonTitleForEditOrNewInternalInvoice();
      }
    }
  }

  async getButtonTitleForEditOrNewInternalInvoice() {
    this.InternalInvoiceId = await this.data.getInternalInvoiceForEndInvoice(this.invoice.id).toPromise();
    console.log('getInternalInvoiceForEndInvoice return ' + this.InternalInvoiceId)
    if (this.InternalInvoiceId > 0) {
      this.InternalInvoiceEditTitle = 'View the invoice from India';
    } else {
      this.InternalInvoiceEditTitle = 'Create invoice from India';
    }
  }
}
