import { Component, OnInit } from "@angular/core";
import { DataService } from "../data/data.service";
import { ActivatedRoute, Router } from "@angular/router";
import { Location } from "@angular/common";
import { AttachmentInterface } from "../attachments/attachments.component";
import { SupplierInvoiceBase } from "./supplierInvoiceBase";

class fmonad<T> {
  fobs: Promise<T>;
  constructor(obs: Promise<T>) {
    this.fobs = obs;
  }

  bind<U>(fn: (arg0: T) => Promise<U>): fmonad<U> {
    const k = this.fobs.then((t) => fn(t));
    return new fmonad(k);
  }

  then(fn: (arg0: T) => any): any {
    return this.fobs.then((t) => fn(t));
  }

  unwrap() {
    return this.fobs;
  }
}

@Component({
  selector: "app-supplier-invoice-edit",
  templateUrl: "./supplierInvoiceForm.component.html",
  styleUrls: ["./supplierInvoiceForm.component.scss"],
})
export class SupplierInvoiceEditComponent
  extends SupplierInvoiceBase
  implements OnInit
{
  constructor(
    data: DataService,
    protected router: Router,
    private route: ActivatedRoute,
    protected location: Location
  ) {
    super(data, router);
    this.viewMode = true;
    this.updateTitles();
    this.showReceived = true;
  }

  updateTitles() {
    this.buttonTitle = this.viewMode ? "Modify this Invoice" : "Save changes";
    this.formTitle = this.viewMode ? "Invoice" : "Modify Invoice";
  }

  ngOnInit() {
    const idstr = this.route.snapshot.paramMap.get("id");
    const id = idstr ? +idstr : 0;
    //this.initialize(id);
  }

  resetForm(reInit: boolean) {
    const tout = reInit ? 2000 : 2000;
    setTimeout(async () => {
      // console.log('resetForm-invoiceEdit');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data
          .getSupplierInvoiceForInvoiceNumber(this.invoice.invoiceNumber)
          .toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl("/supplierinvoice/" + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  onSubmit() {
    if (this.viewMode) {
      this.viewMode = undefined;
      this.updateTitles();
      return;
    }
    this.successMessage = "";
    this.errorMessage = "";
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = "Saving ...";
    this.data
      .updateSupplierInvoice(this.invoice)
      .toPromise()
      .then(
        () => {
          this.successMessage = "Updated Invoice " + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(true);
        },
        (err) => {
          this.buttonTitle = oldButtonTitle;
          this.setError("Could not update Invoice : ", err);
        }
      );
  }

  getSupplierAttachmentList() {
    this.data
      .getListOfSupplierInvoiceAttachments(this.invoice.id)
      .subscribe((ls) => {
        this.attachments = ls;
        this.showAttachmentButton = true;
      });
  }

  async fileUploadChanged(files: any) {
    if (files.length !== 1) {
      return;
    }
    const attachment = files[0] as File;
    this.attachmentmsg = "Attaching " + attachment.name;
    this.data
      .addSupplierInvoiceAttachment(this.invoice.id, attachment)
      .subscribe(
        (e) => {
          this.attachmentmsg = attachment.name + " attached to this invoice";
          this.getSupplierAttachmentList();
        },
        (err) => {
          this.attachmentmsg = "Error: Attachment could not be uploaded " + err;
          this.resetForm(false);
        }
      );
  }

  initialize(id: number) {
    try {
      this.showForm = false;
      this.viewMode = true;
      this.successMessage = 'Fetching invoice details...';
      this.updateTitles();

      this.getRestOfPrefillData(id).then(() => {
          this.successMessage = '';
          this.showForm = true;
        });

    } catch (e) {
      this.errorMessage = 'Error: Unable to get invoice details ' + e;
      this.resetForm(false);
    }
  }


  getRestOfPrefillData(invoiceId: number): Promise<void> {
    return this.data
      .getSupplierInvoice(invoiceId)
      .toPromise()
      .then((invoice) => {
        this.invoice = invoice;
        return this.data
          .getSupplierPurchaseOrder(this.invoice.supplierPOId)
          .toPromise();
      })
      .then((spo) => {
        this.selectedSpo = spo;
        this.supplierId = spo.supplierId;
        return this.data.getSupplier(this.supplierId);
      })
      .then((supplier) => {
        this.supplierName = supplier.name;
        this.supplier = supplier;
        this.showSupplierSelection = false;
        return this.data.getcurSymbol(this.selectedSpo!.currencyId);
      })
      .then(() => {
        return this.data.getListOfProjects().toPromise();
      })
      .then((projects) => {
        this.projects = projects;
        this.selectedProject = this.projects.find(
          (p) => p.id === this.invoice.projectId
        );
        this.invoice.paymentDate = new Date(this.invoice.paymentDate);
        if (this.invoice.paymentDate.getFullYear() > 2100) {
          this.invoice.paymentDate = new Date();
        }
        return Promise.resolve();
      });
  }
}
