export class FinancialYear {
  rawmonths = [
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
    'Jan',
    'Feb',
    'Mar',
    'Q1',
    'Q2',
    'Q3',
    'Q4',
    'FY'
  ];


  get startYear() {
    return this.financialYear;
  }
  get endYear() {
    return this.startYear + 1;
  }


  constructor(public readonly financialYear: number) {
  }

  static GetCurrentFinancialYear(): FinancialYear {
    const dt = new Date();
    const yr = dt.getFullYear();
    const mt = dt.getMonth(); // 0 for Jan
    return mt < 3 ? new FinancialYear(yr - 1) : new FinancialYear(yr);
  }

   NextYear(): FinancialYear {
    return new FinancialYear(this.financialYear + 1);
  }

  PrevYear(): FinancialYear {
    return new FinancialYear(this.financialYear - 1);
  }

  toString() {
        return 'FY ' + this.financialYear + '-' + ((this.financialYear + 1) % 2000);
    } // FY 2018-19

  // calendar month number from FY month index
  // fyMonthIndex = 0 ==> April ==> calendar month 4
  getCalMonthNumber(fyMonthIndex: number): number {
    let mth = fyMonthIndex + 3;
    mth = mth % 12; // wrap around
    return mth + 1; // in 1 based calendar numbering
  }

  getFyMonthNumber(calMonthNumber: number): number {
    let mth = calMonthNumber - 1; // make it first zero based
    mth = mth - 3;
    if (mth < 0) {
      mth = mth + 12;
    }
    return mth;
  }
  getQuarterRange = (quarterNumber: number, year: number) =>
    quarterNumber < 4 ? this.getCalMonthRange(quarterNumber * 3 + 1, 3, year) : this.getCalMonthRange(1, 3, year + 1)
  // tslint:disable-next-line:semicolon
  getCalMonthRange(startCalMonth: number, countMonths: number, year: number) {
    let rng: MonthYear[] = [];
    for (let mth = startCalMonth; mth < startCalMonth + countMonths; mth++) {
      rng = rng.concat(new MonthYear(mth, year));
    }
    return rng;
  }

  getFyMonthAndQuarterRangeForFullYear(): MonthYear[] {
    return this.getFyMonthRange(16);
  }

  getFyMonthRange(fyMonthIndex: number): MonthYear[] {
    const nfinancialYear = this.financialYear;
    if (fyMonthIndex < 9) {
      // apr to dec
      return this.getCalMonthRange(this.getCalMonthNumber(fyMonthIndex), 1, nfinancialYear);
    }
    if (fyMonthIndex < 12) {
      // jan to mar calendar year is + 1
      return this.getCalMonthRange(this.getCalMonthNumber(fyMonthIndex), 1, nfinancialYear + 1);
    }
    if (fyMonthIndex < 17) {
      // q1, q2, q3, q4
      switch (fyMonthIndex) {
        case 12: // Q1
          return this.getQuarterRange(1, nfinancialYear);
        case 13: // Q2
          return this.getQuarterRange(2, nfinancialYear);
        case 14: // Q3
          return this.getQuarterRange(3, nfinancialYear);
        case 15: // Q4
          return this.getQuarterRange(4, nfinancialYear);
        case 16: // Q1 + Q2 + Q3 + Q4
          return this.getQuarterRange(1, nfinancialYear)
            .concat(this.getQuarterRange(2, nfinancialYear))
            .concat(this.getQuarterRange(3, nfinancialYear))
            .concat(this.getQuarterRange(4, nfinancialYear));
      }
    }
    return [];
  }
}

export class MonthYear {
  constructor(public month: number, public year: number) {}
}
