import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { DataService } from '../data/data.service';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { Invoice } from '../data/invoice';
import { Utils } from '../utils/utils';


@Component({
    selector: 'app-invoice',
    templateUrl: './invoice.component.html',
    styleUrls: ['./invoice.component.scss'],
})

// Displays a single invoice in a row
// Used for showing list of invoices for a period
//                  list of receivables - invoices not yet paid - for end clients
//                  list of receivables within Tismo group - internal
export class InvoiceComponent implements OnInit {
    @Input()
    inview: InvoiceViewObj | undefined;

    @Output()
    headerClicked: EventEmitter<string> = new EventEmitter();

    clientName = 'Client';
    clientId = 0;
    invoiceNumber = 'Inv Number';
    invoiceId = 0;
    invoiceDate: Date | undefined; // = 'Date';
    projectCode = 'Project';
    projectId = 0;
    description = 'Description';
    invoiceAmount = 'Amount';
    invoiceAmountInUsd = 'In USD';
    status = 'Status';

    overdue = false;
    endclientoverdue = false;

    endClientStatus = '-';
    header = false;
    constructor() {
    }

    OnHeaderClicked(headerName: string) {
        if (this.header) {
            this.headerClicked.emit(headerName);
        }
    }

    ngOnInit() {
        if (!this.inview) {
            this.header = true;
            return;
        }
        this.clientName = this.inview.client.name;
        this.clientId = this.inview.client.id;
        this.invoiceNumber = this.inview.invoice.invoiceNumber;
        this.invoiceId = this.inview.invoice.id;
        this.invoiceDate = this.inview.invoice.invoiceDate; // Utils.getDateStr(this.inview.invoice.invoiceDate);
        this.projectCode = this.inview.projectCode;
        this.projectId = this.inview.invoice.projectId;
        this.description = this.inview.invoice.description;
        this.invoiceAmount = Utils.getAmountStr(this.inview.curSymbol, this.inview.invoice.amount);
        this.invoiceAmountInUsd = Utils.getAmountStr('$', this.inview.amountInUSD);
        [this.status, this.overdue] = this.getStatuString(this.inview.invoice);

        if (this.inview.hasExternalInvoice) {
            [this.endClientStatus, this.endclientoverdue]  = this.getStatuString(this.inview.externalInvoice);
        }
    }

    getStatuString(inv: Invoice): [string, boolean] {
        let status = Invoice.getStatusStr(inv);
        let overdue = false;
        const LateByMonths = 5;
        if (Invoice.isOverdue(inv)) {
            overdue = true;
        } else if (  Invoice.isFullyReceived(inv)) {
            status = '\u2714 '  + Utils.getDateStr(inv.receivedDate);
        } else if (Invoice.isPartlyReceived(inv)) {
            status = 'Part payment';
        }

        return [status, overdue];
    }

}


