import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../utils/utils';
import { InvoiceViewObj } from '../data/InvoiceViewObj';


@Component({
    selector: 'app-clientinvoice',
    templateUrl: './clientinvoice.component.html',
    styleUrls: ['./clientinvoice.component.scss'],
})
export class ClientInvoiceComponent implements OnInit {
    @Input()
    inview: InvoiceViewObj | undefined;
    invoiceNumber = 'Invoice';
    invoiceId = 0;
    invoiceDate = 'Dated';
    projectCode = 'Project';
    description = 'Description';
    invoiceAmount = 'Amount';
    received = 'Received';
    bankcharges = 'Bank Charges';
    status = 'Status';
    projectId = 0;
    header = false;
    constructor() {
    }


    ngOnInit() {
        if (!this.inview) {
            this.header = true;
            return;
        }

        const cursymbol = this.inview.curSymbol;
        const amtstr = (amt: number) => Utils.getAmountStr(cursymbol, amt);

        this.invoiceNumber = this.inview.invoice.invoiceNumber;
        this.invoiceId = this.inview.invoice.id;
        this.invoiceDate = Utils.getDateStr(this.inview.invoice.invoiceDate);
        this.projectCode = this.inview.projectCode;
        this.description = this.inview.invoice.description;
        this.invoiceAmount = amtstr(this.inview.invoice.amount);
        this.received =  amtstr(this.inview.invoice.receivedAmount);
        this.bankcharges =  amtstr(this.inview.invoice.bankCharges);
        this.projectId = this.inview.invoice.projectId;
        this.status = this.inview.status;
    }

}


