'use strict';

import { Component, OnInit } from '@angular/core';
import { Client, DataService, PurchaseOrder, Contact, UserData } from '../data/data.service';

import { ActivatedRoute, Router } from '@angular/router';
import { Utils } from '../utils/utils';
import { trigger, transition, style, animate } from '@angular/animations';
import { Invoice } from '../data/invoice';
import { UserService } from '../data/user.service';
import { ProjectViewModel } from '../data/ProjectViewModel';
import { POViewInterface } from '../data/POViewInterface';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { ClientPOIntf } from './clientpoboth.component';

interface Dictionary<T> {
  [key: number]: T;
}

@Component({
  selector: 'app-client-detail',
  templateUrl: './clientDetail.component.html',
  styleUrls: ['./clientDetail.component.scss'],
  animations: [
    trigger('slowAppear', [
      transition('void => *', [style({ opacity: 0 }), animate('500ms ease-in-out', style({ opacity: 1 }))])
    ])
  ]
})
export class ClientDetailComponent implements OnInit {
  clientId = 0;

  client: Client | null = null;
  // purchaseOrders: POViewInterface[] = [];
  projectViewModels: ProjectViewModel[] = [];

  invoices: Invoice[] = [];
  contactList: Contact[] = [];

  successMessage = '';
  errorMessage = '';
  statusMessage = '';
  userData = new UserData();
  initover = false;

  poList: POViewInterface[] = [];
  poinvList: Dictionary<InvoiceViewObj[]> = {};
  canCreateInvoice = false;

  bothPOList: ClientPOIntf[] = [];

  get canCreatePO() {
    return this.userData.purchaseorderPerm.add > 0 ? true : false;
  }
  canCreateInvoiceFn() {
    return this.userData.invoicePerm.add > 0 ? true : false;
  }

  get canCreateProject() {
    return this.userData.projectPerm.add > 0 ? true : false;
  }

  get canCreateContact() {
    return this.userData.contactPerm.add > 0 ? true : false;
  }




  setError(err: string): void {
    this.errorMessage = err;
    this.statusMessage = '';
    this.successMessage = '';
  }


  constructor(
    private data: DataService,
    private user: UserService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.user.currentUser.subscribe(ud => (this.userData = ud), err => this.setError('Connection Errors'));
  }

  ngOnInit() {
    const idstr = this.route.snapshot.paramMap.get('id');
    this.clientId = idstr ? +idstr : 0;
    this.initialize();
    this.canCreateInvoice = this.canCreateInvoiceFn();
  }

  initialize() {
    this.initover = false;
    this.statusMessage = 'Fetching client information';
    this.data
      .getClient(this.clientId)
      .then(cl => {
        this.client = cl;
        if (this.client.id > 0) {
          this.statusMessage = 'Fetching purchase order information';
          this.getPoDetailsForClient()
            .then(() => {
              this.statusMessage = '';
              this.initover = true;
            })
            .catch(err => this.setError('Connection Errors: '));

          this.data
            .getListOfContactsForClient(this.clientId)
            .subscribe(ls => {
              this.contactList = ls;
            },
              err => {
                this.setError('Connection Errors: ');
              }
            );
        } else {
          this.statusMessage = '';
          this.errorMessage = 'Could not fetch client info.';
        }

      })
      .catch(err => this.setError('Connection Errors: '));
  }

  async getPoDetailsForClient() {
    // both external end client po + any internal pos
    this.poList = await this.data.getListOfPurchaseOrdersForClient(this.clientId).toPromise();
    const extlist = this.poList.filter(po => !po.isInternalPO); // list of external pos only
    const intlist = this.poList.filter(po => po.isInternalPO); // list of internal pos only

    // associated external po with the internal po if it exists
    const comblist = extlist.map(extpo => ({ extpo: extpo, intpo: intlist.find(intpo => intpo.extPOId === extpo.id) }));

    // list of interna pos that has no associated external pos
    const noextpolist = intlist.filter(po => !comblist.find(c => c.intpo ? true : false));

    // create the ClientPOIntf object
    comblist.forEach(item => {
      const bothpoitem = new ClientPOIntf();
      bothpoitem.extOrder = item.extpo;
      bothpoitem.intOrder = item.intpo;
      bothpoitem.canCreateInvoice = this.canCreateInvoice;
      this.bothPOList.push(bothpoitem);
      this.data.getListOfInvoicesForPurchaseOrder(item.extpo.id)
        .subscribe(invlist => bothpoitem.extInvoiceList = invlist);
      if (item.intpo) {
        this.data.getListOfInvoicesForPurchaseOrder(item.intpo.id)
          .subscribe(invlist => bothpoitem.intInvoiceList = invlist);
      }
    });

    noextpolist.forEach(item => {
      const bothpoitem = new ClientPOIntf();
      bothpoitem.extOrder = undefined;
      bothpoitem.extInvoiceList = [];
      bothpoitem.intOrder = item;
      this.bothPOList.push(bothpoitem);
      this.data.getListOfInvoicesForPurchaseOrder(item.id)
        .subscribe(invlist => bothpoitem.intInvoiceList = invlist);
    });

    // this.poList.forEach(po => {
    //   if (po) {
    //     const bothpoitem = new ClientPOIntf();
    //     this.bothPOList.push(bothpoitem);
    //     if (!po.isInternalPO) {

    //     }
    //     this.data.getListOfInvoicesForPurchaseOrder(po.id)
    //       .subscribe(invlist => this.poinvList[po.id] = invlist);
    //   }
    // });

    this.projectViewModels = await this.data.getProjectViewListForClient(this.clientId).toPromise();
    this.projectViewModels = this.projectViewModels.sort((a, b) => (a.project.startDate > b.project.startDate ? 1 : -1));

  }

  toggleShowThisPO(po: any) {
    po.showThisPO = po.showThisPO ? false : true;
  }


  onNewInvoice(po: any) {
    // PO view object
    this.router.navigate(['/newinvoice', { poid: po.id, clientId: this.clientId }]);
  }
}
