import { Component, OnInit } from '@angular/core';
import { DataService, UserData } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { OrderBase } from './orderBase';
import { UserService } from '../data/user.service';

@Component({
    selector: 'app-orderedit',
    templateUrl: './orderForm.component.html',
    styleUrls: ['./orderForm.component.scss']
})
export class OrderEditComponent extends OrderBase implements OnInit {

    InternalPOId = 0;

    isSaving = false;
    get buttonTitle() { return this.viewMode ? 'Modify this Purchase Order' : this.isSaving ? 'Saving...' : 'Save changes'; }
    get formTitle() { return this.viewMode ? 'Purchase Order' : 'Modify Purchase Order'; }

    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        const id = idstr ? +idstr : 0;
        this.showForm = false;
        this.successMessage = 'Fetching...';
        this.data.getPurchaseOrder(id)
            .subscribe(cl => {
                this.po = cl;
                const t = this.initialize();

            },
                err => { console.log(err); this.setError('Error : Could not locate PO :', err); });
    }


    constructor(data: DataService, private route: ActivatedRoute, protected router: Router,
        protected user: UserService, protected location: Location) {
        super(data, router, user, location);
    }

    initialize() {
        // check if this is an internal PO or not first
        return this.data.getEndPOforInternalPO(this.po.id).subscribe(poid => {
            const externalPOId = poid;
            if (externalPOId > 0) { // this is an internal po - use orderInternalEdit component to edit this
                this.successMessage = '';
                this.router.navigateByUrl('/editinternalpo/' + externalPOId);
                return;
            }
            this.checkInternalPOEdit().then(() => {
                this.data.getClient(this.po.clientId).then(cl => {
                    this.clientName = cl.name;
                    this.updateFileblob(() => {
                        this.showClientSelection = false;
                        this.showIsCompleted = true;
                        this.viewMode = true;
                        this.successMessage = '';
                        this.setupAttachments(this.po.id);
                        this.showForm = true;
                    });
                });
            });
        });
    }

    onSubmit() {
        if (this.viewMode) {
            this.viewMode = undefined;
            return;
        }
        this.successMessage = '';
        this.errorMessage = '';
        this.po.isFixedPrice = this.po.isFixedPrice ? 1 : 0;
        this.po.completed = this.po.completed ? 1 : 0;
        console.log('Updating PO' + this.po.clientId, this.po.amount, this.po.poDate, this.po.currencyId, this.po.description,
            this.po.isFixedPrice, this.po.tandMBillRate, this.po.completed, '***');
        this.isSaving = true;
        this.data.updatePurchaseOrder(this.po)
            .subscribe(async c => {
                console.log(c);
                this.upLoadPOFile();
                this.fileblob = await this.data.getFileblobForPO(this.po.id).toPromise();
                this.successMessage = 'Updated PO ' + this.po.poNumber;
                this.isSaving = false;
                this.goToCurrentPO(this.po.id);
            },
                err => { console.log(err); this.setError('Could not update PO: Error: ', err); });
        
    }

  
    // show internal PO edit button only if this PO
    // is from Tismo US or Tismo GmbH
    async checkInternalPOEdit() {
        //  console.log(' po vendorid = ', this.po.vendorId);
        if (this.po.vendorId !== this.data.getTismoIndiaVendorId()) {
            this.showInternalPOEdit = true;
            await this.checkForEditOrNewInternalPO();
        } else {
            this.showInternalPOEdit = false;
        }
    }

    async checkForEditOrNewInternalPO() {
        this.InternalPOId = await this.data.getInternalPOforEndPO(this.po.id).toPromise();
        if (this.InternalPOId > 0) {
            this.InternalPOEditButtonTitle = 'View PO to Tismo India';
        } else {
            this.InternalPOEditButtonTitle = 'Create PO to Tismo India';
        }
    }

    editInternalPO() {
        console.log('Edit internal po ', this.po.id);
        if (this.InternalPOId > 0) {
            this.router.navigateByUrl(`/editinternalpo/${this.po.id}`);
        } else {
            this.router.navigateByUrl(`/newinternalpo/${this.po.id}`);
        }
    }

}

