import { BaseIntf } from './data.service';

export class Project implements BaseIntf {
    id = 0; // = proj.Id,
    name = ''; // = proj.Name,
    clientId = 0; // = clientMgr.GetClientById(proj.ClientId).Name,
    projectCode = '';
    startDate: Date = new Date(); // = proj.StartDate,
    effort = 0; // in person hours -- 160 person hours in a person month
    duration = 0; // in calendar days -- 30 days in a month
    completed = 0; // = proj.Closed,
    workType = 1; // 1 is development, 2 Maintenance, 4 Testing
    serviceType = 0; // 1 => Electronics 2 => Firmware 4 => Software 8 => Mechanical

    stpServiceType = 0;
    // 1 => Application Software, 7 => Embedded Software, 8 => Engineering & Design,
    // 14 => Internet Of Things, 15 => IT Enabled services, 22 => Web development, 23 => Others
    billingType = 0; // 0 => not specified, 1 => T and M, 2 => fixed prices
    poId = 0; // purchase order id 0 => not assigned
    medicalClass = 0; // 0 is not medical project, 
                     // 1 -> Class A,         2 -> class B,        3 -> class C
                     // 4 > Class 1 Resuable 5 > Class 1 Sterile 6 > Class 1 Measure 
                     // 7 > Class 2A         8 > Class 2B        9 > Class 3
    projectManagerId = 0;
    mgmtId = 0;
    clientSponsorId = 0;
    clientManagerId = 0;
    clientCommercialId = 0;
    invoiceCopyTo = '';
    remarks = '';

    static STPServiceTypeOptions() {
        return [
            { val: 0, str: 'Unknown' },
            { val: 14, str: 'Internet Of Things' },
            { val: 7, str: 'Embedded Software' },
            { val: 8, str: 'Engineering and Design' },
            { val: 1, str: 'Application Software' },
            { val: 22, str: 'Web development' },
            { val: 15, str: 'IT Enabled Services' },
            { val: 23, str: 'Others' },
        ];
    }

    static getSTPServiceStr( val: number) {
        const t = this.STPServiceTypeOptions().find(s => s.val === val);
        return t ? t.str : '';
    }


    static WorkTypeOptions() {
        return [
            { val: 1, str: 'Development' },
            { val: 2, str: 'Maintenance' },
            { val: 4, str: 'Testing' },
        ];
    }

    // 0 is not medical project, 
    // 1 -> Class A,         2 -> class B,        3 -> class C
    // 4 > Class 1 Reusable 5 > Class 1 Sterile 6 > Class 1 Measure 
    // 7 > Class 2A         8 > Class 2B        9 > Class 3
    static MedicalClassOptions() {
        return [
            { val: 0, str: 'Non-Medical' },
            { val: 1, str: 'Class A' },
            { val: 2, str: 'Class B' },
            { val: 3, str: 'Class C' },
            { val: 4, str: 'Class 1 Reusable' },
            { val: 5, str: 'Class 1 Sterile' },
            { val: 6, str: 'Class 1 Measure' },
            { val: 7, str: 'Class 2A' },
            { val: 8, str: 'Class 2B' },
            { val: 9, str: 'Class 3' },
        ];
    }

    static BillingTypes() {
        return [
            { val: 1, str: 'Time and Materials' },
            { val: 2, str: 'Fixed Price'},
        ];
    }
    static getServiceType(electronics: boolean, firmware: boolean,
        software: boolean, mechanical: boolean) {
        let x = 0x00;
        x |= (electronics ? 1 : 0);
        x |= (firmware ? 2 : 0);
        x |= (software ? 3 : 0);
        x |= (mechanical ? 8 : 0);
    }

    isFixedPrice() {
        return this.billingType === 2;
    }
    isTandM() {
        return this.billingType === 1;
    }

    isMedical() {
        return (this.medicalClass > 0);
    }

    // isClassA() {
    //     return (this.medicalClass & 0x01) === 0x01;
    // }
    // isClassB() {
    //     return (this.medicalClass & 0x02) === 0x02;
    // }
    // isClassC() {
    //     return (this.medicalClass & 0x03) === 0x03;
    // }

    isDevelopment() {
        return (this.workType & 0x01) > 0;
    }
    isMaintenance() {
        return (this.workType & 0x02) > 0;
    }
    isTesting() {
        return (this.workType & 0x04) > 0;
    }

    setElectronics(electronics: boolean) {
        return (electronics) ? this.serviceType |= 0x01 : this.serviceType &= ~0x01;
    }
    isElectronics() {
        return (this.serviceType & 0x01) > 0;
    }
    setFirmware(firmware: boolean) {
        return (firmware) ? this.serviceType |= 0x02 : this.serviceType &= ~0x02;
    }
    isFirmware() {
        return (this.serviceType & 0x02) > 0;
    }
    setSoftware(software: boolean) {
        return (software) ? this.serviceType |= 0x04 : this.serviceType &= ~0x04;
    }
    isSoftware() {
        return (this.serviceType & 0x04) > 0;
    }
    setMechanical(mechanical: boolean) {
        return (mechanical) ? this.serviceType |= 0x08 : this.serviceType &= ~0x08;
    }
    isMechanical() {
        return (this.serviceType & 0x08) > 0;
    }

    getEffortInPM(): number { // returns up to 3 decimal places
        return Math.round(this.effort * 1000 / 160.0) / 1000;
    }
    setEffortInPM(personMonths: number): number {
        return (this.effort =  Math.round( personMonths * 160.0 ) );
    }
    getdurationInMonths(): number { // returns up to 2 decimal places
        return Math.round(this.duration * 100 / 30.0) / 100;
    }
    setdurationInMonths(months: number): number {
        return this.duration = Math.round (months * 30.0);
    }
    getPath(): string { return 'Project'; }

}
