import { BaseIntf } from './data.service';
import { invalid } from '@angular/compiler/src/render3/view/util';

export class Invoice implements BaseIntf {
    id = 0;
    invoiceNumber = '';
    purchaseOrderId = 0;
    projectId = 0;
    description = '';
    amount = 0;

    tds = 0; // tax deducted at source
    invoiceDate: Date = new Date();
    isExpenseInvoice = 0;
    paymentTerms = -1;
    status = 0;
    receivedAmount = 0;
    receivedDate = new Date();
    bankTransaction = '';
    bankCharges = 0;
    remarks = '';

    softexNumber = '';
    ebrcNumber = '';

    static isReceived(inv: Invoice): boolean {
        return inv.status === 1 || inv.status === 5;
    }

    static isPartlyReceived(inv: Invoice): boolean {
        return inv.status === 5;
    }

    static isFullyReceived(inv: Invoice): boolean {
        return inv.status === 1 ;
    }

    static getStatusStr(inv: Invoice): string {
        const st = Invoice.getStatusOptions().find(opt => opt.value === inv.status);
        return st ? st.key : '';
    }


    static isOverdue(inv: Invoice) {
        return inv.status === 4;
   }

    static getStatusOptions() {
        return [
            { key: 'Review awaited', value: 0 },
            { key: 'Approved', value: 2 },
            { key: 'Sent', value: 3 },
            { key: 'Overdue', value: 4 },
            { key: 'Partly received', value: 5 },
            { key: 'Received', value: 1 },
            { key: 'Balance written Off', value: 6 },
            { key: 'Canceled', value: 7 },
        ];
    }

    getPath() { return 'Invoice'; }
}

// status field enumeration
// 0 > No Status
// 1 > Received
// 2 > Requested : Request to prepare and send this invoice to the client
// 3 > Sent
// 4 > Overdue
// 5 > Partly Received

