import { Injectable } from '@angular/core';
import { DataService } from '../data/data.service';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { MonthYear } from '../utils/financialYear';
import { Observable, from } from 'rxjs';
import { mergeMap } from 'rxjs/operators';


@Injectable()
export class InvoiceService  {

    constructor(private data: DataService) {
    }

    fetchAllInvoices(monthRange: MonthYear[]): Observable<InvoiceViewObj[]> {
        return from(monthRange)
                .pipe(
                    mergeMap(mth => this.data.getInvoiceViewObjList(mth.month, mth.year))
                );
    }
}


