import { BaseIntf } from "./data.service";

export class SupplierInvoice implements BaseIntf {
    
    id = 0;
    invoiceNumber = '';
    supplierPOId = 0;
    invoiceDate: Date = new Date();
    projectId = 0;
    description = '';
    currencyId = 0;
    amount = 0;
    techTeamReview = 0;
    projectManagerReview = 0;
    paymentDate = new Date();
    status = 0;
    clientInvoiceid = 0;
    clientInvoiceStatus = 0;
    remarks = ''
    fileBlobId = 0;


    static isPartlySent(inv: SupplierInvoice): boolean {
        return inv.status === 4;
    }

    static isFullySent(inv: SupplierInvoice): boolean {
        return inv.status === 2 ;
    }

    static getStatusStr(inv: SupplierInvoice): string {
        const st = SupplierInvoice.getStatusOptions().find(opt => opt.value === inv.status);
        return st ? st.key : '';
    }


    static isOverdue(inv: SupplierInvoice) {
        return inv.status === 3;
   }

    static getStatusOptions() {
        return [
            { key: 'Review awaited', value: 0 },
            { key: 'Approved', value: 1 },
            { key: 'Sent', value: 2 },
            { key: 'Overdue', value: 3 },
            { key: 'Partly Sent', value: 4 },
            { key: 'Balance written Off', value: 5 },
            { key: 'Cancelled', value: 6 },
        ];
    }

    getPath() {
        return 'SupplierInvoice';
    }
}