import { Component } from '@angular/core';
import { DataService } from '../data/data.service';

import { ActivatedRoute, Router } from '@angular/router';
import { SupplierContactBase } from './supplierContactBase';
import { Location } from '@angular/common';

@Component({
    templateUrl: './supplierContactsForm.component.html',
    styleUrls: ['./supplierContactsForm.component.scss']
})
export class SupplierContactEditComponent extends SupplierContactBase  {

    constructor(protected data: DataService, protected router: Router,
        protected route: ActivatedRoute, protected location: Location) {
            super(data, router, route, location);
    }
    get buttonTitle() { return 'Save Contact'; }
    get formTitle() { return 'Modify Contact'; }

    // for new contact the id passed is the supplierId
    // for edit contact the id passed is the supplierContactId
    async initialize(contactId: number) {
            this.data.getSupplierContact(contactId).subscribe( con => {
                this.contact = con;
                this.data.getSupplier(this.contact.supplierId).then( sp => this.supplier = sp);
            },
            err => {
                this.errorMessage = 'Error: Unable to get contact info' + err;
                 this.resetForm();
             }
            );
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Updating Contact' + this.contact.name);
        this.data.updateSupplierContact(this.contact)
            .subscribe(s => { console.log(s); this.successMessage = 'saved Contact ' + this.contact.name; this.resetForm(); },
                err => { console.log(err); this.setError('Could not save Contact: Error: ', err); });
    }
}

