import { Component, OnInit } from '@angular/core';
import { DataService, Supplier  } from '../data/data.service';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';


@Component({
    templateUrl: './supplierNew.component.html',
    styleUrls: ['./supplierNew.component.scss']
})
export class SupplierEditComponent implements OnInit {
    supplier: Supplier = new  Supplier();

    formTitle = 'Modify Supplier';
    btnTitle = 'Update this Supplier';
    successMessage = '';
    errorMessage = '';

    constructor(private data: DataService, private route: ActivatedRoute,  private location: Location) {

    }
    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        const id = idstr ? +idstr : 0;
        this.data.getSupplier(id)
          .then(su => this.supplier = su, err => { console.log(err); this.setError(err); });
    }

    setError(err: any) {
        this.errorMessage = 'Error: Could not update: ' + err.Error;
    }


    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Updating Supplier' + this.supplier.name);
        this.data.updateSupplier(this.supplier)
            .subscribe(s => { console.log(s); this.successMessage = 'Updated ' + this.supplier.name; this.resetForm(); },
                err => { console.log(err); this.setError(err); });
    }

    resetForm() {
        setTimeout(() => {
            this.location.back();
        }, 1000);
    }
}

