import { Invoice } from './invoice';
import { Client } from './data.service';
export class InvoiceViewObj {
  poNumber = '';
  poDate = new Date();
  client = new Client();
  clientCountryCode = '';
  clientAddress = '';
  vendorId = 0;
  projectCode = '';
  workType  = 1; // 1 is development, 2 Maintenance, 4 Testing
  serviceType = 0;  // 1 => Electronics 2 => Firmware 4 => Software 8 => Mechanical

  stpService = 0;
  amountInUSD = 0;
  amountInINR = 0;
  status = '';
  invoice = new Invoice();
  hasExternalInvoice = false;
  externalInvoice = new Invoice();
  curName = '';
  curSymbol = '';
  manager1 = '';
  manager2 = '';
  public static invoiceDateSortCompare() {
    return (a: InvoiceViewObj, b: InvoiceViewObj) => {
      if (a.invoice.invoiceDate > b.invoice.invoiceDate) {
        return 1;
      }
      if (a.invoice.invoiceDate < b.invoice.invoiceDate) {
        return -1;
      }
      return a.invoice.invoiceNumber > b.invoice.invoiceNumber ? 1 : -1;
    };
  }

  public static getDueAmount(inv: InvoiceViewObj): number {
    let due = inv.invoice.amount - inv.invoice.receivedAmount;
    return due <= 5 ? 0 : due;
  }

  public static getDueAmountInUSD(inv: InvoiceViewObj): number {
    if (inv.invoice.amount === 0) return 0;
    let rateToUSD = inv.amountInUSD / inv.invoice.amount;
    return InvoiceViewObj.getDueAmount(inv) * rateToUSD;
  }
}
