import { Component, OnInit } from "@angular/core";
import { DataService, Supplier, UserData } from "../data/data.service";
import { UserService } from "../data/user.service";

@Component({
  selector: "app-supplier-list",
  templateUrl: "./supplierList.component.html",
  styleUrls: ["./supplierList.component.scss"],
})
export class SupplierListComponent {
  supplierList: Supplier[] = [];
  fullSupplierList: Supplier[] = [];
  serverError = false;
  filterTerm = "";
  userData = new UserData();
  get canAddNewSuppliers() {
    return this.userData.supplierPerm.add ? true : false;
  }
  get canUpdateSuppliers() {
    return this.userData.supplierPerm.update ? true : false;
  }

  constructor(data: DataService, private user: UserService) {
    user.currentUser.subscribe(
      (ud) => {
        (this.userData = ud);
        console.log(ud)
      },
      (err) => {
        console.log(err);
        this.serverError = true;
      }
    );

    data.getListOfSupplierObs().subscribe(
      (res) => {
        this.fullSupplierList = res.sort((a, b) =>
          a.name.toUpperCase() >= b.name.toUpperCase() ? 1 : -1
        );
        this.updateSupplierList();
      },
      (err) => {
        console.log("Error " + err);
        this.serverError = true;
      }
    );
  }
  updateSupplierList() {
    this.supplierList = this.fullSupplierList.filter(
      this.filterForSearchTerm()
    );
  }

  filterTermChanged(newSrchTerm: string) {
    this.filterTerm = newSrchTerm;
    this.updateSupplierList();
  }

  filterForSearchTerm(): (su: Supplier) => boolean {
    if (this.filterTerm === "" || this.filterTerm.length < 2) {
      return (su: Supplier) => true;
    }
    const srchTarget = (su: Supplier) =>
      [su.name, su.supplierCode, su.location].join(" ");
    const regEx = new RegExp(this.filterTerm, "i");
    return (su: Supplier) => regEx.test(srchTarget(su));
  }
}
