import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';
import { InvoiceBase } from './invoiceBase';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-invoicenew',
  templateUrl: './invoiceForm.component.html',
  styleUrls: ['./invoiceForm.component.scss']
})
export class InvoiceNewComponent extends InvoiceBase implements OnInit {
  constructor(
    data: DataService,
    private route: ActivatedRoute,
    protected router: Router
  ) {
    super(data, router);
    this.showReceived = false;
  }

  // http://localhost:4200/newinvoice;poid=48;clientId=39
  async ngOnInit() {
    //   console.log('NG On Init called - invoiceNewComponent');
    const poidstr = this.route.snapshot.paramMap.get('poid');
    const poid = poidstr ? +poidstr : 0;
    const clientIdstr = this.route.snapshot.paramMap.get('clientId');
    const clientId = clientIdstr ? +clientIdstr : 0;
    if (poid > 0 && clientId > 0) {
      // first check if this is an internal PO
      const extPOid = await this.data.getEndPOforInternalPO(poid).toPromise();
      if (extPOid > 0) {
        // this is an internal PO
        console.log('new PO creation with internal PO client id and poid');
        return;
      }

      this.clientId = clientId;
      await super.onClientSelected();
      this.showClientSelection = false;
      this.invoice.purchaseOrderId = poid;
      this.invoice.invoiceDate = await this.computeDefaultInvoiceDate();
      this.singlePO = true;
      await super.onPOSelected();
    }
  }
  async onSubmit() {
    this.successMessage = '';
    this.errorMessage = '';
    console.log('Submitting new Invoice');
    if (this.invoice.projectId === 0) {
      this.setError(' Please select a project for this invoice. ', '');
      return;
    }
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = 'Creating Invoice...';
    await this.data
      .addInvoice(this.invoice)
      .toPromise()
      .then(
        c => {
          console.log(c);
          this.successMessage = 'added Invoice ' + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(false);
        },
        err => {
          this.buttonTitle = oldButtonTitle;
          console.log(err);
          this.setError('Could not add Invoice: Error: ', err);
        }
      );
  }

  resetForm(reInit: boolean) {
    const tout = reInit ? 500 : 2000;
    setTimeout(async () => {
      //  console.log('resetForm-invoiceNew');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data.getInvoiceForInvoiceNumber(this.invoice.invoiceNumber).toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl('/invoice/' + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  async computeDefaultInvoiceDate(): Promise<Date> {
    let dt = new Date();
    if (dt.getDate() <= 6) {
      const anyInvoicesThisMonth = await this.data.anyInvoice(dt.getMonth() + 1, dt.getFullYear()).toPromise();
      if (anyInvoicesThisMonth <= 0) {
        // if no invoices this month, return last month's last working day
        console.log('No invoices this month');
        dt = this.getPreviousMonthsLastWorkingDay(dt);
      }
    }
    return dt;
  }
  getPreviousMonthsLastWorkingDay(dt: Date): Date {
    dt.setDate(dt.getDate() - dt.getDate()); // go to previous month
    if (dt.getDay() === 0) {
      // sunday ?
      dt.setDate(dt.getDate() - 2); // go to the previous friday
    }
    if (dt.getDay() === 6) {
      // saturday ?
      dt.setDate(dt.getDate() - 1); // go to the previous friday
    }
    return dt;
  }

  async refreshInvoiceNumberClicked() {
    await this.generateInvoiceNumber();
  }

  async OnFocusInvoiceNumber() {
    if (this.invoice.invoiceNumber.length < 1) {
      await this.generateInvoiceNumber();
    }
    this.enableRefreshInvoiceNumber = true;
  }
}
