import { Inject, LOCALE_ID } from '@angular/core';
import { formatDate } from '../../../node_modules/@angular/common';


export class Utils {

    static locale =  'en-IN';

    public static doLater(delayInMillisecs: number, fun: () => void) {
        setTimeout(() => {
            fun();
        }, delayInMillisecs);
    }
    public static delay(durationInMillisecs: number): Promise<void> {
        return new Promise<void>(resolve => {
            setTimeout(() => {
                resolve();
            }, durationInMillisecs);
        });
    }

    public static getWidth(columns: any[]): number {
        return columns.reduce((acc, col) => acc + col.width, 10);
    }

    public static getDateStr(date: Date): string {
        // request a weekday along with a long date
        // const options = { year: 'numeric', month: 'short', day: 'numeric' };
        // const dt = new Date(date);
        // return (dt.toLocaleDateString('en-GB', options));
        return formatDate(new Date(date), 'dd MMM yyyy', this.locale);
    }


    public static getTwoDigitDateStr(date: Date): string {
        return formatDate(new Date(date), 'dd/MM/yyyy', this.locale);
    }

    // https://stackoverflow.com/questions/8847766/how-to-convert-json-to-csv-format-and-store-in-a-variable
    public static tocsv(objectArray: any[]): string {
        const stringify = (someval: any) => JSON.stringify(someval, replacer)
                                                .replace(/\\"/g, '""') // replace \" to ""  Excel prefers it that way
        const replacer = (_: any, value: any) => value === null ? '' : value; // null values to empty strings
        const header = Object.keys(objectArray[0]); // list of header names
        const csvStrings = objectArray
                            .map(row => header.map(headerName => stringify(row[headerName])).join(','));
          
        csvStrings.unshift(header.join(',')); // put header names at the start 
        const csv = csvStrings.join('\r\n'); // each string in its own line
       
        return csv;
    }




    public static exportCSVFile(csvData: string, fileName: string) {
        const blob = new Blob([csvData], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        if (link.download !== undefined) {
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', this.createFileName(fileName));
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }

    private static createFileName(exportFileName: string): string {
        const date = new Date();
        return (exportFileName + date.toLocaleDateString() + '_' + date.toLocaleTimeString() + '.csv');
    }

    public static getAmountStr(curSymbol: string, amount: number, decimalPlaces: number = 0): string {
        if (amount === 0) { return '-'; }
        return curSymbol + ' ' + this.toStr(amount, decimalPlaces);
            // amount.toLocaleString(undefined, { maximumFractionDigits: decimalPlaces });
    }
    public static toStr(amount: number, decimalPlaces: number = 0): string {
        return amount.toLocaleString(undefined, { maximumFractionDigits: decimalPlaces, minimumFractionDigits: decimalPlaces });
    }

    public static toStrWithNoDecimals(amount: number): string {
        return Math.round(amount).toString()
    }
    public static isOlderThanNMonths(dt: Date, months: number) {
        const threshold = new Date();
        threshold.setMonth(threshold.getMonth() - months);
        return dt < threshold;
    }

}



