import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { Observable, throwError, of } from 'rxjs';
import { tap, mergeAll, find, defaultIfEmpty, filter, first } from 'rxjs/operators';

@Injectable()
export class AngularDateHttpInterceptor implements HttpInterceptor {
    private static castDateAsMoment(event: HttpEvent<any>): void {
        if (event instanceof HttpResponse) {
            AngularDateHttpInterceptor.convertToDate(event.body);
        }
    }


    private static convertToDate(body: any) {
        if (body === null || body === undefined) {
            return body;
        }

        if (typeof body !== 'object') {
            return body;
        }

        for (const key of Object.keys(body)) {
            const value = body[key];
            if (AngularDateHttpInterceptor.isIso8601(value)) {
                body[key] = new Date(value);
            } else if (typeof value === 'object') {
                AngularDateHttpInterceptor.convertToDate(value);
            }
        }
    }

    private static isIso8601(value: string | null | undefined) {
        const iso8601 = /^\d{4}-\d\d-\d\dT\d\d:\d\d:\d\d(\.\d+)?(([+-]\d\d:\d\d)|Z)?$/;
        if (value === null || value === undefined) {
            return false;
        }
        return iso8601.test(value);
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(req).pipe(tap(AngularDateHttpInterceptor.castDateAsMoment));
    }
}
