import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../utils/utils';
import { ProjectViewModel } from '../data/ProjectViewModel';

@Component({
    selector: 'app-clientproject',
    templateUrl: './clientproject.component.html',
    styleUrls: ['./clientproject.component.scss'],
})
export class ClientProjectComponent implements OnInit {
    @Input()
    projview: ProjectViewModel | undefined;

    header = false;
    projectCode = 'Project Code';
    name = 'Name';
    start = 'Start';
    status = 'Status';
    billingStatus = '';
    projid = 0;
    noInvoiceInLongTime = false;
    constructor() {
    }


    ngOnInit() {
        if (!this.projview) {
            this.header = true;
            return;
        }

        this.header = false;

        this.projectCode = this.projview.project.projectCode;
        this.projid = this.projview.project.id;
        this.name = this.projview.project.name;
        this.start = Utils.getDateStr(this.projview.project.startDate);
        this.status = this.projview.project.completed ? 'Completed' : 'On going';

        if (ProjectViewModel.firstInvoiceIsOverdue(this.projview, 0)) {
            this.noInvoiceInLongTime = true;
            this.billingStatus = 'First invoice pending';
        } else if (ProjectViewModel.nextInvoiceOverdue(this.projview, 3)) {
            this.noInvoiceInLongTime = true;
            this.billingStatus = 'Last invoiced ' + ProjectViewModel.lastInvoicedDateStr(this.projview);
        }
    }

}


