// Send only date information when encoding to JSON --
// sending time info messes up with time zone and time keeps changing
// on every round trip and changes the date too !
Date.prototype.toJSON = function () {
  //2021-02-26
  return formatDate(new Date(this), 'yyyy-MM-dd', 'en-In')
}

// When dates are received from the server in JSON data, they remain as strings
// they would need to be explicitly parsed to Date objects.

