  getcurSymbolForUSD(): string {
    return '$';
  }

  async getcurSymbol(id: number): Promise<string> {
    const cur = await this.getCurrency(id);
    if (!cur) {
      return '';
    }
    switch (cur.name) {
      case 'USD':
        return '$';
      case 'EUR':
        return '';
      case 'GBP':
        return '';
      case 'INR':
        return '';
      default:
        return cur.name;
    }
  }


  /// Returns the USD rate of this currency as of today
  async getToUSDRate(curId: number): Promise<number> {
    let dt = new Date();
    return this.getCurrencyRate(curId, dt.getMonth(), dt.getFullYear())
  }


  async getCurrencyRate(curId: number, mth: number, yr: number): Promise<number> {
    const ch = this.xchgRates.find(x => x.id === curId && x.month === mth && x.year === yr);
    if (ch) {
      return ch.rate;
    } else {
      const rt = await this.getCurrencyRateInternal(curId, mth, yr).toPromise();
      this.xchgRates.push({
        id: curId,
        month: mth,
        year: yr,
        rate: rt
      });
      return rt;
    }
  }

  // http://localhost:5000/api/Currency/GetCurrencyRateToUSD?currencyId=5&month=4&year=2018
  getCurrencyRateInternal(curId: number, mth: number, yr: number): Observable<number> {
    const params = new HttpParams({
      fromObject: {
        currencyId: curId.toString(),
        month: mth.toString(),
        year: yr.toString()
      }
    }); // query parameters
    return this.http.get<number>(this.baseServerAPIUrl + 'Currency' + '/GetCurrencyRateToUSD', {
      headers: this.httpOptions.headers,
      params: params
    }); //  .pipe(catchError(this.handleError));
  }

