  async getPOViewWithBalance(pv: POViewInterface): Promise<POViewWithBalance> {
    const rate = await this.getToUSDRate(pv.currencyId);
    const balanceInUSD = pv.balance * rate;
    const pvWithBalance: POViewWithBalance = { ...pv, balanceInUSD };
    return pvWithBalance;
  }

  numToStr(amount: number, curSymbol?: string) {
    if (curSymbol) {
      return Utils.getAmountStr(curSymbol, amount);
    }
    return Utils.toStr(amount);
  }

