export class Manager implements BaseIntf {
  id = 0;
  name = '';
  email = '';

  fullName = '';

  role = 0; // NONE = 0, MGMT = 1, ACCOUNTING = 2, MANAGER = 4, SALES = 8, ADMIN = 100 

  public static canManageProjects(manager: Manager): boolean {
    return manager.role == 1 || manager.role == 4;
  }

  public static isActive(manager: Manager): boolean {
    return manager.active > 0;
  }

  active = 1;
  
  getPath() {
    return 'Manager';
  }
}

