import { Component, Input, OnInit } from '@angular/core';
import { Utils } from '../utils/utils';

export class ChartInput {
  chartTitle = '';
  isRate = false;
  months: Month[] = [];
  financialYear: () => string = () => '';
}

@Component({
  selector: 'app-monthchart',
  templateUrl: './monthchart.component.html',
  styleUrls: ['./monthchart.component.scss']
})
export class MonthchartComponent implements OnInit {
  @Input()
  chartinput: ChartInput = new ChartInput();

  @Input()
  chartinput2: ChartInput = new ChartInput();

  get months() {
    return this.chartinput.months;
  }
  get isRate() {
    return this.chartinput.isRate;
  }
  get chartTitle() {
    return this.chartinput.chartTitle;
  }
  get financialYear() {
    return this.chartinput.financialYear();
  }

  get financialYear2() {
    return this.chartinput2.financialYear();
  }

  monthsdisplay: MonthDisp[] = [];

  monthStrs = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

  maxHeight = 150; // 200 pixels;
  minHeight = 1; // pixels
  progress = '';
  errorMessage = '';
  fytotal = '';
  fytotal2 = '';

  constructor() {
    console.log('MonthChart constructed');
  }
  ngOnInit() {
    try {
      // this.chartinput.refresh = () => this.initCard();
      console.log('MonthChart ngOnInit');
      this.initCard();
    } catch (e) {
      this.errorMessage = 'Error while fetching information.';
    }
  }

  getValueStr(val: number) {
    if (val <= 0) {
      return ' ';
    }
    if (!this.isRate) {
      return Utils.getAmountStr('', val / 1000) + 'k';
    }
    return Utils.toStr(val, 2);
  }

  initCard() {
    console.log('monthchart initCard()');
    const allmonths = this.chartinput.months.concat(this.chartinput2.months);
    let min = allmonths.reduce((a, m) => (m.monthValue > 0 && m.monthValue < a.monthValue ? m : a)).monthValue;
    const max = allmonths.reduce((a, m) => (a.monthValue > m.monthValue ? a : m)).monthValue;
    // console.log(`${this.chartTitle} min value = ${min} and max = ${max}`);
    min = this.isRate ? min * 0.9 : 0;
    // max = this.isRate ? max * 1.0 : max;
    const mulfactor = (this.maxHeight - this.minHeight) / (max - min); // = m
    const constant = this.minHeight - mulfactor * min; // = c
    const mlist: MonthDisp[] = this.months.map((pmth, i) => {
      return {
        name: this.monthStrs[pmth.monthNumber - 1],
        value: pmth.monthValue > 0 ? constant + mulfactor * pmth.monthValue : 1,
        valuestr: this.getValueStr(pmth.monthValue),

        value2: this.financialYear2
          ? this.chartinput2.months[i].monthValue > 0
            ? constant + mulfactor * this.chartinput2.months[i].monthValue
            : 1
          : 0,
        valuestr2: this.financialYear2 ? this.getValueStr(this.chartinput2.months[i].monthValue) : ''
      };
    });

    if (!this.isRate) {
      const total = this.months.reduce((a, v) => a + v.monthValue, 0);
        this.fytotal = total > 0 ? Utils.getAmountStr('$ ', total / 1000) + 'k' : '';

      const total2 = this.chartinput2.months.reduce((a, v) => a + v.monthValue, 0);
        this.fytotal2 = total2 > 0 ? Utils.getAmountStr('$ ', total2 / 1000) + 'k' : '';
    }
    this.monthsdisplay = mlist;
  }
}

export class Month {
  monthNumber = 0;
  monthValue = 0;
}

class MonthDisp {
  name = '';
  value = 0.0;
  valuestr = '';

  value2 = 0.0;
  valuestr2 = '';
}
