import { OnInit, Directive } from '@angular/core';
import { DataService, Client, Contact, PurchaseOrder, Manager } from '../data/data.service';
import { Project } from '../data/project';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { POViewInterface } from '../data/POViewInterface';
import { ProjectListComponent } from './projectList.component';

type Some<T> = { tag:'some', result: T }
type None = { tag: 'none' }
const NONE : None = {tag: 'none'}
type Option<T> = Some<T> | None;

function toOption<T>(some: T | undefined): Option<T> {
    if (some) return { tag: 'some', result: some }
    return NONE;
}

function action<T>(operand: Option<T>, someAction: (sm:T) => void): void {
    if (operand.tag == 'some') someAction(operand.result);
}

function func<T, U>(operand: Option<T>, someFunc: (sm: T) => Option<U>): Option<U> {
    switch (operand.tag) {
        case 'some': return someFunc(operand.result); 
        case 'none' : return NONE
    }
}


// match<T, U>(option < T >, someFunc, noneFunc) : option < U >
// {
        

// }

@Directive()
export class ProjectBase implements OnInit {
    clients: Client[] = [];
    clientName = '';
    clientId = 0;
    project = new Project();
    successMessage = '';
    errorMessage = '';

    viewMode: boolean | undefined = undefined;
    showIsCompleted = false;
    showClientSelection = true;

    get projectcompleted() { return this.project.completed; }
    set projectcompleted(val) { this.project.completed = val ? 1 : 0; }

    effort = 0; // in person months
    duration = 0; // in calendar months

    workTypeOptions: any[] = [];

    stpOptions: any[] = [];
    medClassOptions: any[] = [];
    billingTypes: any[] = [];
    purchaseOrderOptions: POViewInterface[] = [];
    mgrOptions: Manager[] = [];
    electronics = false;
    firmware = false;
    software = false;
    mechanical = false;
    contacts: Contact[] = [];
    showForm = false;

    get buttonTitle() { return 'Create this Project'; }
    get formTitle() { return 'New Project'; }


    constructor(protected data: DataService, protected route: ActivatedRoute, protected location: Location) {
        this.workTypeOptions = Project.WorkTypeOptions();
        this.stpOptions = Project.STPServiceTypeOptions();
        this.medClassOptions = Project.MedicalClassOptions();
        this.billingTypes = Project.BillingTypes();
        
        this.data.getListOfClients()
                .then(res => this.clients = res.sort((a, b) => a.name > b.name ? 1 : -1),
                    err => this.setError(err));
        this.getManagerList();
    }

    getManagerList()
    {
        this.data.getListOfManagers().subscribe(lst => this.mgrOptions = lst.filter(m => Manager.canManageProjects(m)));
    }

    ngOnInit() {
    }
    setError(err: any) {
        this.errorMessage = 'Error: Could not update. ';
    }
    OnProjectCodeFocus() {
    }

    onClientSelected() {
        if (this.project.clientId) {
            this.data.getListOfContactsForClient(this.project.clientId)
                     .subscribe(lst => this.contacts = lst);
            this.data.getClient(this.project.clientId)
                .then(cl => {
                    this.clientId = cl.id;
                    this.clientName = cl.name;
                    this.data.getListOfPurchaseOrdersForClient(this.clientId)
                             .subscribe(polist => this.purchaseOrderOptions = polist); // .filter(po => !po.isCompleted()));
                });
        }
    }

    onPOChanged() {
        let po = toOption(this.purchaseOrderOptions.find(por => por.id == this.project.poId));
        action(po, p => this.project.billingType = p.isFixedPrice ? 2 : 1 )
    }

    prepareProject() {
        this.project.setEffortInPM(this.effort);
        this.project.setdurationInMonths(this.duration);
        this.project.setElectronics(this.electronics);
        this.project.setFirmware(this.firmware);
        this.project.setSoftware(this.software);
        this.project.setMechanical(this.mechanical);
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
    }

    resetForm() {
        setTimeout(() => {
            this.location.back();
        }, 1000);
    }
}

