
import { Component, OnInit, Input } from '@angular/core';
import { POViewInterface, POViewWithBalance } from '../data/POViewInterface';
import { InvoiceViewObj } from '../data/InvoiceViewObj';
import { POStatus } from '../orderbook/order.component';
import { DataService } from '../data/data.service';


@Component({
    selector: 'app-clientpoboth',
    templateUrl: './clientpoboth.component.html',
    styleUrls: ['./clientpoboth.component.scss']
})
export class ClientpobothComponent implements OnInit {
    @Input()
    pointf = new ClientPOIntf();


    intPoViewWithBalance!: POViewWithBalance
    extPoViewWithBalance! : POViewWithBalance
    showclienttab = false;
    showinttab = false;

    clientpopresent = false;
    internalpopresent = false;
    clientabactive = false;
    internaltablactive = false;

    clientPOBadge = false;
    clientPOWarn = false;
    internalPOBadge = false;
    internalPOWarn = false;


    constructor(private data: DataService) {
        
    }
    async ngOnInit() {

        this.clientpopresent = this.pointf.extOrder  ? true : false;
        this.internalpopresent = this.pointf.intOrder ? true : false;

        if (this.pointf.extOrder) {
            this.extPoViewWithBalance = { ...this.pointf.extOrder, balanceInUSD : 0.0 }; // balance in USD is not used in client details
        }
        if (this.pointf.intOrder) {
            this.intPoViewWithBalance = {...this.pointf.intOrder, balanceInUSD : 0.0} // balance in USD is not used in client details
        }

       
        // show the tabs only if both the POs are present
        this.showclienttab = this.clientpopresent && this.internalpopresent;
        this.showinttab = this.showclienttab;

        if (this.pointf.extOrder) {
            this.clienttabclicked();
        } else if (this.pointf.intOrder) {
            this.internaltabclicked();
        }
    }
    clienttabclicked() {
        if (this.pointf.extOrder) {
            this.clientabactive = true;
            this.internaltablactive = false;
        }
    }
    internaltabclicked() {
        if (this.pointf.intOrder) {
            this.clientabactive = false;
            this.internaltablactive = true;
        }
    }
    badgeValue(status: POStatus) {
        return status === POStatus.FIRST_INVOICE_PENDING
            || status === POStatus.LAST_INVOICE_LONG_AGO
            || status === POStatus.NEGATIVE_BALANCE
            || status === POStatus.NIL_BALANCE
            ;
    }
    badgeWarn(status: POStatus) {
        return status === POStatus.FIRST_INVOICE_PENDING
            || status === POStatus.LAST_INVOICE_LONG_AGO
            ;
    }
    clientPoStatus(status: POStatus) {
        if (this.pointf.extOrder) {
            setInterval(() => { // short delay to avoid the error that expression changed after checking
                this.clientPOBadge = this.badgeValue(status);
                this.clientPOWarn = this.badgeWarn(status);
            }, 500);
        }
    }
    internalPoStatus(status: POStatus) {
        if (this.pointf.intOrder) {
            setInterval(() => { // short delay to avoid the error that expression changed after checking
                this.internalPOBadge = this.badgeValue(status);
                this.internalPOWarn = this.badgeWarn(status);
            }, 500);
        }
    }
}

export class ClientPOIntf {
    extOrder: POViewInterface | undefined;
    extInvoiceList: InvoiceViewObj[] = [];
    intOrder: POViewInterface | undefined;
    intInvoiceList: InvoiceViewObj[] = [];

    canCreateInvoice = false;
}
