import { Component, OnInit } from '@angular/core';
import { DataService} from '../data/data.service';
import { OrderBase } from './orderBase';
import { Router, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { UserService } from '../data/user.service';

@Component({
    selector: 'app-ordernew',
    templateUrl: './orderForm.component.html',
    styleUrls: ['./orderForm.component.scss']
})
export class OrderNewComponent extends OrderBase implements OnInit {

    buttonTitleStr = 'Create this Purchase Order';
    get buttonTitle() { return this.buttonTitleStr  }
    get formTitle() { return 'New Purchase Order'; }

    constructor(data: DataService, protected router: Router, protected user: UserService,
        protected location: Location, private route: ActivatedRoute) {
        super(data, router, user, location);
    }

    // client id may be specified as a route parameter
    async ngOnInit() {
        const clientIdstr = this.route.snapshot.paramMap.get('clientId');
        const clientId = clientIdstr ? + clientIdstr : 0;
        if ( clientId > 0) {
            this.po.clientId = clientId;
            this.clientName = (await this.data.getClient(clientId)).name;
            this.showClientSelection = false;
        }
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';

        this.po.isFixedPrice = this.po.isFixedPrice ? 1 : 0;
        this.po.completed = this.po.completed ? 1 : 0;
        console.log(
            'Submitting new PO' + this.po.clientId,
            this.po.amount,
            this.po.poDate,
            this.po.currencyId,
            this.po.description,
            this.po.isFixedPrice,
            this.po.completed
        );
        this. buttonTitleStr = "Creating PO...";
        this.data.addPurchaseOrder(this.po).subscribe(
            poid => {
                this.po.id = poid;
                this.setupAttachments(this.po.id);
                this.successMessage = 'Uploading PO file ' + this.po.poNumber;
                this.upLoadPOFile();
                this.successMessage = 'Created PO ' + this.po.poNumber;
                this.goToCurrentPO(poid);
            },
            err => {
                console.log(err);
                this.setError('Could not create PO: Error: ', err);
            }
        );
    }
}

