import { Component, OnInit } from '@angular/core';
import { DataService, Manager } from '../data/data.service';
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { ProjectBase } from './projectBase';

@Component({
    selector: 'app-projnew',
    templateUrl: './projectForm.component.html',
    styleUrls: ['./projectForm.component.scss']
})
export class ProjectNewComponent extends ProjectBase implements OnInit {

    constructor(protected data: DataService, protected route: ActivatedRoute, protected location: Location) {
        super(data, route, location);

    }
    getManagerList() {
        console.log('Project New managers list called')
        // for new projects show only managers who are active (currently at Tismo)
        this.data.getListOfManagers()
                 .subscribe(lst => this.mgrOptions = lst.filter(m => Manager.canManageProjects(m) && Manager.isActive(m)));
    }
    async ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('clientId');
        this.showForm = true;
        if (idstr) {
            const clientId = +idstr;
            const client = await this.data.getClient(clientId);
            if (client) {
                this.project.clientId = client.id;
                this.showClientSelection = false;
                this.clientName = client.name;
                this.clientId = client.id;
                this.onClientSelected();
            }
        }
    }

    

    OnProjectCodeFocus() {
        if (!this.project.projectCode) {
            this.data.getNextProjectCode(new Date(this.project.startDate))
                .subscribe(pc => {
                    if (!this.project.projectCode) {
                        this.project.projectCode = pc;
                    }
                });
        }
    }


    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        this.prepareProject();
        console.log('Submitting new Project' + this.project.clientId);
        this.data.addProject(this.project)
            .subscribe(() => {
                    this.successMessage = this.project.projectCode + ' project added';
                    this.resetForm();
                },
                err => { console.log(err); this.errorMessage = 'Error : Could not add project' + err; });
    }
}

