import { DataService, Client, Currency, Vendor, PurchaseOrder, Fileblob, UserData } from '../data/data.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { UserService } from '../data/user.service';
import * as FileSaver from 'file-saver';
import { AttachmentInterface } from '../attachments/attachments.component';
export class OrderBase {
  clients: Client[] = [];
  clientName = '';
  currencies: Currency[] = [];
  vendors: Vendor[] = [];
  po = new PurchaseOrder();
  get POFile() {
    return this.fileblob.name;
  }
  POFileLink = '';
  fileblob = new Fileblob();
  successMessage = '';
  errorMessage = '';
  showInternalPOEdit = false;
  showUploadPOFile = true; // the original PO upload button is shown always except for internal POs where it is created
  InternalPOEditButtonTitle = '';
  InternalPOEdit = false;
  userData = new UserData();
  showForm = true;
  attachmentIntf: AttachmentInterface = new AttachmentInterface();


  InternalPOId = 0; // internal po edit / new
  externalPO: PurchaseOrder = new PurchaseOrder();  // internal po edit / new
  updating = false; // internal po edit new
  extVendor = new Vendor();  // internal po edit new
  endClient = new Client(); // internal po edit new
  extCurSymbol = '';

  get canModifyPO() {
    return this.userData.purchaseorderPerm.update > 0 ? true : false;
  }

  poFileToBeUploaded: File | undefined;

  poNumberPlaceHolder = '';
  showClientSelection = true;
  viewMode: boolean | undefined = undefined;
  showIsCompleted: boolean | undefined = undefined;

  get currencyName() {
    if (!this.currencies || !this.po.currencyId) {
      return '';
    }
    // tslint:disable-next-line:triple-equals
    const cur = this.currencies.find(c => c.id == this.po.currencyId);
    return cur ? cur.name : '';
  }

  constructor(
    protected data: DataService,
    protected router: Router,
    protected user: UserService,
    protected location: Location
  ) {
    this.po.isFixedPrice = 0;
    this.po.tandMBillRate = 0;
    this.po.completed = 0;
    this.po.paymentTerms = 15;

    this.user.currentUser.subscribe(ud => (this.userData = ud), err => this.setError('Connection Errors', err));

    data.getListOfClients().then(
      res => (this.clients = res.sort((a, b) => (a.name > b.name ? 1 : -1))),
      err => {
        console.log('Error ' + err);
        this.setError('Connection Error: ', err);
      }
    );

    data.getListOfCurrencies().then(
      cs => (this.currencies = cs),
      err => {
        console.log('Error ' + err);
        this.setError('Connection Error: ', err);
      }
    );

    data.getListOfVendors().then(
      vs => (this.vendors = vs),
      err => {
        console.log('Error ' + err);
        this.setError('Connection Error: ', err);
      }
    );
  }

  setError(msg: string, err: any) {
    this.errorMessage = msg + err;
  }

  onSubmit() {

  }

  goToCurrentPO(poid: number, delayTime: number = 2000) {
    setTimeout(() => {
      this.router.navigateByUrl('/')
        .then(b => this.router.navigateByUrl('/order/' + poid));
    }, delayTime);
  }

  resetForm() {
    setTimeout(() => {
      this.po = new PurchaseOrder();
      this.successMessage = '';
      this.errorMessage = '';
      this.location.back();
    }, 2000);
  }

  editInternalPO() { }

  fileUploadChanged(files: any) {
    if (files.length !== 1) {
      return;
    }
    this.poFileToBeUploaded = files[0];
    this.fileblob = new Fileblob();
    // this.upLoadPOFile();
  }

  setupAttachments(poId: number) {
    // console.log(`setting up attachment interface for ${poId}`);
    const af = new AttachmentInterface();
    af.id = poId;
    af.addAttachment = (id, file) => this.data.addPOattachment(id, file);
    af.getAttachmentDetails = id => this.data.getPOAttachmentList(id);
    af.getAttachmentLink = id => this.data.getPOAttachmentLink(id);
    af.downloadResource = str => this.data.downloadResource(str);
    this.attachmentIntf = af;
  }

  updateFileblob(onCompletion: () => void = () => { }) {
    if (this.po.id > 0) {
      this.data.getFileblobForPO(this.po.id).subscribe(fb => {
        this.fileblob = fb;
        this.POFileLink = this.data.getPOFileLink(this.po.id);
        onCompletion();
      });
    }
  }

  async downloadPOFileLinkClicked() {
    if (this.po.id <= 0) {
      console.log('po id is zero');
      return;
    }
    const urlstr = this.data.getPOFileLink(this.po.id);
    const res = await this.data.downloadResource(urlstr);
    const fileblob = new Blob([res], { type: 'application/octet-stream' });
    FileSaver.saveAs(fileblob, this.POFile);
  }

  upLoadPOFile() {
    if (this.poFileToBeUploaded) {
      if (this.po.id !== 0) {
        console.log('Uploading PO file ' + this.poFileToBeUploaded.name);
        this.data.uploadPO(this.po.id, this.poFileToBeUploaded).subscribe(
          async e => {
            await this.updateFileblob();
            // this.successMessage = 'PO file uploaded';
          },
          err => {
            this.errorMessage = 'PO file could not be uploaded ' + err;
            throw err;
          }
        );
      }
    }
  }
}
