import { DataService, Supplier, SupplierContact } from '../data/data.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { OnInit, Directive } from '@angular/core';

@Directive()
export class SupplierContactBase implements OnInit {
    supplier: Supplier = new Supplier();
    contact = new SupplierContact();

    successMessage = '';
    errorMessage = '';
    viewMode: boolean | undefined = undefined;

    get buttonTitle() { return 'Create Contact'; }
    get formTitle() { return 'New Contact'; }


    constructor(protected data: DataService, protected router: Router,
             protected route: ActivatedRoute, protected location: Location) {

    }

    ngOnInit() {
        const idstr = this.route.snapshot.paramMap.get('id');
        const id = idstr ? +idstr : 0;
        this.initialize(id);
    }

    // for new contact the id passed is the supplierId
    async initialize(supplierId: number) {
        this.supplier = await this.data.getSupplier(supplierId);
        this.contact.supplierId = supplierId;
        const lst = await this.data.getListOfContactsForSupplier(supplierId).toPromise();
        if ( lst.length > 0 ) {
            this.contact.address = lst[0].address;
            this.contact.telephone = lst[0].telephone;
        }
    }

    setError(msg: string, err: any, ) {
        this.errorMessage = msg + err;
    }

    onSubmit() {
        this.successMessage = '';
        this.errorMessage = '';
        console.log('Submitting new Contact' + this.contact.name);
        this.data.addSupplierContact(this.contact)
            .subscribe(c => { console.log(c); this.successMessage = 'added Contact ' + this.contact.name; this.resetForm(); },
                err => { console.log(err); this.setError('Could not add Contact: Error: ', err); });
    }

    resetForm() {
        setTimeout(() => {
            this.contact = new SupplierContact();
            this.successMessage = '';
            this.errorMessage = '';
            this.location.back();
        }, 1000);
    }

}

