import { Component, OnInit } from '@angular/core';
import { Project } from '../data/project';
import { DataService, Manager, PurchaseOrder, Contact } from '../data/data.service';
import { ActivatedRoute } from '@angular/router';
import { Utils } from '../utils/utils';
import html2pdf from 'html2pdf.js';

@Component({
    selector: 'app-projectpin',
    templateUrl: './projectpin.component.html',
    styleUrls: ['./projectpin.component.scss']
})
export class ProjectPinComponent implements OnInit {
    project = <Project>{
        id: 0,
        name: 'projectName',
        clientId: 0,
        projectCode: 'TDummy',
        startDate: new Date(),
        effort: 10,
        duration: 30,
        completed: 0,
        workType: 1,
        serviceType: 0,
        stpServiceType: 0,
        medicalClass: 0,
        projectManagerId: 0,
        mgmtId: 0,
        clientSponsorId: 0,
        clientManagerId: 0,
        clientCommercialId: 0,
    };

    effort = '';
    duration = '';
    development = ' ';
    maintenance = ' ';
    testing = ' ';

    electronics = ' ';
    firmware = ' ';
    software = ' ';
    mechanical = ' ';
    fixedprice = ' ';
    tandm = ' ';
    // classA = ' ';
    // classB = ' ';
    // classC = ' ';

    isMedicalProject = false;
    medicalClass = ' ';

    companyName = '';
    webSite = '';
    poNumber = '';
    poDate = '';
    customerId = '';

    projectManager = '';
    managementRep = '';
    errorMessage = '';
    successMessage = '';

    invoicePaymentTerms = 15;
    dateOfInitiation = '';

    initiatedBy = '';
    contactList: ContactInfoView[] = [];
    invoiceCopyTo = '';

    constructor(protected data: DataService, protected route: ActivatedRoute) {
        const idstr = this.route.snapshot.paramMap.get('id');
        if (idstr) {
            const id = +idstr;
            this.data.getProject(id)
                .subscribe(proj => this.setupProject(proj),
                    err => { console.log(err); this.setError(err); });
        }

    }

    getMedicalClassString(proj: Project) {
        let t = Project.MedicalClassOptions()
                       .find(item => item.val == proj.medicalClass)
      
        return t ? t.str
                 : Project.MedicalClassOptions()[0].str
        // Non-medical
    }
    setupProject(proj: Project) {
        const tickmark = 'done'; // for material icons 'done' is the text to show a tickmark
        const notapplicable = 'remove'; // for material icons 'remove' is the text to show a dash
        this.project = new Project();
        Object.assign(this.project, proj);
        this.effort = this.project.getEffortInPM().toString() + ' person-months';
        this.duration = this.project.getdurationInMonths().toString() + ' calendar-months';
        this.electronics = this.project.isElectronics() ? tickmark : ' ';
        this.firmware = this.project.isFirmware() ? tickmark : ' ';
        this.software = this.project.isSoftware() ? tickmark : ' ';
        this.mechanical = this.project.isMechanical() ? tickmark : ' ';
        this.development = this.project.isDevelopment() ? tickmark : ' ';
        this.maintenance = this.project.isMaintenance() ? tickmark : ' ';
        this.testing = this.project.isTesting() ? tickmark : ' ';

        this.isMedicalProject = this.project.isMedical();
        this.medicalClass = this.getMedicalClassString(this.project);
        // this.classA = this.project.isMedical() ? (this.project.isClassA() ? tickmark : ' ') : notapplicable;
        // this.classB = this.project.isMedical() ? (this.project.isClassB() ? tickmark : ' ') : notapplicable;
        // this.classC = this.project.isMedical() ? (this.project.isClassC() ? tickmark : ' ') : notapplicable;
        this.fixedprice = this.project.isFixedPrice() ? tickmark : ' ';
        this.tandm = this.project.isTandM() ? tickmark : ' ';
        this.invoiceCopyTo = this.project.invoiceCopyTo;

        if (this.project.projectManagerId > 0) {
            this.data.getById<Manager>(Manager, this.project.projectManagerId)
                .subscribe(mgr => this.projectManager = mgr.name);
        }

        if (this.project.mgmtId > 0) {
            this.data.getById<Manager>(Manager, this.project.mgmtId)
                .subscribe(mgr => { this.managementRep = mgr.name; this.initiatedBy = mgr.name; });
        }
        this.dateOfInitiation = Utils.getDateStr(this.project.startDate);
        if (this.project.poId > 0) {
            this.data.getById<PurchaseOrder>(PurchaseOrder, this.project.poId)
                .subscribe(po => {
                    this.poNumber = po.poNumber;
                    this.poDate = Utils.getDateStr(po.poDate);
                    this.invoicePaymentTerms = po.paymentTerms;
                    const now = new Date();
                    const earlierThan = (first: Date, otherDate: Date) => first.getTime() <= otherDate.getTime();
                    const pindate = earlierThan(now, this.project.startDate) ? now : this.project.startDate;
                    this.dateOfInitiation = Utils.getDateStr(pindate);
                });
        }

        this.data.getClient(this.project.clientId)
            .then(cl => {
                this.companyName = cl.name;
                this.webSite = '';
                this.customerId = cl.customerCode;
            });

        const fill = (label: string, ct: Contact) => {
            const ctv = new ContactInfoView(label);
            Object.assign(ctv, ct);
            
            // split the address string 
            // so that each address line will be on a separate line
            ctv.addresslines = ctv.address.split('\n');
           
            return ctv;
        };
        this.data.getById<Contact>(Contact, this.project.clientManagerId)
            .subscribe(ct => this.contactList.push(fill('Client Project Manager', ct)));

        this.data.getById<Contact>(Contact, this.project.clientSponsorId)
            .subscribe(ct => this.contactList.push(fill('Project Sponsor', ct)));

        this.data.getById<Contact>(Contact, this.project.clientCommercialId)
            .subscribe(ct => this.contactList.push(fill('Commercial Manager', ct)));

    }
    setError(err: any) {
        this.errorMessage = 'Error: Could not update. ';
    }

    ngOnInit(): void {

    }

    OnSaveToPDF() {
        const element = document.getElementById('elementtoprint');
        if (!element) { return; }
        const opt = {
            margin: 0.5,
            filename: this.customerId + '_PIN_' + this.project.projectCode + '.pdf',
            html2canvas: {
                 windowWidth:  element.scrollWidth,
                // windowHeight: element.scrollHeight,
                // backgroundColor: null, // transparent
                scale: 2,
            },
            jsPDF: { unit: 'in', format: 'a4', orientation: 'portrait' }
        };

        console.log('OnSaveToPDF: ' + opt.filename);
        html2pdf(element, opt);

    }

}
class ContactInfoView extends Contact {
    constructor(public label: string) {
        super();
    }
    addresslines : string[] = []
}


