import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { Utils } from '../utils/utils';
import { POViewInterface, POViewWithBalance } from '../data/POViewInterface';
import { DataService } from '../data/data.service';

export const enum POStatus {
    NONE = 0,
    NIL_BALANCE = -1,
    NEGATIVE_BALANCE = -2,
    FIRST_INVOICE_PENDING = -3,
    LAST_INVOICE_LONG_AGO = -4,

    COMPLETED = 100
}

@Component({
    selector: 'app-order',
    templateUrl: './order.component.html',
    styleUrls: ['./order.component.scss']
})
export class OrderComponent implements OnInit {

    @Input()
    order!: POViewWithBalance | null

    @Output()
    headerClicked: EventEmitter<string> = new EventEmitter();


    clientName = 'Client';
    clientId = 0;
    vendorName = 'Vendor';
    description = 'Description';
    poNumber = 'PO Number';
    poDate = 'Dated';
    orderId = 0;
    amountStr = 'Amount';
    billedStr = 'Billed';
    balanceStr = 'Balance';

    balanceInUSDStr = 'Balance (USD)';
    expenseBudgetStr = 'Expense Budget';
    expenseStr = 'Expensed';
    expenseBalanceStr = 'Expense Balance';
    receivedStr = 'Received';
    bankChargesStr = 'Bank Charges';
    dueAmountStr = 'Due';
    status = 'Remarks';
    nobalance = false;
    notInvoicedRecently = false;
    header = false;
    completed = '';
    statusValue = POStatus.NONE;


    OnHeaderClicked(headerName: string) {
        if (this.header) {
            this.headerClicked.emit(headerName);
        }
    }

    constructor(protected data: DataService) {
        
    }

    ngOnInit() {
        this.initialize();
    }

    initialize(): void {
        if (!this.order) {
            this.header = true;
            return;
        }

        this.clientName = this.order.clientName;
        this.clientId = this.order.clientID;
        this.vendorName = this.order.vendorName;
        this.description = this.order.description;
        this.poNumber = this.order.poNumber;
        this.orderId = this.order.id;
        this.poDate = Utils.getDateStr(this.order.poDate);

        this.amountStr = Utils.getAmountStr(this.order.curSymbol, this.order.amount);
        this.billedStr = Utils.getAmountStr(this.order.curSymbol, this.order.billed);
        this.balanceStr = Utils.getAmountStr(this.order.curSymbol, this.order.balance);
        this.expenseBudgetStr = Utils.getAmountStr(this.order.curSymbol, this.order.expenseBudget);
        this.expenseStr = Utils.getAmountStr(this.order.curSymbol, this.order.expense);
        this.expenseBalanceStr = Utils.getAmountStr(this.order.curSymbol, this.order.expenseBalance);
        this.receivedStr = Utils.getAmountStr(this.order.curSymbol, this.order.received);
        this.bankChargesStr = Utils.getAmountStr(this.order.curSymbol, this.order.bankCharges);
        this.dueAmountStr = Utils.getAmountStr(this.order.curSymbol, this.order.dueAmount);
        this.completed = this.order.completed ? 'Completed' : '';

        this.status = '';
        this.nobalance = false;
        this.notInvoicedRecently = false;

        const curSymbol = '$';
        
        if( this.order.balanceInUSD) // this field is not present in clientpo.component.ts
            this.balanceInUSDStr = Utils.getAmountStr(curSymbol, this.order.balanceInUSD);
        else 
            this.balanceInUSDStr = Utils.getAmountStr(curSymbol, 0);

        if (this.order.balance <= 10 && !this.order.completed) { // nil balance and order is open
            this.nobalance = true;
            if (this.order.balance < 0) {
                this.status = 'Negative balance';
                this.statusValue = POStatus.NEGATIVE_BALANCE;
            } else if (this.order.balance === 0) {
                this.status = 'Nil balance';
                this.statusValue = POStatus.NIL_BALANCE;
            } else {
                this.status = 'Low balance';
                this.statusValue = POStatus.NIL_BALANCE;
            }
            if ( this.order.dueAmount <= 0) { // if no dues, then po can be closed
                this.status += ', PO completed ?';
            }
        } else if (this.isFirstInvoicePending(this.order)) { // never billed on this PO
            this.notInvoicedRecently = true;
            this.status = 'First invoice pending';
            this.statusValue = POStatus.FIRST_INVOICE_PENDING;
        } else if (this.isNoRecentInvoice(this.order)) {
            this.notInvoicedRecently = true;
            this.status = 'Last invoice ' + Utils.getDateStr(this.order.lastInvoicedOn);
            this.statusValue = -3;
        } else if (this.order.completed) {
            this.status = 'Completed';
            this.statusValue = POStatus.COMPLETED;
        }

    }

    isNoRecentInvoice(po: POViewInterface): boolean {
        if (!this.order) return false
        if (this.order.completed) { return false; } // completed order - don't check
        if (this.order.balance < 10) { return false; } // less balance - don't check
        const threeMonths = 3;

        // po is not recent and last invoice was before 3 months
        return !this.isRecentPO(threeMonths) && this.isLastInvoiceBefore(threeMonths);
    }

    isFirstInvoicePending(po: POViewInterface): boolean {
        if (this.order) {
            if (this.order.completed) { return false; } // completed order - don't check
            const povalue = this.order.amount + this.order.expenseBudget;
            const totalbilled = this.order.billed + this.order.expense;
            if (povalue > 0 && totalbilled === 0) { return true; }
        }

        return false;
    }

    isRecentPO(months: number): boolean {
        return !Utils.isOlderThanNMonths(this.order!.poDate, months);
    }

    isLastInvoiceBefore(months: number): boolean {
        return Utils.isOlderThanNMonths(this.order!.lastInvoicedOn, months);
    }
}
