import { Invoice } from '../data/invoice';
import { Observable } from 'rxjs';
import { SBAssignment } from './softexImport.component';

// helper class for doing the softex and ebrc number assignment to invoices
// this class has the common code for both
export class SBAssignmentMgr {

    public isThisTheCorrectInvoice: (arg0: SBAssignment, arg1: Invoice) => boolean = (a, b) => false;

    public alreadyAssigned: (arg0: SBAssignment, arg1: Invoice) => boolean = (a, b) => false;

    public assignAndUpdate!: (arg0: SBAssignment, arg1: Invoice) => Observable<number>;

    public fetchInvoice!: (p: SBAssignment) => Observable<Invoice>;

    txt = '';

    placeholder = '';

    tuplepairs: SBAssignment[] | undefined;
    showClearBtn = true;
    showProceedBtn = true;

    message = '';
    showMessageAsError = false;

    count = 0;
    aborted = false;

    statusUpdated = 'Updated';
    statusUpdating = 'Updating Invoice';
    statusNoUpdateRequired = 'No updation required';
    statusError = 'ERROR';

    completed = false;

    sanityCheckOnInput() {
        if (this.tuplepairs) {
            let allok = this.tuplepairs.every(p => p.invReference && p.numToAssign);
            if (!allok) {
                let ls = this.tuplepairs.filter(p => !p.invReference || !p.numToAssign);
                if (ls) {
                    this.message = 'Errors :' + ls[0].numToAssign + ' ' + ls[0].invReference;
                    this.showMessageAsError = true;
                    this.showProceedBtn = false;
                    ls.forEach(p => p.status = 'ERROR');
                }
            }

        }

    }


    onReceivedList() {
        if (this.tuplepairs) {
            this.showProceedBtn = true;
            this.showClearBtn = true;

            this.showMessageAsError = false;
            this.aborted = false;
            this.sanityCheckOnInput();
            this.updateMessage();
        }

    }
    onPaste(event: ClipboardEvent) {
        if (event.clipboardData != null) {
            let items = event.clipboardData.getData('text');
            if (items != undefined) {
                this.tuplepairs = this.parse(items)
                    .filter(p => p && p.numToAssign && p.invReference) // remove undefined elements
                    .filter(p => !p.numToAssign.includes('Softex') && !p.invReference.includes('Invoice')); // header included ?

                this.onReceivedList();
            }
        }
    }
    // str =
    // S21002110983	TD21AA001
    // S21002110983	TD21AA001
    // S21002110984	TD21AA002
    // S21002110985	TD21AA003
    parse(str: string): SBAssignment[] {
        let m = str.split(/[\r\n]+/) // split on \r \n occurring 1 or more times
            .map(s => s.trim().split(/[ ,\t]+/)) // split on space comma tab occurring 1 or more times
            .map(p => {
                return {
                    numToAssign: p[0],
                    invReference: p[1],
                    invoiceId: 0,
                    status: ''
                };
            });
        return m;
    }



    onClearButton() {
        this.tuplepairs = undefined;
        this.txt = '';
        this.showMessageAsError = false;
        this.aborted = false;
        this.completed = false;
    }


    onProceedButton() {
        if (this.tuplepairs) {
            this.showProceedBtn = false;

            this.message = 'Processing';
            this.showMessageAsError = false;
            this.aborted = false;
            this.tuplepairs.forEach(p => {
                if (!this.aborted)
                    this.doAssignment(p);
            });
        }
    }

    abort(str: string) {
        this.message = 'Aborting : ' + str;
        this.showMessageAsError = true;

        this.showProceedBtn = false;
        this.aborted = true;
    }

    OnCompleted() {
        this.showProceedBtn = false;
        this.completed = true;
        this.message = 'Completed : ' + this.message;
        this.showMessageAsError = false;
        this.aborted = false;
    }

    total = 0
    updated = 0
    updating = 0
    error = 0
    updateMessage() {
        if (this.tuplepairs) {
            let updated = 0;
            let updating = 0;
            let total = 0;
            let error = 0;

            this.tuplepairs.forEach(p => {
                total++;
                switch (p.status) {
                    case this.statusUpdated: updated++; break;
                    case this.statusUpdating: updating++; break;
                    case this.statusNoUpdateRequired: updated++; break;
                    case this.statusError: error++;
                }
            }
            );

            this.total = total
            this.updated = updated
            this.updating = updating
            this.error = error
            this.message = `${total} Invoices `;
            if (updated > 0) {
                this.message += `, ${updated} updated`;
            }
            if (updating > 0) {
                this.message += `, ${updating} in progress`;
            }
            if (error > 0) {
                this.message += `, ${error} errors`;
                this.showMessageAsError = true;
            }
            if (total == updated) {
                this.OnCompleted();
            }
        }
    }


    doAssignment(p: SBAssignment) {
        if (this.aborted)
            return;
        this.count++;
        p.status = 'Fetching Invoice';
        if (!p.numToAssign)
            return this.abort(`Softex number not present for record ${this.count}`);
        if (!p.invReference)
            return this.abort(`Invoice number  not present for record ${this.count}`);
        this.fetchInvoice(p)
            .subscribe(
                inv => { this.updateInvoice(p, inv); },
                err => {
                    console.log(`Error while fetching Invoice  ${p.invReference} ${err}`);
                    p.status = this.statusError;
                    this.abort(err);
                },
                () => { }
            );

    }

    updateInvoice(p: SBAssignment, inv: Invoice) {
        if (!this.isThisTheCorrectInvoice(p, inv)) {
            this.abort(`Invoice number mismatch ${p.invReference}  and ${inv.invoiceNumber}`);
            return;
        }
        p.invoiceId = inv.id
        if (this.alreadyAssigned(p, inv)) {
            p.status = this.statusNoUpdateRequired;
            this.updateMessage();
            return;
        }
        p.status = this.statusUpdating;
        this.assignAndUpdate(p, inv)
            .subscribe(
                n => { },
                err => {
                    console.log(`Error while updating Invoice  ${inv.invoiceNumber} ${err}`);
                    p.status = this.statusError;
                    this.abort(err);
                },
                () => {

                    p.status = this.statusUpdated;
                    this.updateMessage();
                }
            );
    }
}
