import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data/data.service';
import { SPOViewClass, SPOViewInterface } from '../data/SPOViewInterface';

@Component({
  selector: 'app-supplierpo',
  templateUrl: './supplierpo.component.html',
  styleUrls: ['./supplierpo.component.scss']
})
export class SupplierpoComponent implements OnInit {

  @Input()
  sporder = new SPOViewClass();

  canCreateInvoice = true;
  showthispo = true;
  showNewInvoiceButton = true;
  constructor(private router: Router, protected data: DataService) { }

  ngOnInit(): void {
  }

  onNewInvoice() {
    this.router.navigate(['/newsupplierinvoice', { spoid: this.sporder!.id, supplierId: this.sporder!.supplierId }]);
}

}
