import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../data/data.service';
import { UserService } from '../data/user.service';
import { Location } from '@angular/common';
import { SupplierOrderBase } from './supplierOrderBase';

@Component({
  selector: 'app-supplier-order-form',
  templateUrl: './supplierOrderForm.component.html',
  styleUrls: ['./supplierOrderForm.component.scss']
})
export class SupplierOrderFormComponent extends SupplierOrderBase implements OnInit {

  buttonTitleStr = 'Create this Purchase Order';
  get buttonTitle() { return this.buttonTitleStr }
  get formTitle() { return 'New Purchase Order' }

  constructor(data: DataService, protected router: Router, protected user: UserService,
    protected location: Location, private route: ActivatedRoute) {
    super(data, router, user, location);
   }

  async ngOnInit() {
    const supplierIdStr = this.route.snapshot.paramMap.get('supplierId');
    const supplierId = supplierIdStr ? + supplierIdStr : 0;
    if(supplierId>0){
      this.spo.supplierId = supplierId;
      this.supplierName = (await this.data.getSupplier(supplierId)).name;
      this.showSupplierSelection = false;
    }
  }
  onSubmit() {
    this.successMessage = '';
    this.errorMessage = '';
    this.spo.completed = this.spo.completed ? 1 : 0;
    console.log(
        'Submitting new PO' + this.spo.supplierId,
        this.spo.amount,
        this.spo.poIssueDate,
        this.spo.currencyId,
        this.spo.description,
        this.spo.completed
    );
    this. buttonTitleStr = "Creating PO...";
    console.log(this.spo)
    this.data.addSupplierPurchaseOrder(this.spo).subscribe(
        spoid => {
            this.spo.id = spoid;
            this.setupAttachments(this.spo.id);
            this.successMessage = 'Uploading PO file ' + this.spo.poNumber;
            this.upLoadSPOFile();
            this.successMessage = 'Created PO ' + this.spo.poNumber;
            this.goToCurrentSPO(spoid);
        },
        err => {
            console.log(err);
            this.setError('Could not create PO: Error: ', err);
        }
    );
  }
}
