import { DataService, Supplier, Currency, SupplierPurchaseOrder, Fileblob, UserData } from '../data/data.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { UserService } from '../data/user.service';
import * as FileSaver from 'file-saver';
import { AttachmentInterface } from '../attachments/attachments.component';

export class SupplierOrderBase {
  suppliers: Supplier[] = [];
  supplierName = '';
  currencies: Currency[] = [];
  spo = new SupplierPurchaseOrder();
  get POFile() {
    return this.fileblob.name;
  }
  POFileLink = '';
  fileblob = new Fileblob();
  successMessage = '';
  errorMessage = '';
  showUploadPOFile = true; // the original PO upload button is shown always except for internal POs where it is created
  userData = new UserData();
  showForm = true;
  attachmentIntf: AttachmentInterface = new AttachmentInterface();

  updating = false; // internal po edit new
  extCurSymbol = '';

  get canModifyPO() {
    return this.userData.purchaseorderPerm.update > 0 ? true : false;
  }

  spoFileToBeUploaded: File | undefined;

  spoNumberPlaceHolder = '';
  showSupplierSelection = true;
  viewMode: boolean | undefined = undefined;
  showIsCompleted: boolean | undefined = undefined;

  get currencyName() {
    if (!this.currencies || !this.spo.currencyId) {
      return '';
    }
    const cur = this.currencies.find(c => c.id == this.spo.currencyId);
    return cur ? cur.name : '';
  }

  constructor(
    protected data: DataService,
    protected router: Router,
    protected user: UserService,
    protected location: Location
  ) {
    this.spo.completed = 0;

    this.user.currentUser.subscribe(ud => (this.userData = ud), err => this.setError('Connection Errors', err));

    data.getListOfSuppliers().then(
      res => (this.suppliers = res.sort((a, b) => (a.name > b.name ? 1 : -1))),
      err => {
        console.log('Error ' + err);
        this.setError('Connection Error: ', err);
      }
    );

    data.getListOfCurrencies().then(
      cs => (this.currencies = cs),
      err => {
        console.log('Error ' + err);
        this.setError('Connection Error: ', err);
      }
    );

  }

  setError(msg: string, err: any) {
    this.errorMessage = msg + err;
  }

  onSubmit() {

  }

  goToCurrentSPO(spoid: number, delayTime: number = 2000) {
    setTimeout(() => {
      this.router.navigateByUrl('/')
        .then(b => this.router.navigateByUrl('/supplierorder/' + spoid));
    }, delayTime);
  }

  resetForm() {
    setTimeout(() => {
      this.spo = new SupplierPurchaseOrder();
      this.successMessage = '';
      this.errorMessage = '';
      this.location.back();
    }, 2000);
  }

  fileUploadChanged(files: any) {
    if (files.length !== 1) {
      return;
    }
    this.spoFileToBeUploaded = files[0];
    this.fileblob = new Fileblob();
    // this.upLoadPOFile();
  }

  setupAttachments(spoId: number) {
    // console.log(`setting up attachment interface for ${poId}`);
    const af = new AttachmentInterface();
    af.id = spoId;
    af.addAttachment = (id, file) => this.data.addSPOattachment(id, file);
    af.getAttachmentDetails = id => this.data.getSPOAttachmentList(id);
    af.getAttachmentLink = id => this.data.getSPOAttachmentLink(id);
    af.downloadResource = str => this.data.downloadResource(str);
    this.attachmentIntf = af;
  }

  updateFileblob(onCompletion: () => void = () => { }) {
    if (this.spo.id > 0) {
      this.data.getFileblobForSPO(this.spo.id).subscribe(fb => {
        this.fileblob = fb;
        this.POFileLink = this.data.getSPOFileLink(this.spo.id);
        onCompletion();
      });
    }
  }

  async downloadSPOFileLinkClicked() {
    if (this.spo.id <= 0) {
      console.log('spo id is zero');
      return;
    }
    const urlstr = this.data.getSPOFileLink(this.spo.id);
    const res = await this.data.downloadResource(urlstr);
    const fileblob = new Blob([res], { type: 'application/octet-stream' });
    FileSaver.saveAs(fileblob, this.POFile);
  }

  upLoadSPOFile() {
    if (this.spoFileToBeUploaded) {
      if (this.spo.id !== 0) {
        console.log('Uploading PO file ' + this.spoFileToBeUploaded.name);
        this.data.uploadSPO(this.spo.id, this.spoFileToBeUploaded).subscribe(
          async e => {
            await this.updateFileblob();
            // this.successMessage = 'PO file uploaded';
          },
          err => {
            this.errorMessage = 'PO file could not be uploaded ' + err;
            throw err;
          }
        );
      }
    }
  }
}
