import { Component, OnInit, Input } from '@angular/core';
import { Fileblob } from '../data/data.service';
import { Observable, of } from 'rxjs';
import * as FileSaver from 'file-saver';

export class AttachmentInterface {
  id = 0;
  getAttachmentDetails: ((id: number) => Observable<Fileblob[]>) = i => of([]);
  addAttachment: ((id: number, fileToAttach: File) => Observable<number>) = (a, b) => of(0);
  getAttachmentLink: (attachmentId: number) => string = i => '';
  downloadResource: (urlink: string) => Promise<Blob> = s => Promise.resolve(new Blob());
}

@Component({
  selector: 'app-attachments',
  templateUrl: './attachments.component.html',
  styleUrls: ['./attachments.component.scss']
})
export class AttachmentsComponent implements OnInit {

  @Input()
  set attachmentIntf(val: AttachmentInterface) {
    this._attachmentIntf = val;
    this.initialize();
  }
  get attachmentIntf(): AttachmentInterface {
    return this._attachmentIntf;
  }
  _attachmentIntf!: AttachmentInterface;
  attachmentmsg = '';
  attachments: Fileblob[] = [];
  showAttachmentButton = false;


  ngOnInit() {
    this.initialize();
  }

  initialize() {
    if (this.attachmentIntf.id === 0) { this.showAttachmentButton = false; console.log('attachment id is 0'); return; }
    console.log('attachment id is ' + this.attachmentIntf.id);
    this.showAttachmentButton = true;
    this.attachmentIntf.getAttachmentDetails(this.attachmentIntf.id)
      .subscribe(lst => this.attachments = lst);
  }
  async attachmentLinkClicked(attachmentId: number, filename: string) {
    if (attachmentId <= 0 || !filename) { return; }
    const urlstr = this.attachmentIntf.getAttachmentLink(attachmentId);
    const res = await this.attachmentIntf.downloadResource(urlstr);
    const fileblob = new Blob([res], { type: 'application/octet-stream' });
    FileSaver.saveAs(fileblob, filename);
  }

  fileUploadChanged(files: any) {
    if (files.length !== 1) { return; }
    const attachment = files[0] as File;
    if (attachment.size <= 0) {
      this.attachmentmsg = 'File size is zero! ';
      return;
    }
    if (attachment.size > Math.pow(2,22)) { // 2 ^ 22 = 4 MB
      this.attachmentmsg = 'File size should be less than 4 MB';
      return;
    }
    this.attachmentmsg = 'Attaching ' + attachment.name;
    this.attachmentIntf.addAttachment(this.attachmentIntf.id, attachment)
      .subscribe(
        () => {
          this.attachmentmsg = '';
          this.initialize();
        }
      );
  }


}
