import { Component, OnInit } from '@angular/core';
import { DataService, Client, PurchaseOrder, Vendor } from '../data/data.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { InvoiceBase } from './invoiceBase';
import { Invoice } from '../data/invoice';

@Component({
  selector: 'app-invoiceinternalnew',
  templateUrl: './invoiceForm.component.html',
  styleUrls: ['./invoiceForm.component.scss']
})
export class InvoiceInternalNewComponent extends InvoiceBase implements OnInit {
 

  constructor(
    data: DataService,
    protected router: Router,
    protected route: ActivatedRoute,
    protected location: Location
  ) {
    super(data, router);
    this.viewMode = undefined;
    this.InternalInvoiceEdit = true;
    this.showReceived = false;
    this.buttonTitle = 'Create this Invoice';
    this.formTitle = 'Internal Invoice';
  }

  ngOnInit() {
    const idstr = this.route.snapshot.paramMap.get('id');
    const id = idstr ? +idstr : 0;
    //  console.log('NG On Init called - invoiceInternalNewComponent');
    this.initialize(id);
    this.isInternalCustomer = true;
  }

  async initialize(externalInvoiceId: number) {
    try {
      await this.prefillData(externalInvoiceId);
    } catch (e) {
      this.errorMessage = 'Error: Unable to get invoice details ' + e;
      this.resetForm(false);
    }
  }

  async prefillData(externalInvoiceId: number) {
    // get everything else from invoice Id
    this.externalInvoice = await this.data.getInvoice(externalInvoiceId).toPromise();
    this.InternalInvoiceId = await this.data.getInternalInvoiceForEndInvoice(this.invoice.id).toPromise();
    this.externalPO = await this.data.getPurchaseOrder(this.externalInvoice.purchaseOrderId).toPromise();
    this.externalVendor = await this.data.getVendor(this.externalPO.vendorId);
    if (this.InternalInvoiceId > 0) {
      // there is an internal invoice, so we are updating it now
      this.router.navigateByUrl('/editinternalinvoice/' + externalInvoiceId);
    } else {
      // creating a new internal invoice
      // find the purchase order for this internal invoice
      this.updating = false;
      this.viewMode = false;
      const externalPO = await this.data.getPurchaseOrder(this.externalInvoice.purchaseOrderId).toPromise();
      const internalPOId = await this.data.getInternalPOforEndPO(externalPO.id).toPromise();
      if (internalPOId <= 0) {
        this.errorMessage = 'Internal PO to Tismo India has to be created first.';
        setTimeout(() => {
          this.router.navigateByUrl(`/newinternalpo/${this.externalInvoice.purchaseOrderId}`);
        }, 1500);
        return;
      }
      const internalPO = await this.data.getPurchaseOrder(internalPOId).toPromise();

      let transferRate = internalPO.amount / externalPO.amount; // fraction transferred to Tismo India - usually 0.90 or 0.75 or 1.0
      if (transferRate > 1.0) {
        transferRate = 1.0
      }

      this.selectedPo = internalPO;
      this.clientId = this.selectedPo.clientId; // Tismo Inc or Tismo GmbH
      this.client = await this.data.getClient(this.clientId);
      this.clientName = this.client.name;
      //  this.purchaseOrders = [this.selectedPo];
      this.curSymbol = await this.data.getcurSymbol(this.selectedPo.currencyId);
      this.vendor = await this.data.getVendor(this.selectedPo.vendorId); // Tismo India

      // Only for Tismo India to Tismo GmbH invoices show the tip 
      // Period of service should be included in the description
      this.showPeriodOfServiceTip = this.data.isClientTismoGermany(this.client) && this.data.isTismoIndia(this.vendor.id); 

      this.projects = await this.data.getListOfProjectsForClient(this.externalPO.clientId).toPromise();
      this.selectedProject = this.projects.find(p => p.id === this.externalInvoice.projectId);
      this.invoice.receivedDate = new Date(this.externalInvoice.receivedDate);

      this.invoice.invoiceDate = this.externalInvoice.invoiceDate;
      this.invoice.isExpenseInvoice = this.externalInvoice.isExpenseInvoice;

      await this.generateInvoiceNumber();
      // this.invoice.invoiceNumber
      //     = await this.data.getNextInvoiceNumber(internalPO.vendorId,
      //         this.invoice.invoiceDate as Date,
      //         this.invoice.isExpenseInvoice).toPromise();

      this.invoice.amount = this.externalInvoice.amount * (this.externalInvoice.isExpenseInvoice ? 1.0 : transferRate);
      this.invoice.description = this.externalInvoice.description;
      this.invoice.projectId = this.externalInvoice.projectId;
      this.invoice.purchaseOrderId = internalPO.id;
      await this.getExternalInvoiceDetails();
      this.showClientSelection = false;
      this.enableRefreshInvoiceNumber = true;
      this.invoice.paymentTerms = 30; // internal invoice payment terms
    }
  }

  async getExternalInvoiceDetails() {
    this.endClient = await this.data.getClient(this.externalPO.clientId);
    this.extCurSymbol = await this.data.getcurSymbol(this.externalPO.currencyId);
  }

  async onSubmit() {
    this.successMessage = '';
    this.errorMessage = '';
    const oldButtonTitle = this.buttonTitle;
    this.buttonTitle = 'Creating Invoice...';
    await this.data
      .addInternalInvoiceForEndInv(this.invoice, this.externalInvoice.id)
      .toPromise()
      .then(
        c => {
          this.successMessage = 'Created internal Invoice ' + this.invoice.invoiceNumber;
          this.buttonTitle = oldButtonTitle;
          this.resetForm(false); // ngOnInit is called when creating new invoice => viewing it
        },
        er => {
          this.buttonTitle = oldButtonTitle;
          this.setError('Could not create internal Invoice', er);
        }
      );
  }

  resetForm(reInit: boolean) {
    const tout = reInit ? 500 : 2000;
    setTimeout(async () => {
      //    console.log('resetForm-internalNew');
      this.resetUserInputs();
      let invId = this.invoice.id;
      if (this.invoice.id <= 0) {
        const inv = await this.data.getInvoiceForInvoiceNumber(this.invoice.invoiceNumber).toPromise();
        invId = inv.id;
      }
      this.router.navigateByUrl('/editinternalinvoice/' + invId);
      if (reInit) {
        this.ngOnInit();
      }
    }, tout);
  }

  async refreshInvoiceNumberClicked() {
    await this.generateInvoiceNumber();
  }

  // resetForm() {
  //     setTimeout(() => {
  //         this.location.back();
  //         this.successMessage = '';
  //         this.errorMessage = '';
  //     }, 2000);
  // }

  editInternalInvoice() {}
}
